﻿/* Primary Block
*
*  This block is a reusable piece of styling.
*  You can either call the mixin within your code or just use the class `block-primary`
*  Background-colour is optional
*
*/

/* Default scoped block vars */
$border-width: 2px;
$border-alpha: 0.11;
$border-fallback: #195c99;
$border-radius: 5px;

@mixin block-primary($background-color: "") {
    width: 100%;
    padding: 1em;
    border-width: $border-width;
    border-color: $border-fallback;
    border-color: rgba(255, 255, 255, $border-alpha);
    border-radius: $border-radius;
    border-style: solid;
    background-color: unquote($background-color);
}

.block-primary {
    @include block-primary();
}

.block-primary-flush {
    padding: 0;
}

.block-primary-title {
    color: $white;
    @include fontsize(1.6);
    font-weight: 700;
    line-height: 1.1;
}