﻿$paragraph-small: 18px;
$background-color: $jaguar;
$pc-image-block-height: 275px;
$footer-link-color: #ccc;
$footer-link-color-hover: white;
$max-width: 1500px;
$simple-guide-margin-top: -47px;
$navigation-background: $deep-ice;

.pc-master {
    background-color: $background-color;

    .pc-master-header {
        background-color: $navigation-background;
    }

    // yuk, just yuk
    #global-footer {
        color: $footer-link-color;

        .link-list {
            border-color: $footer-link-color !important;

            li {
                a {
                    color: $footer-link-color !important;

                    &:hover {
                        color: $footer-link-color-hover;
                    }
                }
            }
        }

        .panel {
            .footer {
                a {
                    color: $footer-link-color;

                    &:hover {
                        color: $footer-link-color-hover;
                    }
                }
            }
        }
    }

    #pc-master-header,
    #page-wrapper,
    #global-footer{
        margin: 0 auto;
        max-width: 1500px;
    }

    .pc-header-large,
    .pc-header-small{
    	font-size: 2em;
        margin-bottom: 30px;
        position: relative;
        text-align: center;
        line-height: 1.1em;
        letter-spacing: -1px;
        &::after, &::before{
            content: '';
            position: absolute;
            bottom: -15px;
            margin-left: -35px;
            left: 50%;
            width: 70px;
            height: 2px;
            background-color: #fff;
        }
        &.pc-header-top-line::before{
            top: -15px;
            bottom: auto;
        }
    }
    .pc-header-small{
    	font-size: 1.8em;
    }
    .pc-header-large{
        h1 {
            font-size:1em;
        }
    }
    .pc-play-button{
        display: block;
        width: 50px;
        height: 50px;
        border: 2px solid #fff;
        border-radius: 50%;
        position: relative;
        margin: 0 auto;
        &::after{
            @include icon($play, 16px, #fff);
            position: absolute;
            top: 15px;
            left: 18px;
        }
        &.pc-play-button-inverse{
            border-color: #85a9ca;
            &::after{
                color: #85a9ca;
            }
        }
    }

    .pc-button,
    .ss-click-to-share .ss-title{
        background-image: none;
        position: relative;
        @include pc-button();
    }

    .pc-button.pc-button-hollow{
        @include pc-button();
        background-color: transparent;
        border: 2px solid #fff;
        color: #fff;
        box-shadow: none;
        padding-top: 8px;
        &::before{
            color: #fff;
            top: 10px;
        }
        &.pc-button-inverse{
            color: #38b132;
            border-color: #38b132;
            background-color: transparent;
            &::before{
                color: #38b132;
            }
        }
    }

    .pc-button.pc-button-inverse,
    .ss-click-to-share.pc-content-share-inverse .ss-title{
        background-color: #38b132;
        border-color: transparent;
        color: #fff;
        &::before{
            color: #fff;
        }
        
    }
    .ss-click-to-share .ss-title{
        @include pc-button($share);
    }

    .ss-click-to-share{
        width: 180px;
        .ss-container{
            display: none;
            text-align: center;
            width: 100%;
        }
        &.clicked{
            .ss-container{
                display: block;
            }
            .ss-title{
                display: none;
            }
        }
        .ss-container{
            top: 2px;
            left: -1px;
            margin-left: 0;
        }
        .ss-link{
            height: 30px;
            width: 30px;
        }
        .ss-link-fb:before,
        .ss-link-tw:before,
        .ss-link-gp:before,
        .ss-link-li:before,
        .ss-link-wa:before{
            line-height: 8px;
            font-size: 19px;
        }
    }

    .pc-content-intro-time{
        font-size: .7em;
        text-align: center;
        &::before{
            @include icon($clock, .7em, inherit);
            padding-right: 2px;
        }
    }

    .pc-content-intro{
        background-size: cover;
        background-repeat: no-repeat;
        min-height: 420px;
        background-position: center;
        position: relative;
        
        .ss-click-to-share{
            margin: 0 auto;
        }
    }

    .pc-content-intro-container{
        width: 50%;
        margin: 0 auto;
        padding: 150px 0 70px 0;
        position: relative;
        z-index: 1;
        @media(max-width: $touch-nav){
            width: 100%;
            padding-left: 20px;
            padding-right: 20px;
        }
    }

    .pc-content-intro-date{
        text-align: center;
        text-transform: uppercase;
        font-size: .7em;
    }

    .pc-content-intro-content{
        width: 80%;
        margin: 30px auto;
        font-size: $paragraph-small;
        text-align: center;
    }
    
    .pc-maincontent{
        background-color: #fff;
        color: $deep-ice;
    }
}