﻿$pulse-color: rgba(253,253,255,.56);
@keyframes pulse {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.3);
    }
    100% {
        transform: scale(1);
    }
}
.iih-container {
    position: relative;
    display: none;
}
.iih-image {
    width: 100%;
    height: auto;
}
.iih-content {
    position: relative;
    max-width: 100%;
    margin: 0 auto;
    display: block;
    padding: 10px;
    @media (min-width: $mobile) {
        max-width: 980px;
        padding: 20px;
    }
}
.iih-pulse-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
}

.iih-hotspot {
    position: absolute;
}

.iih-pulse {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 30px;
    height: 30px;
    margin: -15px 0 0 -15px;
    cursor: pointer;
    z-index: 10;
    will-change: transform;
    animation-fill-mode: both;
    animation-iteration-count: infinite;
    animation-name: pulse;
    animation-duration: 1s;
    transition: all .2s ease-in-out;
    -webkit-transform: translateZ(0);
    
    &:hover {
        transform: scale(1.3);
        transition: all .5s ease-in-out;
    }
    &:focus {
        outline: 0;
        transform: scale(1.1);
        animation: 0;
        &:after {
            animation: none;
        }
    }

    &--small {
        width: 20px;
        height: 20px;
        margin: -10px 0 0 -10px;
    }

    &--large {
        width: 40px;
        height: 40px;
        margin: -20px 0 0 -20px;
    }

    @media (min-width: $mobile) {
        width: 40px;
        height: 40px;
        margin: -20px 0 0 -20px;
        &--small {
            width: 30px;
            height: 30px;
            margin: -15px 0 0 -15px;
        }
        &--large {
            width: 50px;
            height: 50px;
            margin: -25px 0 0 -25px;
        }
    }
}
.iih-tooltip-mobile {
    width: 100%;
    margin: 0 auto;
    background-color: #EBF2D6;
    color: #000000;
    border-radius: 5px;
    padding: 20px;
    animation: 0;
    transition: all .5s ease-in-out;
    opacity: 1;
    font-size: 18px;
    letter-spacing: 0.3px;

    @media (min-width: 1001px) {
        display: none;
    }
}

.iih-tooltip {
    background-color: #EBF2D6;
    position: absolute;
    color: #000000;
    border-radius: 5px;
    padding: 15px;
    animation: 0;
    opacity: 0.9;
    font-size: 16px;
    letter-spacing: 0.3px;
    margin-top: 25px;

    @media (max-width: 1000px) {
        display: none;
    }
    &--narrow {
         width: 180px;
         left: -90px;
    }
    &--medium {
         width: 250px;
         left: -125px;
    }
    &--large {
         width: 375px;
         left: -188px;
    }

}

.iih-tooltip-content {
    display: inline-block;
}

.iih-tooltip-image {
    display: inline-block;
    vertical-align: top;
    padding-right: 10px;
    max-width: 63px;
}

.iih-content-tooltip {
    position: relative;
    max-width: 100%;
    margin: 0 auto;
    display: block;
    padding: 10px;

    @media (min-width: 1001px) {
        display: none;
    }
}

.iih-close-tooltip {
    position: absolute;
    width: 20px;
    height: 20px;
    top: 8%;
    right: 25px;
    cursor: pointer;
    background-repeat: no-repeat;
    background-image: url('../../responsive2015/images/icons/close.png');
    background-size: cover;
    top: 10px;
    right: 2%;
}