﻿
.git-title {
    @include font-size(1.4);
    padding: 20px 0;
    font-weight: 600;
}

.git-title-right {
    @include font-size(1.1);
    padding: 20px 0;
    font-weight: bold;
}

.git-content {
    display: table;
    width: 100%;
    padding: 0 20px;
    border-radius: 5px;
}

.git-content a {
    text-decoration: underline;
    display: table-cell;    
    word-break: break-all;
    color: #38B148;
}

.git-right {
    display: table-cell;
    width: 30%;
    padding: 5px;

    @media only screen and (max-width: $tablet) {
        width: 100%;
        display: block;
    }
}

.git-left {
    display: table-cell;
    width: 70%;
    padding: 5px;

    @media only screen and (max-width: $tablet) {
        width: 100%;
        display: block;
    }
}
