﻿.pc-list-block-container {
    font-size: 0px;
    padding: 25px;
}

.pc-list-block-title-container {
    display: inline-block;
    width: 100%;
    vertical-align: top;
    margin-bottom: 20px;

    @media only screen and (min-width: $tablet) {
        width: 50%;
        margin-bottom: 0;
    }
}

.pc-list-block-title {
    @include pc-block-heading();

    @media only screen and (min-width: $tablet) {
        padding-right: 2em;
    }
}

.pc-list-block-line-break {
    @include pc-line-break();
    background-color: #fff;
}

.pc-list-block-list {
    display: inline-block;
    width: 100%;
    font-size: 16px;

    @media only screen and (min-width: $tablet) {
        width: 50%;
    }
}

.pc-list-block-list ul {
    display: block;
    list-style-type: disc !important;
    margin: 0 0 20px 0;
    list-style-position: inside;

    li {
        padding: 5px 0;
        font-size: 1.2em;
        font-weight: 600;
    }
}