﻿.start-comparing-block.call-centre {

	.start-comparing-block-inner {
	
		@media only screen and (min-width: $mobile) {
			@include fontsize(1);
			width: 46%;
			margin: 12px 0 0 0;
			display: inline-block;
			vertical-align: top;
		}
						
		.button-holder {
			a {
				width: auto;
					
				@media only screen and (min-width: $mobile) {
					margin-top: 0;
				}

				margin-bottom: 25px;
			}

			@media only screen and (min-width: $mobile) {
				width: 100%;
			}
			
			+.icon-holder {
				position: relative;
				right: inherit;
				top: inherit;

				@media only screen and (min-width: $mobile) {
					right: 20px;
					position: absolute;
					top: inherit;
				}
			}	
		}

		.icon-holder {

			position: absolute;
			right: 2%;
			top: 3%;
			width: 25%;

			@media only screen and (min-width: $mobile-low) {
				right: 30%;
			}
			
			@media only screen and (min-width: $mobile) {
				left: inherit;
				right: 0;
				top: 17%;
			}

			.icon {
				position: static;
			}

			.icon {

				&.Digitaltv {
					
					&:before {

						font-size: 40px;
						
						@media only screen and (min-width: $mobile-low) {
							font-size: 66px;
						}
						@media only screen and (min-width: $mobile) {
							font-size: 38px;
							position: static;
						}
						@media only screen and (min-width: $tablet) {
							font-size: 55px;
						}

						

					}

				}
			
				&:before {

					font-size: 35px;

					@media only screen and (min-width: $mobile-low) {
						font-size: 50px;
					}
					@media only screen and (min-width: $mobile) {
						font-size: 43px;
						position: static;
					}
					@media only screen and (min-width: $tablet) {
						font-size: 60px;
					}
				}

                &.Energy {
                    display: none;

                    @media only screen and (min-width: $mobile) {
                        display: block;
                    }
                }
			}

			@media only screen and (min-width: $mobile) {
				position: absolute;
				bottom: 15px;
				right: 20px;
				top: inherit;

			}
		}
	}



	.call-centre-detail {
		border: 0 solid $deep-ice;
		border-top-width: 1px; 
		margin: 20px;
		padding-top: 20px;
		
		
		@media only screen and (min-width: $mobile) {
			text-align: left;
			border-width: 0;
			border-left-width: 2px;
			width: 43%;
			margin: 0 0 0 3%;
			display: inline-block;
			padding: 0 10px 0 3%;
			margin-top: 12px;
        }
		
	}

	.call-centre-telephone {
		font-family: $font-sun;
		font-weight: 700;

		@media only screen and (min-width: $mobile) {
			@include font-size(1.3);
		}
		@media only screen and (min-width: $tablet) {
			@include font-size(1.5);
		}
		@media only screen and (min-width: $desktop) {
			@include font-size(1.8);
		}
	}

	.call-centre-hours, .call-centre-copy {
		@include font-size(0.8);
	}

	.call-centre-hours,
	.call-centre-hours + .call-centre-copy {
		margin-top: 0.6em;
	}

	.call-centre-copy {
		line-height: 1.3;

		@media only screen and (min-width: $mobile) {
			width: 78%;
		}
	}

	.secondary-button {
		@include branded-button;
		@include linear-gradient(white, $secondary-light-grey, false);
		color: $primary-green;
		margin: 10px 0;
		width: 100%;
		padding: 10px;

		.lt-ie9 & {
			background: $secondary-light-grey;
			-ms-filter: "progid:DXImageTransform.Microsoft.gradient(enabled=false)" !important;
			
			&:hover {
				background: $white;
			}
		}

		&:hover{
			@include linear-gradient($secondary-light-grey, white, false);
		}

		@media only screen and (min-width: $mobile-low) {
		width: auto;
		padding: 10px 40px;
		}
		
		@media only screen and (min-width: $mobile) {
			@include font-size(0.7);
			padding: 10px;
		}
	}
}


//  Call back plugin overrides
@media only screen and (max-width: $mobile) {
	.modal.in .modal-dialog{
		width: 96%;
		margin: 12px auto;

		.callback-lightbox .advisor-help{
			width: 100%;
		}
		.callback-lightbox .customer-details {
			width: 100%;
			margin: 20px 0 0 0;
		}
	}
}

.callback-lightbox {
	h1 {
		line-height: 1;
	}

	.inputs,
	#callMeBack {
		max-width: 100%;
	}

	.advisor-help {
		p {
			line-height: 1.1;
		}
	}
}