﻿#global-header {

    background-color: #1f2a5b;
    position: static;
    z-index: 2;
}

.header-hero {
    @media (min-width: $mobile + 1) and (max-width: $touch-nav) {
        margin-bottom: -65px;
    }
}

#global-nav,
.logo,
#page-wrapper {
    position: relative;
    z-index: 1;
}

#global-nav {
    display: none;

    @media (min-width: $desktop) {
        display: block;
        
        a {
            display: inline-block;
            padding: 1em;
        }
    }
}

.logo {
    width: 300px;
    padding: 1.25em 2em;
    display: inline-block;
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    left: 50%;
    margin-left: -150px;
    text-align: center;

    @media (min-width: $desktop) {
        position: relative;
        top: auto;
        left: auto;
        margin: 1em 0 2em 1em;
        background: rgba(255, 255, 255, 0.3);
    }
}

.hero {
    width: 100%;
}

.hero-inner {
    position: relative;


    #global-header & {
        @media (min-width: $touch-nav) {
            position: static;
        }
    }
}

.hero-copy {
    position: absolute;
    bottom: 5%;
    z-index: 1;
    width: 100%;
    text-align: center;
    padding: 0 1em 0 1.3em;
    text-shadow: 0 1px 4px rgba(0, 0, 0, 1);

    @media(min-width: $mobile-smallest + 1) {
        width: 100%;
        max-width: $content-width;
        text-align: left;

        @media(min-height: 635px) {
            padding-bottom: 30px;
        }
    }

    @media(min-width: $mobile-smallest + 1) and (max-width: $touch-nav - 1) {
        height: 100%;
        margin: 0;
        top: 0;
        padding: 0 1em;

        &:before {
            // used to vertically center .hero-copy-inner
            content: "";
            display: inline-block;
            height: 100%;
            vertical-align: middle;
            margin-right: -0.25em;
        }
    }

    @media(min-width: $touch-nav) {
        margin-top: 40px;
        position: relative;
        bottom: auto;
    } 

    @media(min-width: $desktop)  {
        margin: 40px auto 0;
    }

    @media(min-width: $desktop) and (min-height: 635px) {
        margin: 65px auto 0;
    }

    @media(min-width: $desktop) and (min-height: 720px) {
        margin-top: 127px;
    }
}

.hero-copy-inner {
    width: 100%;
    max-width: $content-width;
    margin: 0 auto;
    display: inline-block;
    vertical-align: middle;
}

.hero-heading {
    margin-bottom: .5em;

    @media(min-width: $mobile-smallest + 1) {
        width: 48%;
    }

    * {
        @include fontsize(1.8);
        line-height: 1.2;
        color: $white;
        font-weight: 700;

        @media(min-width: $mobile-smallest + 1) and (max-width: $desktop) {
            @include fontsize(1.3);
        }
    }
}

.hero-details {
    display: none;
    margin-bottom: 1em;
    @include fontsize(1);

    @media(min-width: $mobile-smallest + 1) {
        width: 48%;
    }

    @media(min-width: $mobile-low) {
        display: block;
    }
}

.hero-cta {
    @include fontsize(1.3);
    text-decoration: underline;
    color: $brand-green-bright;

    &:hover {
        color: lighten($brand-green-bright, 10%);
    }
}

.hero-image {
    width: 100%;
    display: block;

    #global-header & {   
        @media (min-width: $touch-nav) {
            width: 978px;
            height: auto;
            margin-left: -489px;
            position: relative;
            left: 50%;
        }
        @media (min-width: $desktop) {
            width: 1365px;
            margin-left: -682px;
        }
    }
}

.hero-is-logged-in {
    display: none;

    @media(min-width: $mobile-low) {
        display: block;
    }
}