﻿$skinny-cta-main-background-color: $white;
$skinny-cta-title: #4CB848;
$skinny-cta-text-color: $shark;

.pc-skinny-cta {
    background-color: $skinny-cta-main-background-color;
}

.pc-skinny-cta-wrapper {
    display: block;
    text-align: center;
    margin: 0 auto;
    width: 100%;
	padding: 0 20px;

    @media(min-width: $touch-nav) {
        width: 50%;
		padding: 0;
    }

    .button {
        @include branded-button();
        display: inline-block;
        margin-top: 10px;
    }
}

.pc-skinny-cta-title {
    color: $skinny-cta-title;
    font-weight: 700;
    @include font-size(1.4);
    margin-bottom: 10px;
}

.pc-skinny-cta-text {
    color: $skinny-cta-text-color;
    margin-bottom: 10px;
}
