.tinfo-wrapper {
    position: relative;
    @media(min-width: $mobile) {
        display: table;
        width: 100%;
    }
}
.tinfo-container {
    @include content-width();
    @media(min-width: $mobile) {
        display: table;
        width: 100%;
        table-layout: fixed;
    }
}
.tinfo-content {
    padding-bottom: 30px;
    @media(min-width: $mobile) {
        display: table-cell;
        vertical-align: top;
        width: 50%;
    }
    .tinfo-content-left & {
        @media(min-width: $mobile) {
            padding-right: 30px;
        }
    }
}
.tinfo-support-content {
    padding-bottom: 30px;
    padding-top: 0;
    ol, ul {
        margin: 0;
    }
    li {
        list-style: disc;
        margin-left: 25px;
        font-size: 18px;
        padding: 5px 0 5px 0px;
    }
    blockquote {
        font-weight: bold;
        padding: 0 30px 30px 30px;
        font-family: "Sun";
        font-size: 22px;
        line-height: 28px;
        position: relative;
        @media(min-width: $mobile) {
            padding: 0px 30px 30px 80px;
        }
        &:before {
            content: "\201C";
            font-size: 55px;
            color: $white;
            position: absolute;
            left: 0px;
            top: 7px;
            @media(min-width: $mobile) {
                left: 45px;
                top: 12px;
            }
        }
        &:after {
            content: '\201D';
            font-size: 55px;
            color: $white;
            position: absolute;
            right: 15px;
            bottom: 8px;
        }
    }
    @media(min-width: $mobile) {
        display: table-cell;
        width: 50%;
    }
    .tinfo-vertical-bottom & {
        padding-bottom: 0;
        @media(min-width: $mobile) {
            vertical-align: bottom;
            padding-top: 30px;
            padding-bottom: 0;
            padding-left: 50px;
        }
    }
    .tinfo-vertical-center & {
        vertical-align: middle;
        @media(min-width: $mobile) {
            padding-top: 30px;
        }
    }
}
.tinfo-panel-title {
    @include title();
    font-weight: bold;
    margin-top: 30px;
}
.tinfo-title-containter {
    display: table-row;
}
.tinfo-copy {
    p {
        font-size: 18px;
        margin-bottom: 15px;
        letter-spacing: .3px
    }
}
.tinfo-panel-button {
    @include cta-button();
    margin: 20px 0 0;
}
.tinfo-panel-inverse-button {
    @include cta-button(inverse);
    margin: 20px 0 0;
}
.tinfo-panel-hollow-button {
    @include cta-button(hollow);
    margin: 20px 0 0;
}
.tinfo-button-supporting-text {
    display: block;
    margin-top: 10px;
    font-size: 13px;
}
.tinfo-two-things-line {
    @include two-things-line();
}