﻿.pc-fourpanelhub {

}

.pc-fourpanelhub-container {
    position: relative;
} 

.pc-fourpanelhub {
	clear: both;
	overflow: auto;
}

.pc-fourpanelhub-large,
.pc-fourpanelhub-small { 
	height: 66.66%;
	background-size: cover !important;
	display: block;
	text-align: center;
}

.pc-fourpanelhub-inner {
    display: table;
	height: 100%;
	width: 50%;
	margin: 0 auto;
	color: #fff;

    > div{
		display: table-cell;
		vertical-align: middle;
	}
}

.pc-fourpanelhub-small{
	height: 33.33%;
}

.pc-fourpanelhub-right,
.pc-fourpanelhub-left{
	float: left;
	width: 50%;
	height: 700px;
}

@media(max-width: $touch-nav) {

	.pc-fourpanelhub-right,
	.pc-fourpanelhub-left {
		float: none;
	    height: initial;
		width: 100%;
	}

	.pc-fourpanelhub-large,
	.pc-fourpanelhub-small{
		padding: 35px 0;
	}

}

.pc-fourpanelhub-link-panel-legal-container {
    position: absolute;
    top: 85%;
    left: 50%;
    transform: translatex(-50%);
}

.pc-fourpanelhub-link-panel-legal-copy {
    text-align: center;
    font-size: small;
}
