﻿.lets-compare-block {

	&.full-width {
		.start-comparing-block {
			width: 100% !important;
		}
	}

    h2 {
        margin-bottom: 20px;
        text-align: center;

        @media only screen and (min-width: $mobile) {
			@include fontsize(1.6);
		    line-height: 1.6rem;
		}
    }

	& > h2 {
        margin: 0px 15px;
            		
	}

    & > p {
		margin: 0px 15px;
	}

    
    .panel {
        @media(max-width: $mobile) {
            padding: 0;    
        }
    }

    .compare-actions {
 		width: 100%;

		@media only screen and (min-width: $mobile) {
				    
    		@include flexbox();
    		@include justify-content(space-between);
		}
    	
	    .start-comparing-block {
		    vertical-align: top;
		    text-align: center;
		    display: inline-block;
		    background: $rough-sea;
            width: 100%;
            padding-top: 25px;

			.block-primary-title {
				margin-bottom: 10px;
				width: 100%;

				@media only screen and (min-width: $mobile) {
				    text-align: left;
					margin-left: 30px;
					width: auto;
					margin-bottom: 20px;
				}
			}

			.compare-link-blocks {
			    display: block;
			    margin: 0 $mobile-side-margin;
                @include justify-blocks();

				a {
					@include linear-gradient($brand-green,$brand-green-dark);
					border-radius: 4px;
					vertical-align: top;
					display: inline-block;
					width: 30%;
					margin: 10px 0 3% 0;
                    
                    @media (min-width: $mobile) {
                        display: none;

                        // THIS IS WHAT WE SHOULD BE USING
                        &:nth-child(1),
                        &:nth-child(2),
                        &:nth-child(3),
                        &:nth-child(4),
                        &:nth-child(5),
                        &:nth-child(6) {
                            display: inline-block
                        }

                        // THIS IS A HACK FOR IE8
                        .lt-ie9 & {
                            display: inline-block;
                            + a + a + a + a + a + a {
                                display: none;
                            } 
                        }
                         
                    }

                    &:hover {
                        @include linear-gradient($brand-green-dark,$brand-green);
                    }

                    span {
                        color: $white;
                        display: block;
                        height: 34px;
                        width: 80px;
                        margin: auto;
                        margin-bottom: 6px;
                        margin-top: 56px;
                        position: relative;
                        line-height: 1.2;
                        text-align: center;
                        font-weight: 700;
					    @include fontsize(0.9);
                        @media only screen and (min-width: $mobile) {
                            height: 40px;
                        }
                        &:before {
	                        font-family: 'ctm-icons';
                            @include fontsize(2.2);
	                        color: $white;
                            position: absolute;
                            top: -43px;
                            left: 0;
                            width: 100%;
                            margin: 0;
                            text-align: center;
                            font-weight: 100;
                        	@media only screen and (min-width: $mobile) {
                                @include fontsize(1.5);
					        }
                        }
                    }

                    &.homeCategory span:before{
                        content: $home;
                    }
                    &.carCategory span:before{
                        content: $car;
                    } 
                    &.vanCategory span:before{
                        content: $van;
                    } 
                    &.motorbikeCategory span:before{
                        content: $motorbike;
                    }
                    &.scooterandmopedCategory span:before {
                        content: $moped;   
                    }
                    &.lifeCategory span:before{
                        content: $life;
                    } 
                    &.creditcardsCategory span:before{
                        content: $credit-card;
                    } 
                    &.mobilebroadbandCategory span:before{
                        content: $mobile-phone;
                    } 
                    &.energyCategory span:before{
                        content: $energy;
                    }  
                    &.travelCategory span:before{
                        content: $plane;
                    }
                    &.petCategory span:before{
                        content: $pet;
                    }
                    &.hometelandbroadbandCategory span:before{
                        content: $broadband;
                    }

				}
				@media only screen and (min-width: $mobile) {
					margin: 0 32px -20px 32px;
                    
	        	}
			}

		    @media only screen and (min-width: $mobile) {
	            width: 60%;
		        padding: 30px 0px 12px 0px;
	            border-radius: 5px;
		    }

	    }

	    .returning-customer-block {
		    background: $deep-ice;
		    display: inline-block;
	        vertical-align:top;
			width: 100%;
            position: relative;
            margin-top: 25px;
            
            @media only screen and (min-width: $mobile) {
  
            }

	        .returning-block-inner {
	        	margin: 0 25px 25px 25px;
	        	border: 2px solid $rough-sea;
				border-radius: 5px;
				padding: 20px;
				text-align: center;
                background: $rough-sea;
                position: relative;
                
                @media only screen and (min-width: $mobile) {
					margin: 0;
					text-align: left;
					padding: 30px 32px;
                    position: static;
	    		}

                @media only screen and (min-width: $desktop) {
                    border: none;
                }

                &.NotRecognised {
                    background: transparent;
				    text-align: center;
                    margin-bottom: 0;
                }
	        }

		    .returning-main-link {
	            @include branded-button();
	            display: block;
			    width: 200px;
			    margin: 10px auto;
                text-align: center;

			    @media only screen and (min-width: $mobile) {
			    	width: 100%;
			    	margin-bottom: 30px;
			    }

                @media only screen and (min-width: $desktop) {
                    display: inline-block;                }
		    }

		    h2 {
			    margin-bottom: 10px;
			    @media only screen and (min-width: $mobile) {
			    	margin-bottom: 30px;
			    	text-align: left;
			    }
		    }

		    p {
		    	margin-bottom: 20px;

                a {
                    display: inline;
                }

                @media only screen and (min-width: $mobile) {
                    text-align: left;
                    a {
                        display: block;
                    }
                }
		    }

            .sub-heading-text {
                margin-top: -16px;
                @include fontsize(0.8);
                line-height: 0.9rem;
                color: $misty-mountain;
                display: none;

                @media only screen and (min-width: $mobile) {
                    display: block;
                }
            }
            
            @media only screen and (min-width: $mobile) {
		    	@include flex(1, 38%);
	            background: $rough-sea;
		        margin: 0;
		        border-radius: 5px;
		    }

            .quote-details {
                width: 75%;
                margin: auto;
                p {
                    z-index: 2;
                    position: relative;
                }
                span {
                    display: block;
                    font-weight: bold;
                }
                .best-price {
                    @include fontsize(1.5);
                    margin: 12px 0;
                }
                .quote-created {
                    @include fontsize(0.9);
                    margin-top: 3px;
                }
                a{
                      margin-top: 15px;
                      display: block;
                      text-decoration: underline;
                        z-index: 2;
                        position: relative;
                }
                @media only screen and (min-width: $mobile) {
                    margin-left: 0;
                }
            }

            .icon{
                position: absolute;
                right: 10px;
                bottom: 10px;

                @media only screen and (min-width: $mobile) {
                    right:25px;
                    bottom: 30px;
				}
                @media only screen and (min-width: 800px) {
                    display: block;
                    position: absolute;
                    right:25px;
                    bottom: 30px;
                }
                
                &:before {
	                font-family: 'ctm-icons';
                    font-size: 30px;
	                font-size: 2rem;
	                color: $deep-ice;
                    
                    @media only screen and (min-width: $mobile-low) {
                        font-size: 45px;
                        font-size: 4rem;
					}                    
                    @media only screen and (min-width: $mobile) {
                        font-size: 35px;
                        font-size: 2.5rem;
					}
                    @media only screen and (min-width: $tablet) {
                        font-size: 45px;
                        font-size: 4rem;
                    }
                    
                }

                &.homeIcon:before{
                    content: $home;
                }
                &.carIcon:before{
                    content: $car;
                } 
                &.vanIcon:before{
                    content: $van;
                    @include fontsize(1.5);
                    @media only screen and (min-width: $mobile-low) {
                        @include fontsize(3.5);
					}                    
                    @media only screen and (min-width: $mobile) {
                        @include fontsize(2);
					}
                    @media only screen and (min-width: $tablet) {
                        @include fontsize(3.5);
                    }
                } 
                &.bikeIcon:before{
                    content: $motorbike;
                } 
                &.lifeIcon:before{
                    content: $life;
                } 
                &.creditcardsIcon:before{
                    content: $credit-card;
                } 
                &.energyIcon:before{
                    content: $energy;
                }  
                &.travelIcon:before{
                    content: $plane;
                }
				&.petIcon:before{
                    content: $pet;
                }

		    }

	    }
    }
}