﻿.accordion {
    width: 100%; 
    
    &__item {
        margin-bottom: 1px;
    }
    
    &__heading {
	    cursor: pointer;
	    padding: 10px;
	    color: $black;
        position: relative;
        padding: 20px 80px 20px 20px;

        &:before {
            @include icon($chevron-left, 2.5rem);
            color: $brand-green;
            display: block;
            height: 46px;
            line-height: 1;
            margin-top: -23px;
            position: absolute;
            right: 25px;
            top: 50%;
            -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
            width: 30px;
        }
        
        &--open {
            &:before {
                -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
            }
        }
    }

    &__content {
	    background-color: #EFEFEF;
	    display: none;
	    padding: 10px;

        ul {
            list-style-type: disc;
        }

        ol {
            list-style-type: decimal;
        }

        ul,
        ol {
            margin: 1em 0 1em 1em;

            li {
                margin-bottom: .5em;
            }
        }

        p {
            margin: 0 0 1em;
        }
    }

    &__content-close {
        text-align: right;
    }

    &__close-btn {
        background: transparent;
        color: $brand-green;
        cursor: pointer;
        font-family: inherit;
        font-size: 1rem;
    }

    &--single-view {
        .accordion__item:first-child {
            .accordion__content {
                display: block;
            }
        }

        .accordion__content-close {
            display:none;
        }
    }
}

.accordion-article-block {
    background-color: $white;

    &__container {
        margin: 0 auto;
        max-width: 940px;
        padding: 20px 0;
    }
}
