﻿@keyframes hlb-incorrect {
    0% {
        border: solid 1px #ff0000;
        left: 0px;
    }
    30% {
        border: solid 1px transparent;
        left: 5px;
    }
    70% {
        border: solid 1px transparent;
        left: -5px;
    }
    100% {
        border: solid 1px #ff0000;
        left: 0px;
    }
}

.hlb-container {
    position: relative;
    background: #22222a;
}

.hlb-cta-container {
    width: 100%;
    height: auto;
    text-align: center;
    background: #22222a;
}

.hlb-image {
    display: block;
    width: 100%;
    max-width: 600px;
    margin: auto;
}

.hlb-current-container {
    @include clearfix;
    padding: 20px 0;

    @media(min-width: $mobile) {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        padding: 40px 0 0;
    }
}

.hlb-current {
    position: relative;
    max-width: 928px;
    margin: auto;
    padding: 0 20px;
    font-size: 22px;
    font-weight: 600;

    @media(min-width: $mobile) {   
        font-size: 30px;
    }
}

.hlb-current-score {
    float: left;
}

.hlb-current-strikes {
    float: right;
}

.hlb-popup-container {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.5);
    background-color: #d4dfe7;
    z-index: 2;

    @media(min-width: $mobile) {     
        padding: 40px;
        background-color: rgba(0,0,0,0.5);
    }
}

.hlb-popup {
    display: table;
    height: 100%;
    width: 100%;
}

.hlb-popup-align {
    display: table-cell;
    vertical-align: middle;

    @media(min-width: $mobile) {     
        padding: 40px;
    }
}

.hlb-popup-content {
    max-width: 600px;
    margin: auto;
    padding: 20px;
    background: #d4dfe7;
    text-align: center;
    color: #004A8E;

    @media(min-width: $tablet) {     
        padding: 40px;
        border-radius: 10px;
        box-shadow: 0px 0px 30px #555555;
    }
}

.hlb-popup-copy {
    padding-top: 20px;
}

.hlb-city-name {
    text-transform: capitalize;
    padding-bottom: 30px;
    font-size: 40px;
}

.hlb-popup-score {
    color: #049FB1;
    font-weight: 600;
    font-size: 25px;
}

.hlb-final-score {
    color: #049FB1;
    font-weight: 600;
    font-size: 30px;
    padding-bottom: 20px;
}

.hlb-popup-title {
    display: block;
    font-size: 45px;
    font-weight: 600;
    margin-bottom: 30px;
    line-height: 1.3;
}

.hlb-input-container {
    padding: 20px;
}

.hlb-input {
    position: relative;
    display: block;
    width: 100%;
    max-width: 928px;
    margin: auto;
    padding: 10px;
    font-size: 18px;
    border: none;
    outline: none;
    text-align: center;
    font-family: inherit;
    font-weight: bold;
    background-color: #D7E3EF;
    border: 1px solid #D7E3EF;
    color: #004A8E;
    border-radius: 10px;

    @media(min-width: $mobile) {
        font-size: 30px;
        padding: 20px;
    }

    &.incorrect {
        animation-name: hlb-incorrect;
        animation-duration: 0.2s;
        animation-iteration-count: 2;
    }
}

.hlb-viewer-cta {
    @include pc-new-button();
    font-size: 17px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.hlb-share {
    @include pc-new-button(default, $twitter);
    font-size: 17px;
    margin: 20px 10px 0 10px;
    background-color: $picton-blue;
    border-color: $picton-blue;

    &:hover {    
        background-color: darken($picton-blue, 10%);
        border-color: darken($picton-blue, 10%);
    }
}

.hlb-reset {
    @include pc-new-button();
    font-size: 17px;
    margin: 20px 10px 0 10px;
}
