﻿.sfb-container {
    background-color: #004a8e;
    font-size: 18px;
    color: white;
    z-index: 500;
}

.sfb-fixed {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    box-shadow: 0 5px 10px 5px black;
    
    &.hidden {
        display: none;
    }
}

.sfb-permanent {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    box-shadow: 0 5px 10px 5px black;
}

.sfb-catch {
    width: 100%;
}

.sfb-footer {
    text-align: center;
    padding: 15px 0;
}

.sfb-footer-text {
    display: inline-block;
    margin: 10px 20px;
    vertical-align: middle;
}

.sfb-footer-button {
    @include cta-button(hollow);
    display: inline-block;
    margin: 10px 20px;
    vertical-align: middle;
}