.fctcb-container {
	display: block;
	@include clearfix();
}

.fctcb-left-column,
.fctcb-right-column {
	width: 100%;
	display: block;
	@media (min-width: $mobile) {
		width: 50%;
	}
}

.fctcb-left-column {
	@media (min-width: $mobile) {
		float: left;
		clear: left;
	}
}

.fctcb-right-column {
	@media (min-width: $mobile) {
		float: right;
		clear: right;
	}
}