﻿.cta-banner {

    border-radius: 5px;
    margin: 0 25px;
    padding: 14px;

    @media only screen and (min-width: $desktop) {
        margin: 0;
    }

    .cta-banner-copy {
        margin-top: 20px;
    }

    .cta-banner-link {
        @include branded-button();
	    display: block;
        margin-top: 14px;
        text-align: center;
    }

}