﻿$popup-header-blue: #195F9B;
$popup-font-color: #6CA4D6;

#countryPopup {
    display: none;
    position: fixed;
    top: 0;
    z-index: 10;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,0.5);

    #country-popup-contents {
        border: 5px solid #FFFFFF;
        background: #FFFFFF;
        text-align: center;
        color: $popup-font-color;
        font-size: 14px;
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 10;
        transform: translate(-50%, -50%);
        min-width: 300px;

        .country-popup-innerWrapper {
            position: relative;
            border: 1px solid #333333;
        }

        .country-popup-contentWrapper {
            position: relative;
            padding: 5px;

            h3 {
                font-weight: bold;
                font-size:25px;
            }
        }

        .branding-header {
            position: relative;
            margin: 0px auto;
            display: block;
            background:$popup-header-blue;
            text-align:center;
            padding: 20px 0px 8px;

            img {
                position: relative;
                text-align: center;
                width: 50%;
            }
        }

        .heading {
            padding: 10px 12px;
            font-weight: bold;
        }

        hr {
            width: 30%;
            position:relative;
            display: block;
            height: 1px;
            border: 0;
            border-top: 1px solid $popup-font-color;
            padding: 0; 
        }

        a:hover {
            position: relative;
            text-decoration: none;
            display: block;
        }

        p {
            margin: 12px 0;
        }

        strong {
            font-weight: bold;
        }

        .emphasis {
            margin: 12px 12px -13px;
            padding: 7px 10px 9px;
            font-size: 0.7em;
            border-radius: 5px;
            background: #132C58 no-repeat 15px -16px;
        }

        .btn-link {
            position: relative;
            background-color: #57BB56;
            border: 0 none;
            border-radius: 4px;
            color: white !important;
            display: block;
            width: 80%;
            margin: 10px auto 0;
            padding: 5px 10px;

            &:hover {
                background-color: #31AE20;
                text-decoration: none; 
            }

            &::after {
                content: $arrow-full;
                font-family: "ctm-icons";
                font-size: 19px;
                speak: none;
                font-style: normal;
                font-weight: normal;
                font-variant: normal;
                text-transform: none;
                line-height: 1;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
                position: absolute;
                top: 50%;
                right: 12px;
                margin-top: -9px;
            }
        }

        .cta-stay-in-uk {
            position: relative;
            margin: 10px auto 20px;
            border: 2px solid #57BB56;
            border-radius: 4px;
            display: block;
            width: 80%;
            padding: 5px 10px;

             &:hover {
                border-color: #31AE20;
                text-decoration: none; 
            }

             &::after {
                content: $arrow-full;
                font-family: "ctm-icons";
                font-size: 19px;
                speak: none;
                font-style: normal;
                font-weight: normal;
                font-variant: normal;
                text-transform: none;
                line-height: 1;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
                position: absolute;
                top: 50%;
                right: 12px;
                margin-top: -9px;
            }
        }
    }
}
