$button-font-color: $white;
$button-bg-color: $brand-green;
$button-border-color: $brand-green;
$button-hover-font-color: $white;
$button-hover-bg-color: $brand-green-dark;
$button-hover-border-color: transparent;
$button-inverse-font-color: $brand-green;
$button-inverse-bg-color: $white;
$button-inverse-border-color: transparent;
$button-inverse-hover-font-color: $brand-green-hover;
$button-inverse-hover-border-color: #E8EFF5;
$button-inverse-hover-bg-color: #E8EFF5;
$button-hollow-font-color: $white;
$button-hollow-border-color: $white;
$button-hollow-bg-color: transparent;
$button-hollow-hover-font-color: $white;
$button-hollow-hover-border-color: $brand-green;
$button-hollow-hover-bg-color: transparent;
$button-hollow-on-white-font-color: $brand-blue-dark;
$button-hollow-on-white-border-color: $brand-blue-dark;
$button-hollow-on-white-hover-border-color: $brand-green;
$button-hollow-on-white-hover-font-color: $brand-green;
$play-button-border-color: $white;
$play-button-font-color: $brand-green;
$play-button-hover-border-color: $brand-green;
$play-button-hover-font-color: $brand-green;
$play-button-inverse-border-color: $white;
$play-button-inverse-font-color: $white;
$play-button-inverse-hover-border-color: #85a9ca;
$play-button-inverse-hover-font-color: #85a9ca;
$article-title-underline-color: $deep-ice;
$dark-overlay-color: $mine-shaft;
$two-things-line-left-bg-color: #255b99;
$two-things-line-right-bg-color: #3ea931;

@mixin content-width() {
    max-width: 940px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
}

@mixin title($size: 'small', $pseudo-position: 'left', $colour: $white) {
    @if ($size == 'large') {
        font-size: 34px;
    }

    @else {
        font-size: 28px;
    }

    margin-bottom: 15px;
    line-height: 1.3;
    letter-spacing: -1px;

    &:after {
        content: '';
        display: block;
        width: 70px;
        height: 2px;
        margin-top: 15px;
        background-color: currentColor;
    }

    @if ($pseudo-position == 'center') {
        &:after {
            margin-left: auto;
            margin-right: auto;
        }
    }
}

@mixin cta-button($style: default, $icon: $arrow-full) {
    display: inline-block;
    position: relative;
    margin-top: 15px;
    margin-left: auto;
    margin-right: auto;
    padding: 8px 40px 8px 15px;
    min-width: 160px;
    font-size: 17px;
    font-weight: 700;
    line-height: 1.2;
    color: $button-font-color;
    text-align: left;
    background-color: $button-bg-color;
    border-radius: 8px;
    border: 2px solid $button-border-color;
    box-shadow: 0 2px 2px rgba(0,0,0,0.3);
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.17);
    cursor: pointer;

    &:after {
        @include icon($icon, 19px, $button-font-color);
        position: absolute;
        top: 50%;
        right: 12px;
        margin-top: -9px;
    }

    &:hover {
        color: $button-hover-font-color;
        border-color: lighten($button-bg-color, 7%);
        background-color: lighten($button-bg-color, 7%);

        &:after {
            color: $button-hover-font-color;
        }
    }

    @if ($style == 'inverse') {
        color: $button-inverse-font-color;
        border-color: $button-inverse-border-color;
        background-color: $button-inverse-bg-color;

        &:after {
            color: $button-inverse-font-color;
        }

        &:hover {
            color: $button-inverse-hover-font-color;
            border-color: $button-inverse-hover-border-color;
            background-color: $button-inverse-hover-bg-color;

            &:after {
                color: $button-inverse-hover-font-color;
            }
        }
    }

    @if ($style == 'hollow') {
        color: $button-hollow-font-color;
        border-color: $button-hollow-border-color;
        background-color: $button-hollow-bg-color;

        &:after {
            color: $button-hollow-font-color;
        }

        &:hover {
            color: $button-hollow-hover-font-color;
            border-color: $button-hollow-hover-border-color;
            background-color: $button-hollow-hover-bg-color;

            &:after {
                color: $button-hollow-hover-font-color;
            }
        }
    }

    @if ($style == 'hollow-on-white') {
        color: $button-hollow-on-white-font-color;
        border-color: $button-hollow-on-white-border-color;
        background-color: $button-hollow-bg-color;
        box-shadow: 0 2px 2px rgba(255,255,255,0.3);
        text-shadow: 0 1px 1px rgba(255, 255, 255, 0.17);

        &:after {
            color: $button-hollow-on-white-font-color;
        }

        &:hover {
            color: $button-hollow-on-white-hover-font-color;
            border-color: $button-hollow-on-white-hover-border-color;
            background-color: $button-hollow-hover-bg-color;

            &:after {
                color: $button-hollow-on-white-hover-font-color;
            }
        }
    }

    @if ($style == 'new-default') {
        display: block;
        text-align: center;
        text-shadow: none;
        box-shadow: 0 3px 0  1px rgb(43, 139, 39);
        border-radius: 30px;
        padding: 10px 30px;
        font-size: 24px;

        &:after {
            content: "";
        }

        &:hover {
            background-color:$button-bg-color;
            border: 2px solid $button-border-color;
        }
    }
}

@mixin cta-number {
    display: inline-block;
    position: relative;
    margin: 5px auto;
    padding: 4px 15px 4px 15px;
    min-width: 160px;
    font-size: 24px;
    font-weight: 700;
    line-height: 1.2;
    color: $button-font-color;
    text-align: center;
    background-color: $button-bg-color;
    border-radius: 8px;
    border: 2px solid $button-border-color;
    box-shadow: 0 2px 2px rgba(0,0,0,0.3);
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.17);
    cursor: pointer;

    &:hover {
        color: $button-hover-font-color;
        border-color: lighten($button-bg-color, 7%);
        background-color: lighten($button-bg-color, 7%);

        &:after {
            color: $button-hover-font-color;
        }
    }

    color: $button-hollow-font-color;
    border-color: $button-hollow-border-color;
    background-color: $button-hollow-bg-color;

    &:after {
        color: $button-hollow-font-color;
    }

    &:hover {
        color: $button-hollow-hover-font-color;
        border-color: $button-hollow-hover-border-color;
        background-color: $button-hollow-hover-bg-color;

        &:after {
            color: $button-hollow-hover-font-color;
        }
    }
}

@mixin play-button($style: default) {
    display: block;
    width: 50px;
    height: 50px;
    border: 2px solid $play-button-border-color;
    border-radius: 50%;
    position: relative;
    margin: 20px auto 0;

    &:after {
        @include icon($play, 16px, $play-button-font-color);
        position: absolute;
        top: 14px;
        left: 18px;
    }

    &:hover {
        border-color: $play-button-hover-border-color;

        &:after {
            color: $play-button-hover-font-color;
        }
    }

    @if ($style == 'inverse') {
        border-color: $play-button-inverse-border-color;

        &:after {
            color: $play-button-inverse-font-color;
        }

        &:hover {
            border-color: $play-button-inverse-hover-border-color;

            &:after {
                color: $play-button-inverse-hover-font-color;
            }
        }
    }
}

@mixin article-title($color: $article-title-underline-color) {
    font-size: 1.8em;
    margin: 40px 0;
    position: relative;

    &:before, &:after {
        content: '';
        position: absolute;
        top: -12px;
        left: 0;
        width: 60px;
        height: 2px;
        background-color: $deep-ice;
    }

    &:after {
        top: auto;
        bottom: -15px;
    }
}

@mixin dark-overlay {
    background-color: $dark-overlay-color;
    opacity: 0.4;
    height: 100%;
    width: 100%;
    position: absolute;
    right: 0;
    top: 0;
}

@mixin two-things-line($left-bg: $two-things-line-left-bg-color, $right-bg: $two-things-line-right-bg-color) {
    width: 100%;

    &:before,
    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        height: 6px;
        width: 50%;
    }

    &:before {
        left: 0;
        background-color: $left-bg;
    }

    &:after {
        right: 0;
        background-color: $right-bg;
    }
}

@mixin icon-center($size) {
    font-size: $size;
    margin-top: -$size / 2;
}

