﻿.martha-testing {
    .ratings-reviews-panel {
        @extend .panel;
        padding-bottom: 0;
    }

    .see-reviews-block {
        text-align: center;
        padding: 15px 0;

        @media(min-width: $mobile) {
            padding: 30px 0;
        }

        padding-top: 0;
    }

    .see-reviews-block-button {
        @include cta-button(inverse);
        box-shadow: 0 2px 2px 1px rgba(0,0,0,0.3);
    }
}
