.collapsible-content-block {
    background-color: #ffffff;


    .panel {
    }


    ul {
        list-style: inside disc;

        li {
            margin: 0 0 8px 0;
        }
    }

    .content-top {
        @include flexbox();
        @include justify-content(space-between);
        padding-bottom: 20px;
        border-top: 2px solid $link-water;

        &.no-border {
            border: none;
        }

        @media (min-width: $mobile) {
            padding-bottom: 30px;
            padding-top: 30px;
        }
    }

    .title-container {
        width: 100%;
        padding: 0 20px 20px 20px;

        @media (min-width: $mobile) {
            padding: 0;
        }

        @media (min-width: $tablet) {
            width: 56%;
            margin-right: 3%;
			@include inline-block;
        }
    }

    .title {
        @include font-size(1.8);
        line-height: 1;
        font-weight: 700;
        color: $primary-blue;
        display: inline-block;
        padding-right: 0;
        padding-bottom: 0.5em;

        @media (min-width: $desktop) {
            @include font-size(2.4);
            padding-right: 2em;
        }
    }

    .below-title-text {
        color: $black;

        h3 {
            font-size: 1rem;
        }
    }

    .quote-area {
        width: 40%;
        display: none;
        vertical-align: top;
		
        @media (min-width: $tablet) {
           @include inline-block;
        }

        img.quote {
            max-width: 100%;
        }

		.image-text {
			color: $black;
		}
    }
}

.collapsible-content-container {
    width: 100%;
    display: block;
    margin-top: 10px;
    padding: 0;
    padding: 0 0 25px 0;
    
    @media(min-width: $mobile) {
        padding-top: 0px;
        padding-bottom: 20px;
    }

    .collapsible-content-link {
        color: $brand-green;
        margin-bottom: 20px;
        position: relative;
        padding-left: 54px;
        cursor: pointer;
        display: inline;
        padding: 20px 25px 20px 54px;

        a {
            text-decoration: underline;
            font-weight: 700;
            font-size: 1.1rem;
        }

        &:before {
            @include icon($info-circle,22px,$brand-green);
            margin-right: 10px;
            text-decoration: none;
            position: absolute;
            top: 20px;
            left: 20px;
            display: inline-block;
        }
    }

    .collapsible-content {
        color: $black;
        display: none;
        line-height: 1.3;

        @media (min-width: $desktop) {
            border-radius: 5px;
        }

        p {
            padding: 5px 0;
        }
    }

    &.open-content {
        .collapsible-content-link, .collapsible-content {
            background-color: $link-water;
        }

        .collapsible-content-link {
            border-radius: 5px 5px 0 0;

            &:before {
                @include icon($close-circle,22px,$brand-green);
            }
        }


        .collapsible-content {
            display: block;
            padding: 25px;
        }
    }
}

.collapsible-quote {
    color: $brand-green;
    @include font-size(2.4);
    line-height: 1;
    font-weight: 700;
    position: relative;
    padding-top: 25px;


    &:before {
        @include icon($quotes,20px,$brand-green);
        position: absolute;
        top: 0;
    }
}

.martha-testing 
{
    
    $ccb-button-color: #38b132;
    $ccb-heading-color: #004A8E;
    $ccb-body-color: #333;
    $ccb-border-color: #E6EBF2;
    
    .ccb-wrapper
    {
        background-color: $white;
        border-top: 1px solid $ccb-border-color;

        .panel
        {
            border-top: 0;
            padding-top: 20px;
            padding-bottom: 20px;
            
            @media(min-width: $mobile) {
                padding-top: 60px;
                padding-bottom: 60px;
            }

            @include clearfix;
        }

        .ccb-title-container
        {
            color: $ccb-heading-color;
            font-weight: 700;
            font-size: 35px;
            line-height: 50px;
            letter-spacing: -1px;
            width: 100%;
            padding: 10px;
            float: none;

            
            @media(min-width: $mobile) {
                width: 40%;
                max-width: 300px;
                margin-right: 8%;
                padding-left: 0;
                float: left;
            }

        }

        .ccb-title {
            font-size: 1.4rem;
            line-height: 1.2;
        }

        .ccb-title-underline
        {
            @include title(small, left);
            
            &:after
            {
                background-color: $ccb-heading-color;
            }
        }

        .ccb-question-content
        {
            line-height: 24px;
            font-size: 16px;
            color: $ccb-body-color;
            width: 100%;
            padding: 10px;
            float: none;
            
            @media(min-width: $mobile) {
                width: 52%;
                float: left;
                padding: 0;
                padding-top: 10px;
            }

            a
            {
                @extend .underlinedTextOnHover;
            }
        }

        .ccb-collapsible-content
        {
            @include clearfix;
            display: block;
            padding: 10px;
            
            @media(min-width: $mobile) {
                padding: 0px;
            }

            .collapsible-content-link {
                @include cta-button('hollow');
                border-color: $ccb-button-color;
                color: $brand-green;
                font-family: "SUN";

                &:after {
                    color: $ccb-button-color;
                }
                
                &:hover {
                    color: $brand-green;
                    &:after {
                        color: $ccb-button-color;
                    }
                }
            }


            .collapsible-content {
                display: none;
                padding: 25px;
                padding-left: 0;
                color: $ccb-body-color;
            } 

            &.open-content {
                .collapsible-content {
                    display: block; 
                }
            }
        }
    }

}
