﻿//*****************************************************************
// mixins.scss
// defines mixin functions for use in styles
//*****************************************************************

@mixin shadow($top, $left, $blur, $colour: rgba(26, 56, 116, 0.7), $inset:"") {
	box-shadow: $top $left $blur $colour, $inset;
}

@mixin drop-shadow-speech-bubble {
	box-shadow: 0 1px 4px 1px rgba(0, 0, 0, 0.3);
}

@mixin drop-shadow-calendar {
	box-shadow: 0 1px 4px 3px rgba(0, 0, 0, 0.4);
}

@mixin drop-shadow-button {
	box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.1);
}

@mixin drop-shadow-white {
	box-shadow: 0 0 8px -2px $white;
}

@mixin content-box {
	-webkit-box-sizing: content-box;
	-moz-box-sizing: content-box;
	box-sizing: content-box;
}

@mixin drop-shadow-logo {
	box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.3);
}

@mixin drop-shadow-help-icon {
	box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.25);
}

@mixin border-top-radius($topradius) {
	border-top-left-radius: $topradius;
	border-top-right-radius: $topradius;
}

@mixin border-bottom-radius($bottomradius) {
	border-bottom-left-radius: $bottomradius;
	border-bottom-right-radius: $bottomradius;
}

@mixin border-right-radius($rightradius) {
	border-top-right-radius: $rightradius;
	border-bottom-right-radius: $rightradius;
}

@mixin border-left-radius($leftradius) {
	border-top-left-radius: $leftradius;
	border-bottom-left-radius: $leftradius;
}

@mixin fontsize($fontsize) {
	font-size: $fontsize * 19 + px;
	font-size: $fontsize + rem;
}

@mixin body-radial-bg {
	background-color: $primary-blue;
	background: $primary-blue -webkit-radial-gradient(25% -100%, ellipse cover, #4E79E8 30%, $primary-blue 50%) no-repeat;
	background: $primary-blue -moz-radial-gradient(25% 0% 174deg, #4E79E8, $primary-blue 30%) no-repeat;
	background: $primary-blue radial-gradient(at 30% 0%, #4E79E8, $primary-blue 25%) no-repeat;
	.ie9 &, .ie8 & {
		background: url('../img/bg_ie.jpg') no-repeat center top $primary-blue;
		min-width: 990px;
	}
}

@mixin border-box {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}

//NEW MIXINS

@mixin flexbox() {
	display: -webkit-box;
	display: -moz-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
}

@mixin flexflow($direction, $wrap) {
	-webkit-flex-flow: $direction $wrap;
	-moz-flex-flow: $direction $wrap;
	-ms-flex-flow: $direction $wrap;
	flex-flow: $direction $wrap;
}

@mixin flex($values, $width) {
	-webkit-box-flex: $values, $width;
	-moz-box-flex: $values, $width;
	-webkit-flex: $values, $width;
	-ms-flex: $values, $width;
	flex: $values, $width;
	width: $width;
}

@mixin align-items($align) {
	align-items: $align;
    -webkit-align-items: $align; /* Safari 7.0+ */
}

@mixin justify-content($value) {
	-webkit-justify-content: $value; /* Safari 6.1+ */
    justify-content: $value;
}

@mixin flex-grow($value) {
	-webkit-flex-grow: $value;
	-moz-flex-grow: $value;
	-ms-flex-grow: $value;
	flex-grow: $value;
}

@mixin flex-shrink($value) {
	-webkit-flex-shrink: $value;
	-moz-flex-shrink: $value;
	-ms-flex-shrink: $value;
	flex-shrink: $value;
}

@mixin flex-basis($value) {
	-webkit-flex-basis: $value;
	-moz-flex-basis: $value;
	-ms-flex-basis: $value;
	flex-basis: $value;
}

@mixin order($val) {
	-webkit-box-ordinal-group: $val;
	-moz-box-ordinal-group: $val;
	-ms-flex-order: $val;
	-webkit-order: $val;
	order: $val;
}

@mixin justify-blocks() {
	text-align: justify;
	&:after {
		content: "";
		width: 100%;
		display: inline-block;
	}
}

@mixin transition($transition...) {
    -moz-transition: $transition;
    -o-transition: $transition;
    -webkit-transition: $transition;
    transition: $transition;
}
@mixin transition-property($property...) {
    -moz-transition-property: $property;
    -o-transition-property: $property;
    -webkit-transition-property: $property;
    transition-property: $property;
}
@mixin transition-duration($duration...) {
    -moz-transition-property: $duration;
    -o-transition-property: $duration;
    -webkit-transition-property: $duration;
    transition-property: $duration;
}
@mixin transition-timing-function($timing...) {
    -moz-transition-timing-function: $timing;
    -o-transition-timing-function: $timing;
    -webkit-transition-timing-function: $timing;
    transition-timing-function: $timing;
}
@mixin transition-delay($delay...) {
    -moz-transition-delay: $delay;
    -o-transition-delay: $delay;
    -webkit-transition-delay: $delay;
    transition-delay: $delay;
}

// generic transform
@mixin transform($transforms) {
    -moz-transform: $transforms;
    -o-transform: $transforms;
    -ms-transform: $transforms;
    -webkit-transform: $transforms;
    transform: $transforms;
}
 
// scale
@mixin scale($scale) {
    @include transform(scale($scale));
} 
// translate
@mixin translate ($x, $y) {
	@include transform(translate($x, $y));
}

@mixin translate3d ($x, $y, $z) {
	@include transform(translate3d($x, $y, $z));
}

// skew
@mixin skew ($x, $y) {
    @include transform(skew(#{$x}deg, #{$y}deg));
}

//transform origin
@mixin transform-origin ($origin) {
    moz-transform-origin: $origin;
    -o-transform-origin: $origin;
    -ms-transform-origin: $origin;
    -webkit-transform-origin: $origin;
    transform-origin: $origin;
}

@mixin gradient-vertical ($start-color: $light-primary-blue, $end-color: $dark-primary-blue) {
	background-color: $end-color;
	background-image: -webkit-linear-gradient(top, $start-color 0%, $end-color 100%);
	background-image: linear-gradient(to bottom, $start-color 0%, $end-color 100%);
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#{ie-hex-str($start-color)}', endColorstr='#{ie-hex-str($end-color)}', GradientType=1);
}

@mixin gradient-radial($inner-color: #555, $outer-color: #333) {
	background-color: $outer-color;
	background-image: -webkit-radial-gradient(circle, $inner-color, $outer-color);
	background-image: radial-gradient(circle, $inner-color, $outer-color);
	background-repeat: no-repeat;
}

@mixin marketing-border-gradient {
	background-color: #847844;
	background-image: -webkit-linear-gradient(left, #f8dcb4 0%, #847844 20%, #f8dcb4 60%, #847844 100%);
	background-image: linear-gradient(to right, #f8dcb4 0%, #847844 20%, #f8dcb4 60%, #847844 100%);
	filter: progid:DXImageTransform.Microsoft.gradient(GradientType=1, startColorstr='#847844', endColorstr='#f8dcb4');
}

@mixin media-block-gradient {
	background: -moz-linear-gradient(top,  rgba(0,0,0,0.01) 0%, $black 23%); /* FF3.6+ */
	background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(0,0,0,0.01)), color-stop(23%,$black)); /* Chrome,Safari4+ */
	background: -webkit-linear-gradient(top,  rgba(0,0,0,0.01) 0%,$black 23%); /* Chrome10+,Safari5.1+ */
	background: -o-linear-gradient(top,  rgba(0,0,0,0.01) 0%,$black 23%); /* Opera 11.10+ */
	background: -ms-linear-gradient(top,  rgba(0,0,0,0.01) 0%,$black 23%); /* IE10+ */
	background: linear-gradient(to bottom,  rgba(0,0,0,0.01) 0%,$black 23%); /* W3C */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='transparent', endColorstr='$black',GradientType=0 ); /* IE6-9 */
}

//Add font familys' in dynamically
@mixin font-face($list) {
    @each $font in $list {
        @font-face {
            font-family: nth($font, 1);
            src: url("#{nth($font, 2)}.eot");
            src: url("#{nth($font, 2)}.eot?#iefix") format("embedded-opentype"),
                 url("#{nth($font, 2)}.svg?##{nth($font, 1)}") format("svg"),
                 url("#{nth($font, 2)}.woff") format("woff"),
                 url("#{nth($font, 2)}.ttf") format("truetype");
            font-weight: #{nth($font, 3)};
            font-style: #{nth($font, 4)};
        }
    }
}

@mixin font-size($size, $line-height: 1.125) {
	@include fontsize($size);
    line-height: ($size * $line-height) + rem;
}

@mixin user-select($val:all) {
    -webkit-user-select: $val;
    -moz-user-select: $val;
    -ms-user-select: $val;
    -o-user-select: $val;
    user-select: $val;   
}


@mixin clearfix {
    &:before,
    &:after {
        content: " ";
        display: block;
        clear: both;
    }

    /**
     * For IE 6/7 only
     * Include this rule to trigger hasLayout and contain floats.
     */
    *zoom: 1;
}



/**
* RESPONSIVE 2015 REDESIGN
*
*/

@mixin linear-gradient($fromColor, $toColor, $filter: true) {
    background-color: $toColor; /* Fallback Color */
    background-image: -webkit-gradient(linear, left top, left bottom, from($fromColor), to($toColor)); /* Saf4+, Chrome */
    background-image: -webkit-linear-gradient(top, $fromColor, $toColor); /* Chrome 10+, Saf5.1+, iOS 5+ */
    background-image: -moz-linear-gradient(top, $fromColor, $toColor); /* FF3.6 */
    background-image: -ms-linear-gradient(top, $fromColor, $toColor); /* IE10 */
    background-image: -o-linear-gradient(top, $fromColor, $toColor); /* Opera 11.10+ */
    background-image: linear-gradient(top, $fromColor, $toColor);
    
	@if $filter {
		filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0,StartColorStr='#{$fromColor}', EndColorStr='#{$toColor}');
	}
}

@mixin border-radius ($radius, $section: default) {
	//Extended this mixin to have top and bottom as a radius
	@if $section == 'default' {
		border-radius: $radius;
	    -moz-border-radius-: $radius;
	    -webkit-border-radius: $radius;
	}

	@if $section == 'top' {
		-webkit-border-top-left-radius: $radius;
		-webkit-border-top-right-radius: $radius;
		-moz-border-radius-topleft: $radius;
		-moz-border-radius-topright: $radius;
		border-top-left-radius: $radius;
		border-top-right-radius: $radius;
	}

	@if $section == 'bottom' {
		-webkit-border-bottom-left-radius: $radius;
		-webkit-border-bottom-right-radius: $radius;
		-moz-border-radius-bottomleft: $radius;
		-moz-border-radius-bottomright: $radius;
		border-bottom-left-radius: $radius;
		border-bottom-right-radius: $radius;
	}
	
}

@mixin branded-button {
    color: $white;
    border: none;
    cursor: pointer;
    font-weight: 700;
    font-size: 1em;
    line-height: 1em;
    width: auto;
    padding: 15px;
    text-decoration: none;
    @include border-radius(5px);
    @include linear-gradient($brand-green,$brand-green-dark);

    &:hover{
        @include linear-gradient($brand-green-dark,$brand-green);
    }
}

@mixin meerkat-branded-button($position: left, $icon: $double-chevron-right, $borders: true, $start-color: $forest-green, $end-color: $slytherin) {
	//$position decides whether or not the button is centrally aligned, needs refining at a later date
	@if $position == 'center' {
		margin: 0 auto;
	}

	@else {
		margin: 0;

	}

	margin-top: 20px;
	color: $white;
	display: inline-block;
    font-size: 18px;
    
    @media only screen and (min-width: $mobile-low) {
        line-height: 1.3;
    }

    padding: 6px 45px 6px 20px;
	text-align: left;
	width: auto;
	border-bottom: none;
    line-height: 1.3;
	font-family: $font-sun3;
	font-weight: normal;
	@include linear-gradient($start-color, $end-color);
	text-decoration: none;
	position: relative;
	cursor: pointer;
	border-radius: 0;
	border: none;

	@if $borders == true {
        &:before {
            content: "";
            background-color: transparent;
            position: absolute;
            height: 100%;
            width: 92%;
            left: 4%;
            top: -3px;
            border-top: 2px solid $start-color;
            border-bottom: 2px solid $end-color;
            padding-bottom: 2px;
            @include content-box;
        }
	}
	
    @else {

    }

	&:hover {
		@include linear-gradient($end-color, $start-color);
	}
	
	&:after {
        @include icon ($icon, 8px, $white);
        font-style: normal;
        position: absolute;
        right: 20px;
        top: 50%;
        margin-top: -3px;
	}
}

@mixin meerkat-font($size: 32px, $uppercase: true) {
    font-family: "fjalla-one", "SUN", Verdana, Arial, sans-serif;
    letter-spacing: normal;

    @if $size {
        font-size: $size;
    }

    @if $uppercase {
        text-transform: uppercase;
    }
}

@mixin branded-button-small {
	@include branded-button;
	padding: .6em .8em;
}

@mixin has-title-flourish {
    &.has-title-flourish {
        &:after {
            @include icon($border-flourish, 36px, $flourish-gold);
            text-align: center;
            display: block;
            margin-top: 10px;
            margin-bottom: 40px;
            background: none;
            width: auto;
         }
    }
}
@mixin border-image($image, $size, $property) {
    border-image: url('../images/#{$image}.svg') $size $property;
}

@mixin has-border-flourish($padding: "", $margin: "", $min-width: $mobile) {
    &.has-border-flourish {
        @if $padding != "" {
            padding: $padding;
        }
        
        @if $margin != "" {
            margin: $margin;    
        }
        &:after {
            @media screen and (min-width: $min-width) {
                border: 8px solid transparent;
                @include border-image(funny-corners, 100, repeat);
                border-image-width: 6;
                padding: 20px;
                content: '';
                position: absolute;
                top: 10px;
                bottom: 10px;
                left: 10px;
                right: 10px;
                pointer-events: none;
            }
        }

        &.no-borderimage {
            &:after {
                @media screen and (min-width: $mobile) {
                    border: 1px solid $driftwood;
                    padding: 20px;
                    content: '';
                    position: absolute;
                    top: 10px;
                    bottom: 10px;
                    left: 10px;
                    right: 10px;
                }
            }
        }
    }
}

@mixin diagonal-gradient-border ($fromColor, $toColor) {
	border: 2px solid transparent;
	-moz-border-image: -moz-linear-gradient(top left, $fromColor 0%, $toColor 100%);
	-webkit-border-image: -webkit-linear-gradient(top left, $fromColor 0%, $toColor 100%);
	border-image: linear-gradient(to bottom right, $fromColor 0%, $toColor 100%);
	border-image-slice: 1;
}

@mixin icon($content, $font-size: inherit, $color: inherit) {
	content: $content;
	font-family: $font-icons;
	font-size: $font-size;
	color: $color;
}

@mixin add-panel-arrow($colour){
    .panel{
        &.arrow {
        	&:after{
	            position: absolute;
	            left: 50%;
	            bottom: -15px;
	            content:"";
		        width: 0; 
		        height: 0; 
		        border-left: 15px solid transparent;
		        border-right: 15px solid transparent;
		        border-top: 15px solid $colour;
		        margin-left: -15px;
	    	}
        }
    }
}

@mixin inline-block($alignment: top) {
  display: inline-block;
  vertical-align: $alignment;
  zoom: 1;
  *display: inline;
  *vertical-align: auto;
}

@mixin intertia-title($align: center) {
	display: block;
	text-align: $align;
    font-size: 34px;
    font-weight: 700;
    margin-bottom: 30px;
    line-height: 1.3;
}

@mixin opacity($trans) {
  filter: alpha(opactiy=($trans * 100));
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=#{$trans * 100})";
  -moz-opacity: $trans;
  -khtml-opacity: $trans;
  opacity: $trans;
}

@mixin absolute-center($direction, $size) {
    position: absolute;
    @if ($direction == 'vertical') {
        top: 50%;
        margin-top: -$size / 2;
    }
    @if ($direction == 'horizontal') {
        left: 50%;
        margin-left: -$size / 2;
    }
}

@mixin underline($space: 10px, $color: $white, $width: 70px, $thickness: 3px) {
    &:after {
        content: "";
        display: block;
        width: $width;
        height: $thickness;
        margin: $space auto 0;
        background: $color;
    }
}
