﻿$meganav-tablet: 590px;
$meganav-desktop: 810px;

html {
    // handling mobile/tablet expanding animations    // handling mobile/tablet expanding animations

    &.mn__expanded #meganav {
        display: block;
		padding-bottom: 15px;
		background-color: $brand-blue-dark;
    }

    #meganav {
        display: none;

        @media (max-width: $meganav-tablet) {
            height: auto !important;
        }

        @media (min-width: $meganav-desktop) {
            height: auto !important;
            display: block;
        }
    }

    @media (min-width: $meganav-tablet) and (max-width: $meganav-desktop) {
        #mainForm {
            transition: transform 660ms cubic-bezier(0.22, 0.61, 0.36, 1);
            -webkit-transition: -webkit-transform 660ms cubic-bezier(0.22, 0.61, 0.36, 1);
        }

        &.mn__expanded {
            body {
                height: 100%;
                overflow: hidden;
                position: relative;
            }

            #mainForm {
                -webkit-transform: translateX(-380px);
                transform: translateX(-380px);
            }

            .global-site-wrapper {
                overflow: visible;
            }

            #mn__overlay {
                background-color: #000000;
                cursor: pointer;
                height: 100%;
                left: 0;
                opacity: 0.7;
                position: absolute;
                top: 0;
                z-index: 10;
                width: 100%;
            }

            #meganav {
                background-color: #004a8e;
                transition: transform 660ms cubic-bezier(0.22, 0.61, 0.36, 1);
                -webkit-transition: -webkit-transform 660ms cubic-bezier(0.22, 0.61, 0.36, 1);
                overflow-y: scroll;
                position: absolute;
                right: 0;
                top: 0;
                -webkit-transform: translateX(380px);
                transform: translateX(380px);
                width: 380px;
                z-index: 2;
            }
        }
    }
}

.main-header {
    @media (min-width: $meganav-desktop) {
        padding-left: 6px;
    }

    #mn__toggle {
        background: transparent;
        font-family: inherit;
        padding: 2px 0 0;
        text-align: center;
        width: 45px;

        @media (min-width: $desktop-nav) {
            padding-top: 16px;
        }

        @media (min-width: $meganav-desktop) {
            display: none;
        }
    }
}

.mn__group {
    @media (min-width: $meganav-desktop) {
        float: left;
    }

    &#mn__insurance {
        @media (min-width: $meganav-desktop) {
            width: 400px;
        }
    }

    &#mn__utilities {
        @media (min-width: $meganav-desktop) {
            width: 325px;
        }
    }

    &#mn__faqs {
        @media (min-width: $meganav-desktop) {
            width: 100px;
        }

        .mn__dropdown {
            background-image: none;

            .mn__dropdown-inner {
                background-image: none;
            }
        }
    }

    &#mn__rewards {
        .mn__dropdown {
            background-image: none;

            .mn__dropdown-inner {
                background-image: none;

                .mn__dropdown-column:last-child {
                    background-color: white;

                    .mn__dropdown-link-header {
                         border-top: 1px solid rgba(#89a7d8, 0.3);
                         @media (min-width: $meganav-desktop) {
                            border: 0;
                        }
                    }
                }
            }
        }
    }

    // Top Level Headings
    .mn__group-heading {
        width: 100%;
        font-size: 18px;
        line-height: 48px;
        padding-top: 15px;

        @media (min-width: $meganav-desktop) {
            background-color: transparent;
            font-size: 14px;
            line-height: inherit;
            padding: 15px 10px 0;
        }

        a {
            padding: 0 15px;
            display: block;

            @media (min-width: $meganav-desktop) {
                padding: 0;
				color: $white;
            }
        }
    }

    // Top Level Group of Links
    .mn__group-body {
		border-bottom: 1px solid $deep-ice;
        @media (min-width: $meganav-desktop) {
            padding: 0;
			border-bottom: 0px;
            background-color: transparent;
        }
    }
        &:last-child .mn__group-body {
            border-bottom: 0px;
        }
}

.mn__dropdown {
    background-color: #ffffff;
    display: none;

    @media (min-width: $meganav-desktop) {
        background-image: -moz-linear-gradient(left, #ffffff 60%, #e4f1fd 60%);
        background-image: -webkit-linear-gradient(left, #ffffff 60%, #e4f1fd 60%);
        background-image: -o-linear-gradient(left, #ffffff 60%, #e4f1fd 60%);
        background-image: -ms-linear-gradient(left, #ffffff 60%, #e4f1fd 60%);
        background-image: linear-gradient(to right, #ffffff 60%, #e4f1fd 60%);
        left: 0;
        position: absolute;
        width: 100%;
        z-index: 2;
        border-bottom: 1px solid #89a7d8;
    }
}

.mn__dropdown-column {
    width: 100%;

	&.mn__active {
		.mn__dropdown-link-list {
			display: block;
		}
		.mn__link-header-chevron {
                background-image: url('../../../assets/css/hardcoded-nav-images/chevron_up_green.png');
        }
	}

    @media (min-width: $meganav-desktop) {
        width: 160px;
		padding: 0 12px 24px 0;
		float: left;
    }

    &:last-child {
        background-color: #e4f1fd;

        @media (min-width: $meganav-desktop) {
            margin: 0;
            padding: 0 20px 0 10px;
            width: 160px;
        }
    }

	&.mn__no-href {
		.mn__dropdown-link-header {
			display: block;
			padding: 20px 0;
			border-bottom: 0;		
			@media (min-width: $meganav-desktop) {
				padding: 8px 0;				
			}
		}
		.mn__dropdown-link-list {
			border-bottom: 0;
		}	
	}

    &.mn__border-left {
        background-color: white;
        margin: 0;
        padding: 0;
        border-left: 0;
        width: 100%;

        @media (min-width: $meganav-desktop) {
            width: 160px;
			padding: 0 12px 24px 10px;
            border-left: 1px solid #89a7d8;
            border-top: 0;
        }
    }

    &.mn__dropdown-column_2 {
        width: 100%;

        @media (min-width: $meganav-desktop) {
            width: 320px;
        }
    }
	.mn__dropdown-link-list{
		display: none;
		border-top: 1px solid rgba(#89a7d8, 0.3);
		margin-left: 55px;
		padding: 20px 20px 20px 0;

        &:first-child {
            border-top: 0;
        }

		@media (min-width: $meganav-desktop) {
			display: block;
            margin-left: 0;
            margin-right: 0;
			border-top: 0;
			padding: 0;

        }
	}
}

.mn__dropdown-column--no-header {

    // move closer to drop down link lists that do have headers
    .mn__dropdown-link-list {
        margin-top: -40px;
    }

    @media (min-width: $meganav-desktop) {
        margin-top: 32px;
        min-width: 24px;

        .mn__dropdown-link-list {
            margin-top: 0;
        }
    }
}

.mn__dropdown-inner {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
    flex-wrap: wrap;
    /*padding: 22px 5% 0;*/

    @media (min-width: $meganav-desktop) {
        padding: 0;
    }
}

.mn__dropdown-link-header {
    color: #1f2a5b;
    font-size: 18px;
    font-weight: bold;
    line-height: 16px;
	margin-left: 55px;
	border-top: 1px solid rgba(#89a7d8, 0.3);
    text-decoration: none;
	position: relative;
    @media (min-width: $meganav-desktop) {
        font-weight: normal;
    }

    &.mn__header-bold {
        font-weight: bold;
    }

    .mn__dropdown-column:first-of-type &,
    .mn__dropdown-column:last-of-type & {
        border-top: 0;
    }

	
    a {
        display: block;
		color: #1f2a5b;
		padding: 20px 10px 20px 0;
		margin-right: 55px;
		border-right: 1px solid rgba(#89a7d8, 0.3);

        &:hover {
            text-decoration: underline;
        }
		@media (min-width: $meganav-desktop) {
			padding: 0;
			display: inline;
			border-right: 0;

		}

    }
	.mn__link-header-chevron--container{
		position: absolute;
		right: 0px;
		top: 0px;
		height: 56px;
		width: 55px;
	}
	
	.mn__link-header-chevron {
		background-image: url('../../../assets/css/hardcoded-nav-images/chevron_down_green.png');
		background-repeat: no-repeat;
		background-size: auto 100%;
		display: block;
		height: 8px;
		position: absolute;
		right: 20px;
		top: 24px;
		width: 14px;
		@media (min-width: $meganav-desktop) {
			display: none;
		}
	}

	@media (min-width: $meganav-desktop) {
		padding: 8px 0; 
		margin-left: 0;
		border-top: 0;
		font-size: 15px;
	}
		
}

.mn__dropdown-link {
    color: #1f2a5b;
    font-size: 18px;
    line-height: 16px;
    padding: 8px 0;

    &:hover {
        text-decoration: underline;
    }

    a {
        color: #1f2a5b;
        text-decoration: none;

        img {
            margin-bottom: 8px;
        }
    }
	@media (min-width: $meganav-desktop) {
		font-size: 15px;

    }
}

.mn__nav-section {
    @media (min-width: $meganav-desktop) {
        float: left;
    }

    .mn__nav-link {
        /*background-color: $brand-blue-dark*/;
        font-size: 18px;
        font-weight: bold;
        line-height: 57px;
        padding-left: 40px;
        margin-left: 15px;
        position: relative;
        border-top: 1px solid rgba($deep-ice, 0.5);

        @media (min-width: $meganav-desktop) {
            background-color: transparent;
            font-size: 16px;
            line-height: inherit;
            padding: 10px;
			border-top: 0;
			margin-left: 0px;
        }

        .mn__icon {
            background-position: center top;
            background-repeat: no-repeat;
            background-size: auto 100%;
            display: block;
            height: 16px;
            left: 0px;
            position: absolute;
            top: 20px;
            width: 25px;
            @media (min-width: $meganav-desktop) {
                display: none;
            }
        }

        &:hover {
            cursor: default;
            text-decoration: none;
        }
		//below the border bottom effect

        .mn__chevron {
            background-image: url('../../../assets/css/hardcoded-nav-images/chevron_down.png');
            background-repeat: no-repeat;
            background-size: auto 100%;
            display: block;
            height: 8px;
            position: absolute;
            right: 20px;
            top: 24px;
            width: 14px;
            @media (min-width: $meganav-desktop) {
                display: none;
            }
        }
    }

    &.mn__active {
        .mn__nav-link {
            @media (min-width: $meganav-desktop) {
                background-color: #fff;
                border-radius: 2px 2px 0 0;
                color: $deep-ice;
                padding-bottom: 6px;
                text-decoration: none;
            }
            .mn__chevron {
                background-image: url('../../../assets/css/hardcoded-nav-images/chevron_up.png');
            }
        }

        .mn__dropdown {
            display: block;
            @media (min-width: $meganav-desktop) {
                .mn__dropdown-inner {
                    background-image: -moz-linear-gradient(left, #ffffff 83.3%, #e4f1fd 83.3%);
                    background-image: -webkit-linear-gradient(left, #ffffff 83.3%, #e4f1fd 83.3%);
                    background-image: -o-linear-gradient(left, #ffffff 83.3%, #e4f1fd 83.3%);
                    background-image: -ms-linear-gradient(left, #ffffff 83.3%, #e4f1fd 83.3%);
                    background-image: linear-gradient(to right, #ffffff 83.3%, #e4f1fd 83.3%);
                    margin: 0 auto;
                    padding-bottom: 40px;
                    padding-top: 26px;
                    width: 960px;
                }
            }
        }
    }
}

.mn__image-link {
    padding-top: 4px;
    width: 140px;

    img {
        max-width: 100%;
    }
}

.mm_clear {
    clear: both;
}

// HACKS

// Rewards in FAQ has no looking for something so needs this border even if it's last item
#mn__faqs .mn__dropdown-column:last-of-type .mn__dropdown-link-header {
    border-top: 1px solid rgba(#89a7d8, 0.3);

	@media (min-width: $meganav-desktop) {
		border-top: 0;
	}
}