﻿.cuc {
    margin-top: 30px;
    padding: 0;

    @media(min-width: $mobile) {
        @include flexbox();
        padding: 0 12px;
        @include justify-content(space-between);

        .lt-ie10 & {
            @include clearfix();
            display: block;
        }
    }
}

.cuc-wrapper {
    margin-bottom: 30px;
    
    @media(min-width: $mobile) {
        @include flexbox();
        margin: 0;
        padding: 0 13px;
        width: 50%;

        .lt-ie10 & {
            float: left;
        }
    }
}

.cuc-area {
    background-color: $rough-sea;
    padding: 1em 1em 80px;
    position: relative;
    width: 100%;
    
    @media(min-width: $mobile) {
        border-radius: 5px;
    }
}

.cuc-content {
    a {
        color: #fff;
        text-decoration: underline;

        /*&:hover {
            text-decoration: underline;
        }*/
    }
}

.cuc-title {
    @include font-size(1.5);
    font-weight: 700;
    margin-bottom: 20px;
    text-transform: uppercase;
}

.cuc-image {
    max-width: 100%;
    height: auto;
}

.cuc-image-wrapper {
    text-align: center;
}

.cuc-button-wrapper {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    text-align: center;
    padding: 1em;
}

.cuc-button {
    @include branded-button();
    display: inline-block;
    margin-top: 10px;
    width: 100%;
}

.cuc-substantiation {
    @include font-size(0.7);
    margin-top: 1em;
}
