﻿.lightbox {
		display: none;
}

.featherlight {
	
	overflow: auto;
	
	.featherlight-content {
		background: $jacksons-purple;
		background: -webkit-linear-gradient($jacksons-purple,$matisse);
        background: -o-linear-gradient(transparent, transparent); /* rare browser fallback */
		-webkit-border-radius: 6px;
		-moz-border-radius-: 6px;
		border-bottom: 0;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 1050;
		-webkit-overflow-scrolling: touch;
		outline: 0;
		-webkit-transition: -webkit-transform .3s ease-out;
		-moz-transition: -moz-transform .3s ease-out;
		-o-transition: -o-transform .3s ease-out;
		transition: transform .3s ease-out;
		max-height: inherit;
		overflow: initial;
		padding: 0;
		position: relative;
		width: 750px;
		margin: 50px auto;

		.featherlight-close-icon {
			-webkit-border-radius: 50%;
			-moz-border-radius-: 50%;
			border-radius: 50%;
			background-color: $mischka;
			background-image: -moz-linear-gradient(center top, $white, $french-gray);
			background-image: -webkit-gradient(linear, 0% 0%, 0% 100%, from($white), to($french-gray));
			background-image: -ms-linear-gradient(top, $white 0%, $french-gray 100%);
			background-image: linear-gradient($white, $french-gray);
			text-decoration: none;
			-moz-box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, .1);
			-webkit-box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, .1);
			box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, .1);
			-moz-box-sizing: border-box;
			-webkit-box-sizing: border-box;
			box-sizing: border-box;
			border: none;
			font-size: 16px;
			position: absolute;
			right: -8px;
			font-weight: bold;
			height: 24px;
			width: 24px;
			top: -8px;
			line-height: 24px;
			padding-bottom: 1px;
			text-transform: lowercase;
			color: $primary-green;
			cursor: pointer	
		}
	}
}






