﻿.lets-compare-block {
	.compare-actions { 
		.product-comparing-block {
             position: relative;

             @media only screen and (min-width: $mobile) {
                 margin: 0;
             }
         }
     }

	 .panel-small-links {
			display: block;
			border-top: solid 1px $deep-ice;
			padding-top: 25px;
			position: relative;
			width: 100%;
	 }

	 .or-small-links {
		background: $deep-ice;
		border: 1px solid $deep-ice;
		border-radius: 35px;
		display: inline-block;
		height: 35px;
		left: 50%;
		margin-left: -20px;
		padding: 6px 0;
		position: absolute;
		text-align: center;
		top: -17px;
		width: 35px;
		@media only screen and (min-width: $mobile) {
			padding: 4px 0;
		}
	 }

     .product-comparing-block {

		h2.product-comparing-block-heading{
			
			margin-left: 30px;
			text-align:left;
			width: auto;


			@media only screen and (min-width: $mobile) {
				margin-left: 25px;
			}
		}


		.start-comparing-block-inner {
			display: block;
			margin: 0 $mobile-side-margin;   
			text-align: left;

			.button-holder {
				display: inline-block;
				vertical-align: top;
				width: 67%;

				a {
					width: 100%;
				}
			}

			.icon-holder {
				display: inline-block;
				position: relative;
				text-align: right;
				width: 30%;
			}
				
			.compare-link-blocks {

				margin: 0px 5px;

				&.large-btn {
					text-align: justify;
					margin: 20px 0 0 0;

					@media (min-width: $mobile-smallest) {
						@include flexbox();
						@include justify-content(space-between);
						@include flexflow(row, wrap);
					}
					@media only screen and (min-width: $tablet) {
						margin-bottom: 20px;
					}	

					&:after {
						display:block;
					}

					.product-text{
						  margin-bottom: 10px;
						  @media only screen and (min-width: $tablet) {
								padding-top: 12px;
							}	
					}
					a {
						width: 100%;
						position: relative;
						padding: 15px 0 15px 20px;
						display: block;
						height: auto;
						text-align: center;

						@media (min-width: $mobile-smallest) {
							width: 46%;
							@include flexbox();
							@include align-items(center);
							padding-left: 53px;
							margin: 10px auto;
						}

						@media (min-width: $tablet) {
							width: 46%;
							&:first-child {
								//margin-right: 10px;
							}
						}

						span {
							display: inline-block;
							margin: 0;
							width: 80%;
							height: auto;
							position: static;
							
							@media (min-width: $tablet) {
							}

							&:before {
								display: inline-block;
								font-size: 25px;
								left: 16px;
								width: 10%;
								position: absolute;
								top: 50%;
								margin: -15px 0 0 0; 

							}

						}
					}
				}

				&.small-btn {
					display: block;
					margin: 0;
					text-align: justify;
					@media (min-width: $mobile-smallest) {
						@include flexbox();
						@include justify-content(space-between);
						@include flexflow(row, wrap);
					}
					
					a {
					
						width: 48%;
						text-align: center;
						span {
							display: block;
							@include fontsize(0.75);
							height: auto;
							margin: 15px auto;
							vertical-align: middle;
							padding: 0 5px;
						}

						@media (min-width: $mobile-low) {
						width: 30%;
							
						}

						@media (min-width: $mobile-smallest) {
							width: 22%;
							@include flexbox();
							@include align-items(center);
							@include justify-content(space-between);
						}
					}
				}

				a {
					span {
						width: 100%;
					}
				}
			}            
		}

		.primary-text-button {
			@include branded-button();
			@include fontsize(1);
			display: block;
			width: 200px;
			margin: 10px auto;
			text-align: center;

			@media only screen and (min-width: $mobile) {
				width: 100%;
				margin-bottom: 30px;
			}
		}

        .product-text {
            text-align: left;

  			padding-top: 12px;
  			margin-bottom: 10px;
  			@media only screen and (min-width: $tablet) {
				padding-top: 0;
			}			


            p {
                padding-bottom: 0.7em;
            }

			ul {
				padding: 0 0 20px 20px;
				
				li {
					list-style: disc;
				}
			}

        }

        .icon{

			padding-top: 12px;
			@media only screen and (min-width: $desktop) {
				padding-top: 5px;
			}
			&:before {
				font-family: 'ctm-icons';
                font-size: 65px;
				left: 32%;
				position: absolute;
	            color: $deep-ice;
				top: 18px;
				z-index: 5;
                    
                @media only screen and (min-width: $mobile-low) {
                    font-size: 45px;
                    font-size: 7rem;

				}                    
                @media only screen and (min-width: $mobile) {
                    font-size: 35px;
                    font-size: 5rem;
					top: 14px;

				}
                @media only screen and (min-width: $tablet) {
                    font-size: 45px;
                    font-size: 5rem;
					top: 14px;
                }

				@media only screen and (min-width: $desktop) {
					top: 5px
				}
                    
              }
                 
		  }
     }
}