﻿.contact-press-office-container {
    background-color: $rough-sea;
    padding: 20px 25px;
    @include border-radius(5px);

    max-width: 960px;
    margin: 0 auto;
}

.contact-press-office-title {
    font-weight: 700;
    font-size: 22px;
    margin-bottom: 20px;

    .ratio-block & {
        display: none;
    }
}

.contact-press-office-outer-title {
    @include media-centre-title;
}

@media(min-width: $mobile-low) {
    .ratio-block.seventythirty {
        .blocks-item {
            &:first-child {
                .contact-press-office-person {
                    width: 50%;
                }
            }
            .contact-press-office-person {
                width: 100%;
            }
        }
    }      
    .ratio-block.thirtyseventy {
        .blocks-item {
            &:first-child {
                .contact-press-office-person {
                    width: 100%;
                }
            }
        }
    }
    .ratio-block.fiftyfifty {
        .blocks-item {
            .contact-press-office-person {
                width: 100%;
            }
        }
    }
}

.contact-press-office-person {
    margin: 10px 0;
    @include inline-block;
    width: 100%;

    @media(min-width: $mobile-low) {
        width: 50%;
    }

    img {
        @include inline-block();
        width: 15%;
        margin: 0 3% 10px 0;
    } 

    .contact-person {
        @include inline-block();
        width: 82%;

        span {
            display: block;
            margin-top: 4px;

            &:first-child {
                margin-top: 0;
            }
        }

        .name {
            font-weight: 700;
        }

        .role {

        }

        .email {
            margin-top: 4px;
            display: block;
            &:hover {
                text-decoration: underline;
            }
        }
    }

}

.additional-info {
    font-weight: 700;
    font-size: 16px;
    p {
        margin-top: 8px;
        &:first-child {
            margin-top: 0;
        }
    }
    a {
        font-weight: 300;
        &:hover {
            text-decoration: underline;
        }
    }
}
