﻿
@import "../../martha/mixins/_martha-mixins";

.content-blocks-block {
    background: $rough-sea;
    display: inline-block;
    padding-top: 2%;
    width: 100%;


    .panel-holder {
        padding-left: 4%;


        @media (min-width: $tablet) {
            padding-left: 3%;
        }

        @media (min-width: $desktop) {
            padding-left: 0;
        }

        .content-blocks-wrapper {
            display: block;
            padding: 0 20px;

            @media (min-width: $mobile-low) {
                @include flexbox();
                @include justify-content(center);
                @include flexflow(row, wrap);
                flex-wrap: wrap;
            }

            @media(min-width: $tablet) {
                padding: 0;
            }

            .content-block {
                padding: 20px 34px;
                width: 100%;

                @media (min-width: $mobile) {
                    width: auto;
                }

                .lt-ie10 & {
                    width: 33%;
                    float: left;

                    &:nth-child(3n+1) {
                        clear: left;
                    }
                }

                @media (min-width: $mobile) {
                    width: 48%;
                }

                @media (min-width: $tablet) {
                    width: 33%;
                }

                .content-block-holder {
                    &:before {
                        @include icon ($tick, 1.7rem, $pigeon-post);

                        @media (min-width: $mobile) {
                            margin-left: -49px;
                        }

                        margin-left: -45px;
                        margin-top: 6px;
                        padding-right: 12px;
                        display: inline-block;
                        vertical-align: top;
                    }

                    &.icon-caret-right {
                        &:before {
                            @include icon($caret-right, 1.7rem, $pigeon-post);
                            margin-top: 2px;
                            margin-left: -26px;

                            @media (min-width: $mobile) {
                                margin-top: 0;
                                margin-left: -29px;
                            }
                        }
                    }

                    margin-bottom: 5.5%;

                    a:hover > h2 {
                        text-decoration: underline;
                    }

                    h2, h3 {
                        display: inline-block;
                        @include fontsize(1.25);
                        padding-bottom: 10px;
                        vertical-align: top;
                    }

                    h3 {
                        line-height: 1.7rem;
                        font-weight: 700;
                    }
                }
            }
        }
    }
}


$cb-background: #D9E5EF;
$cb-text-color: #2867A0;

.martha-testing 
{
    .content-blocks-block {
        background: $cb-background;
        color: $cb-text-color;
        display: inline-block;
        padding-top: 2%; 
        padding-bottom: 2%;
        width: 100%;


        .panel-holder {
            padding-left: 4%;


            @media (min-width: $tablet) {
                padding-left: 3%;
            }

            @media (min-width: $desktop) {
                padding-left: 0;
            }

            .content-blocks-wrapper {
                display: block;
                padding: 0;

                @media (min-width: $mobile) {
                    padding: 0 20px;
                }

                @media (min-width: $mobile-low) {
                    @include flexbox();
                    @include justify-content(center);
                    @include flexflow(row, wrap);
                    flex-wrap: wrap;
                }

                @media(min-width: $tablet) {
                    padding: 0;
                }

                .content-block {
                    padding: 0; 
                    width: 100%;
                    font-size: 15px;
                    padding-bottom: 20px;

                    @media (min-width: $mobile) {
                        width: auto;
                        padding: 34px 34px;
                    }

                    .lt-ie10 & {
                        width: 33%;
                        float: left;

                        &:nth-child(3n+1) {
                            clear: left;
                        }
                    }

                    @media (min-width: $mobile) {
                        width: 48%;
                    }

                    @media (min-width: $tablet) {
                        width: 33%;
                    }

                    .content-block-holder {
                        &:before {
                            display: none;
                        }
                        
