                        }

                        h2, h3 {
                            display: inline-block;
                            font-size: 22px;
                            padding-bottom: 10px;
                            vertical-align: top;
                        }

                        h3 {
                            line-height: 1.7rem;
                            font-weight: 700;
                        }
                    }
                }
            }
        }
    }


    .cb-block-title {
        @include title(small, left);
        margin-top: 10px;
        margin-bottom: 30px;

        &:after {
            background: $cb-text-color;
        }
    }
}
