﻿$intro-main-background-color: $white;
$intro-border-left: $deep-ocean;
$intro-border-right: $brand-green;
$intro-main-text-color: $shark;
$intro-title: #1e468c;
$intro-main-title: #3480a7;
$light-blue: #868b99;

.pc-intro {
    background-color: $intro-main-background-color;

    .panel {
        padding-bottom: 0;
    }
}

.pc-intro-wrapper {
    display: block;

    @media(min-width: $touch-nav) {
        display: table;
        table-layout: fixed;
        width: 100%;
    }
}

.pc-intro-border {
    display: table;
    width: 100%;
    margin-top: 20px;
}

.pc-intro-border-left,
.pc-intro-border-right {
    display: table-cell;
    height: 15px;
    width: 50%;
}

.pc-intro-border-left {
    background-color: $intro-border-left;
}

.pc-intro-border-right {
    background-color: $intro-border-right;
}

.pc-intro-image-wrapper {
    display: none;
    overflow: hidden;
    padding: 1em 2em;

    @media(min-width: $touch-nav) {
        display: table-cell;
    }

    img {
        vertical-align: bottom;
        max-height: 170px;
        max-width: 100%;

        @media(min-width: $touch-nav) {
            max-height: none;
        }
    }
}

.pc-intro-content-wrapper {
    color: $intro-main-text-color;
    display: block;
    text-align: center;
    vertical-align: middle;
    margin: 1em 0;
	padding: 0 20px; 

    @media(min-width: $touch-nav) {
        display: table-cell;
		padding: 0;
        width: 50%;
    }
}

.pc-intro-content-wrapper .ss-click-to-share {
    margin: 30px auto 0;
}

.pc-intro-title {
    color: $intro-title;
    font-weight: 700;
    @include font-size(1.4);
    margin-bottom: 10px;
}

.pc-intro-main-title {
    color: $intro-main-title;
    font-weight: 700;
    line-height: 2rem;
    margin-bottom: 30px;
}

.pc-master {
    .pc-intro {
        // margin-top: $simple-guide-margin-top;

        @media(min-width: $touch-nav) {
            padding-top: 70px;
        }

        .ss-click-to-share {
            margin-bottom: 50px;
        }
    }

    .pc-intro-title {
        font-weight: 300;
    }

    .pc-intro-text {
        font-size: $paragraph-small;
        line-height: 1.35;

        @media(min-width: $mobile-low) {
            width: 80%;
            margin: 0 auto;
        }

        @media(min-width: $mobile) {
            padding: 0 3em;
            width: auto;
        }
    }
    .pc-intro-image-wrapper {
        padding: 0;
        vertical-align: bottom;
        text-align: center;
        position: absolute;
        bottom: 15px;
        width: 50%;
        display: block;

        @media(min-width: $touch-nav) {
            width: 25%;
        }
    }
    .pc-intro-wrapper {
        @media(max-width: $touch-nav) {
            padding-bottom: 135px;
        }
        > :last-child{
            left: auto;
            right: 12.5%; 
        }

        .pc-intro-image-wrapper {
            &:first-child {
                left: 0;

                @media(min-width: $touch-nav) {
                    text-align: left;
                    padding-left: 30px;
                }

                @media(min-width: $tablet) {
                    padding-left: 50px;
                }
            }

            &:last-child {
                right: 0;

                @media(min-width: $touch-nav) {
                    text-align: right;
                    padding-right: 30px;
                }

                @media(min-width: $tablet) {
                    padding-right: 50px;
                }
            }
        }
    }
    .pc-intro-border {
        margin: 0;
    }
}