﻿$secondary-nav-bg: $big-stone;
$secondary-nav-bg-hover: $white;
$secondary-nav-text: $white;
$secondary-nav-text-hover: $big-stone;
$secondary-nav-sticky-nav-text: $white;
$secondary-nav-button: $brand-green;
$secondary-nav-selected-bg: $white;
$secondary-nav-selected-text: $big-stone;
$secondary-nav-sticky-bg: $white;

.media-centre-secondary-nav-block {
    .panel {
        padding-bottom: 0;
    }

    .media-centre-secondary-nav {
        display: flex;
    }

    .media-centre-secondary-nav-fullwidth {
        display: table;
        width: 100%;

        .secondary-nav {
            display: table-cell;
            width: 1%;
            text-align: center;
            font-size: 0.8rem;
        }
    }

    .secondary-nav {
        background-color: $secondary-nav-bg;
        padding: 8px;
        color: $secondary-nav-text;
        vertical-align: middle;
        font-weight: 600;

        &:hover {
            background-color: $secondary-nav-bg-hover;
            color: $secondary-nav-text-hover;
        }
    }

    .secondary-nav-sticky-nav {
        background-color: $secondary-nav-button;
        position: relative;
        color: $secondary-nav-sticky-nav-text;

        &:hover {
            background-color: $secondary-nav-sticky-bg;
            color: $secondary-nav-button;
        }
    }

    .secondary-nav-selected {
        background-color: $secondary-nav-selected-bg;
        color: $secondary-nav-selected-text;
    }

    .secondary-nav-selected.secondary-nav-sticky-nav {
        color: $secondary-nav-button;

        @media (max-width: $mobile) {
            color: $white;
        }
    }

    .secondary-nav-sticky-link {
      //  visibility: hidden;
        display: none !important;
    }

    .secondary-nav-sticky-nav {
        display: table-cell !important;
      //  visibility: initial;
    }
    @media only screen and (max-width: $mobile) {
        visibility: hidden;
        position: fixed;
        bottom: 0;
        right: 0;
        left: 0;
        height: 50px;
        background-color: black;
        z-index:30;

        .secondary-nav-sticky-link {
            visibility: visible;
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            top: 0;
            border-top: 2px solid $white;
            padding: 0;
            height: 50px;
            display: table !important;
            width: 100% !important;
            background-color: $secondary-nav-button;
            color: $white;

            &:hover {
                background-color: $secondary-nav-sticky-bg;
                color: $secondary-nav-button;
            }

            span {
                vertical-align: middle;
                display: table-cell;
            }
        }
    }
}
