﻿.feature-text-block {
    direction: ltr;
    text-align: center;
    padding: 20px $mobile-side-margin;

    &.feature-text-block-left-aligned {
        text-align: left;
    }

    p, a {
        margin-bottom: 5px;
    }

    a {
        &:hover {
            text-decoration: underline;
        }
    }
    
    .feature-headline {
		display: inline-block;
        margin-bottom: 20px;
        font-weight: 700;
        font-weight: bold;

        @media only screen and (min-width: $mobile) {
            @include fontsize(1.7);
            line-height: 1.7rem;
        }

        .error-page & {
            @include fontsize(1.7);
            line-height: 1.7rem;
        }
    }
