$background-color: $white;
$font-color: #004a8e;

.pc-article-text-block {
    @include has-border-flourish();

    background-color: $background-color;

    &.has-border-flourish {
        position: relative;
        padding: 32px;
    }
}

.pc-article-text-block-container {
    color: $font-color;
    max-width: $content-width - 20px;
    margin: 0 auto;
    padding: 20px;

    .pc-article-text-cta-panel-button {
        left: 50%;
        right: 50%;
        transform: translate(-50%, -50%)
    }

    .pc-article-text-cta-left-panel-button {
        left: 0;
        right: 50%;
        transform: translate(0, -50%)
    }

    h1 {
        line-height: 1.1;
    }

    h2 {
        @include article-title();

        font-size: 28px;
        margin: 20px 0 30px;

        &::before {
            display: none;
        }
    }

    h2[style*="text-align: center"] {
        &::after {
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    p {
        font-size: $paragraph-small;
        margin: 20px 0;
    }

    ol, ul {
        margin: 15px 0 15px 0;
    }

    li {
        list-style: disc;
        margin-left: 25px;
        font-size: 18px;
        padding: 5px 0 5px 0px;
    }

    .table-wrapper {
        @media (max-width: $tablet) {
            overflow: scroll;
        }
    }

    table {
        max-width: $content-width;
        width: 100%;

        @media (max-width: $mobile) {
            width: 500px;
        }
    }

    td{
        word-wrap: break-word;
    }
}

