.simple-block {
    display: block;

    @media only screen and (min-width: $tablet) {
        @include flexbox();
        @include justify-content(space-between);
    }

    .start-comparing-block {

        h2.product-comparing-block-heading {
            margin-left: 30px;
            text-align: left;
            margin-bottom: 10px;
            width: 60%;

            @media only screen and (min-width: $mobile) {
                text-align: left;
                margin-left: 30px;
                margin-bottom: 18px;
            }
        }
        
        padding-top: 25px;
        border-radius: 5px;
        vertical-align: top;
        text-align: center;
        display: block;
        background: $rough-sea;
        margin-bottom: 20px;
        @include flex(1,100%);

        @media only screen and (min-width: $tablet) {
            @include flex(1,60%);
            display: inline-block;
            margin-bottom: 0;
        }

        .start-comparing-block-inner {
            display: block;
            margin: 0 $mobile-side-margin;
            text-align: left;

            .button-holder {
                display: inline-block;
                vertical-align: top;
                width: 67%;

                a {
                    width: 100%;
                }
            }

            .icon-holder {
                display: inline-block;
                position: relative;
                text-align: right;
                width: 30.5%;



                .icon {
                    padding-top: 17px;

                    &:before {
                        font-family: 'ctm-icons';
                        font-size: 65px;
                        left: 28%;
                        position: absolute;
                        color: $deep-ice;
                        top: 18px;
                    }                    
                }

                @import "_simple-block-icons";
            }

            .primary-text-button {
                @include branded-button();
                display: block;
                width: 200px;
                margin: 18px auto;
                text-align: center;

                @media only screen and (min-width: $mobile) {
                    width: 100%;
                    margin-bottom: 30px;
                }
            }
        }
    }

    .secondary-content {
        @include flex(1,100%);
        display: block;
        background: $rough-sea;
        border-radius: 5px;

        .block-primary {
            border: none;
        }

        @media only screen and (min-width: $tablet) {
            @include flex(1,38%);
            display: inline-block;
        }

    }
}
