.mc-list {
    &.show-filter {
        padding-top: 0;
    }
}

.mc-list-items {
    margin: 0 10px;

    @media screen and (min-width: $tablet) {
        vertical-align: top;
        width: 63%;
        margin: 0 3% 0 0;
        @include inline-block;
    }

    @media screen and (min-width: $desktop) {
    }
}

.mc-list-item {
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid $white;

  @media screen and (min-width: $tablet) {
  }
}

.mc-list-item-detail {
  @media screen and (min-width: $mobile) {
    @include inline-block;
    width: 68%;
  }
}

.mc-list-item-teaser {
  @include inline-block;
  margin-bottom: 16px;
  width: 100%;
  img {
      max-width: 100%;
      max-height: 150px;
  }
  @media screen and (min-width: $mobile) {
    min-height: 50px;
    width: 30%;
    margin-right: 1%;
    margin-bottom: 0;
  }
}

.mc-list-item-title {
  @include fontsize(1.1);
 //font-family: $font-sun;
  font-weight: 700;
}

.mc-list-item-date {
   @include fontsize(0.8);
   margin-top: 5px;
}

.mc-list-item-subtitle {
  margin-top: 10px;
  font-weight: 700;
  font-size: 18px;
}

.mc-list-item-summary {
  @media screen and (min-width: $mobile) {
    margin-top: 10px;
  }
}

.mc-list-item-readmore {
  margin-top: 10px;
  display: inline-block;
  &:hover {
    text-decoration: underline;
  }
}

.mc-list-content-blocks {
    margin: 0 10px;

    @media screen and (min-width: $tablet) {
        margin: 0px;
        vertical-align: top;
        width: 33%;
        @include inline-block;
    }

    > div {
        margin-bottom: 10px;
        @media screen and (min-width: $tablet) {
            margin-bottom: 30px
        }
    }

    .contact-press-office-person {
        @media screen and (min-width: $tablet) {
            width: 100%;
        }
    }
}
