﻿.plb-container {
    @include content-width();
    position: relative;

    .plb-title {
        font-weight: 700;
        margin-bottom: 20px;
    }

    .plb-copy {
        margin: 20px 0;
    }

    .plb-image {
        display: none;
    }

    .plb-filters {
        display: table;
        width: 100%;
        margin-bottom: 7px;
        table-layout: fixed;

        &.plb-filters-wide {
            display: none;
        }

        .plb-filter-item {
            display: table-cell;
            vertical-align: middle;
            text-align: center;
            overflow: hidden;

            > a {
                margin-left: 4px;
                background: $primary-blue60;
                display: block;
                padding: 4px 0;
                font-size: 15px;
                cursor: pointer;
                color: white;

                &.selected {
                    background: $primary-blue;
                }

                &.disabled {
                    background: $primary-blue40;
                    cursor: default;
                }
            }

            &:first-child > a {
                margin-left: 0;
            }
        }
    }

    .plb-paginated-area {
        text-align: center;
        display: table;
        margin: 0 auto;
    }

    .plb-pagination {
        display: table-cell;
        height: 300px;
        max-width: 25px;
        min-width: 25px;
        vertical-align: middle;
        background: $primary-blue60;
        cursor: pointer;
        color: white;

        span {
            transform: scale(2, 3);
            display: inline-block;
        }

        &[disabled] {
            cursor: default;
            background: $primary-blue40;
        }
    }

    .plb-pagination-indicators {
        text-align: center;
    }

    .page-indicator {
        width: 8px;
        height: 8px;
        background: white;
        border-radius: 50%;
        display: inline-block;
        margin: 0 2px;
        cursor: pointer;

        &.page-indicator-active {
            background: $brand-green;
        }
    }

    .plb-providers-container {
        display: table-cell;
        vertical-align: middle;
        width: 100%;
    }

    .plb-provider-item {
        display: inline-block;
        width: 100px;
        margin: 5px;
        position: relative;
    }

    @media(min-width: 590px) {
        padding-top: 20px;
        padding-bottom: 20px;

        .plb-image {
            display: inline-block;
            max-width: 100px;
            max-height: 80px;
            position: absolute;
            top: 20px;
            right: 20px;
        }

        .plb-copy {
            margin-right: 140px;
        }

        .plb-pagination {
            height: 225px;
        }
    }

    @media(min-width: 720px) {
        .plb-pagination {
            height: 150px;
        }

        .plb-filters {
            &.plb-filters-narrow {
                display: none;
            }

            &.plb-filters-wide {
                display: table;
            }
        }
    }
}
