
.how-snapt-works-page {
	width: auto;
    margin: 0 auto;

    .clearfix {
        zoom: 1;

        &:before
        {
            content: "\0020";
            display: block;
            height: 0;
            visibility: hidden;
        }
        &:after
        {
            clear: both;
            content: "\0020";
            display: block;
            height: 0;
            visibility: hidden;
        }
    }

	.how-snapt-works-heading {
		border-bottom: 1px solid white;
		position: relative;

		h1 {
			font-size: 80px;
            line-height: 55px;
			overflow-wrap: break-word;
		}
		h3 {
			margin-bottom: 10px;
		}

		.how-snapt-sub-heading-content{
			width: 70% !important;

            @media(max-width: $mobile)
            {
                width: 95% !important;
                padding-left: 2%;
            }
		}

		.how-snapt-works-sub-heading {
			clear: both;
			float: left;
			width: 100%;
			padding-right: 60px;
			padding-top: 15px;

            @media(max-width: $mobile){
			    padding-right: 0px;
            }

			h2 {
                font-size: 23px;
                margin-top: 10px;
                margin-bottom: 15px;
			}

			p {
				font-size: 17px;
				overflow-wrap: break-word;
				margin-bottom: 15px;
			}
		}

		.download-snapt-cta {
			clear: both;
			float: left;
			font-size: 27px;
            font-weight: bold;
			margin-top: 25px;
		}

		.itunes-link {
			float: left;
			display: block;
			height: 47px;
			width: 136px;
			background: url(../images/snapt/App_Store_Badge_US-UK_135x40.png) 0 0 no-repeat;
			margin: 20px 0 0 25px;
            
            @media(max-width: $mobile){
                margin: 0;
                margin-right: 10px;
                margin-top: 20px;
			    float: none;
			    display: inline-block;
            }
		}

		.play-link {
			float: left;
			display: block;
			height: 47px;
			width: 136px;
			background: url(../images/snapt/play-button-40px.png) 0 0 no-repeat;
			margin: 20px 0 0 15px;
            
            @media(max-width: $mobile){
                margin: 0;
                margin-right: 10px;
                margin-top: 20px;
			    float: none;
			    display: inline-block;
            }
		}

		.how-snapt-sub-heading-content {
			float: left;
			width: 60%;
			min-height: 340px;
		}

		.how-snapt-works-phone-image {
			width: 203px;
			height: 321px;
			position: absolute;
			right: 20px;
			bottom: -1px;
			border: solid 1px #ffffff;

            @media (max-width: $mobile)
            {
			    position: static;
			    right: 0px;
			    bottom: 0px;
                margin: 0 auto;
                display: block;
                margin-bottom: -1px;
            }
		}

		.apple-legal-text {

			span {
				width: 104%;
				display: block;
				float: left;
				margin: 20px 0 10px 0;
                font-size: 11px;
                line-height: 11px;
			}
		}
	}

	.how-snapt-works-steps {
		font-size: 15px;

		padding-bottom: 25px;
		border-bottom: 1px solid white;

		h2 {
            font-size: 60px;
            line-height: 78px;
		}

		.intro-text {
			margin: 35px 0 15px 0;
			// padding-left: 35px;
			position: relative;
			font-weight: 700;
			font-size: 22px;
            margin-left: 2%;

			// &:before {
			// 	background: url("../images/snapt/SnaptPage_billtohand.png") no-repeat scroll 0 0;
			// 	content: "";
			// 	display: block;
			// 	height: 28px;
			// 	left: 0;
			// 	position: absolute;
			// 	width: 25px;
			// }
		}

		.step-one, .step-two, .step-three {
			//float: left;
			width: 32%;
			padding: 20px;
			background-color: #043061;
			-moz-box-sizing: border-box;
			box-sizing: border-box;
			border: 1px solid white;
			border-radius: 7px;
			margin-right: 1%;
			//min-height: 400px;

            
			
            @media(max-width: $mobile){
                width: 100%;
                margin-bottom: 15px;

                &.table-cell
                {
                    display: block;
                }
            }

			h2 {
				position: relative;
				width: 100%;
				padding-left: 10px;
				margin-bottom: 10px;
				&:after {
					content: '';
					position: absolute;
					right: 20px;
				}
			}

			p {
				margin-bottom: 10px;

				strong{
					font-size: 14px;
				}
			}
		}

		.step-one, .step-two {
			margin-right: 5%;
		}

		.step-one h2 {
			&:after {
				top: 10px;
				width: 51px;
				height: 58px;
				background: url("../images/snapt/bill.png") no-repeat scroll 0 0;
			}
		}

		.step-two h2 {
			&:after {
				top: 10px;
				width: 35px;
				height: 59px;
				background: url("../images/snapt/phone.png") no-repeat scroll 0 0;
			}
		}

		.step-three h2 {
			&:after {
				width: 69px;
				height: 70px;
				background: url("../images/snapt/pound.png") no-repeat scroll 0 0;
			}
		}
	}

	.how-snapt-works-video {
        margin: 2%;

		iframe {
			margin: 25px auto;
			display: block;
		}
	}

	.how-snapt-works-timeline {
		margin: 0 auto;

		.timeline-item-one, .timeline-item-two, .timeline-item-three, .timeline-item-four {
			width: 200px;
			display: inline-block;
			position: relative;
			margin-top: 40px;
			background-color: #788FBA;
			border-radius: 7px;

			.timeline-image{
				width: 100%;
				height: 100px;
				border-bottom: 5px solid #788fba;
				box-sizing: border-box;
				position: relative;
				margin-bottom: -4px;
				&:before {
					content: '';
					position: absolute;
					background: url("../images/snapt/timeline-sprite.png") no-repeat scroll 0 0;
					height: 67px;
					width: 67px;
					top: -30px;
					left: 15px;
				}
			}
		}

		.timeline-item {
			width: 100%;
			background-color: #043061;
			-moz-box-sizing: border-box;
			box-sizing: border-box;
			padding: 10px;
		}

		.timeline-item-one, .timeline-item-two, .timeline-item-three {
			margin-right: 1%;
		}

		.timeline-item-one {
			.timeline-image {
				background: url("../images/snapt/timeline-sprite.png") no-repeat scroll 0 -71px;
				&:before {
					background-position: -1px 0;
				}
			}
		}

		.timeline-item-two {
			.timeline-image {
				background: url("../images/snapt/timeline-sprite.png") no-repeat scroll -6px -185px;
				&:before {
					background-position: -63px 0;
				}
			}
		}


		.timeline-item-three {
			.timeline-image {
				background: url("../images/snapt/timeline-sprite.png") no-repeat scroll 0 -295px;
				&:before {
					background-position: -133px 0;
				}
			}
		}

		.timeline-item-four {
			.timeline-image {
				background: url("../images/snapt/timeline-sprite.png") no-repeat scroll -19px -416px;
				&:before {
					background-position: -203px 0;
				}
			}
		}

		.table-cell {
			background-color: #043061;
			border: 5px solid #788fba;
		}
	}

    .how-snapt-works-bottom-text
    { 
        margin-top: 55px;
        border-top: 1px solid #fff;
        padding-left: 2%; 
    }


	.summary-text {
		@include fontsize(1.6);
		margin-top: 10px;
	}

	.table {
		display: table;
		border-collapse: separate;
		border-spacing: 15px;
		width: 100%;

		.table-cell {
			display: table-cell;
		}
	}

	.footer {
		margin-top: 35px;
	}

	h3 {
		margin: 25px 0 15px;
	}
}
