.app-banner {
    position: relative;
    // padding: 1em;
    @include clearfix;
    background-color: $rough-sea;
    border-radius: 5px;

    @media(min-width: $mobile-smallest) {
        position: relative;
    }
}

.app-banner-content {
    @include clearfix;
    margin-bottom: .5em;
    padding: 14px 8px;

    @media(min-width: $mobile-smallest) {
        display: table;
        width: 100%;
        // padding-left: 59px;
        margin-bottom: 0;
        padding: 5px;
        font-size: 21px; /* this is to overide the body changing font size based on a media query */
    }
}

.app-banner-img {
    float: left;
    // width: 50px;
    margin-right: 2.5em;

    img {
        // max-width: 100%;
        height: auto;
    }

    @media(min-width: $mobile-smallest) {
        float: none;
        // position: absolute;
        margin-top: 5px;
        margin-left: 10px;
    }
}

.app-banner-copy {
    margin-bottom: 1em;
    @include fontsize(1.1);
    line-height: 1.1;

    > br {
        display: none;
    }

    @media(min-width: $mobile-smallest) {
        display: table-cell;
        vertical-align: middle;
        width: 55%;
        padding-right: 20px;

        > br {
            display: block;
        }
    }

    @media(min-width: $mobile) {
        width: 64%;
    }
}

.app-banner-link {
    position: relative;
    display: inline-block;
    font-weight: 700;
    line-height: 1;
    @include fontsize(1.1);

    &:after {
        @include icon($arrow-right-circle, 17px, $white);
        display: inline-block;
        margin-left: 10px;
    }

    &:hover {
        color: $brand-green-hover;
    }

    @media(min-width: $mobile-smallest) {
        display: table-cell;
        vertical-align: middle;
        right: 18px;
        width: 45%;
        text-align: right;
    }

    @media(min-width: $mobile) {
        width: 46%;
    }
}