.fcle-container {
    background-size: cover;
    background-position: center center;
    width: 100%;
    position: relative;

    @media (min-width: $mobile) {
        height: 500px;
    }
}

.fcle-link-panel-container {
    @include content-width();
}

.fcle-link-panel {
    margin-right: auto;
    margin-left: auto;
    text-align: center;
    z-index: 200;
    position: relative;
    padding-top: 50px;
    padding-bottom: 50px;

    .fcle-full-width & {
        padding-top: 20px;
        padding-bottom: 20px;
    }

    @media (min-width: $mobile) {
        width: 60%;
        padding-bottom: 0;
        padding-top: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        @include translate(-50%, -50%);

        .fcle-full-width & {
            position: relative;
            top: 0;
            left: 0;
            @include translate(0, 0);
        }
    }
}

.sg-container {
    & + .fcle-link-panel {
        padding-top: 70px;

        @media (min-width: $mobile) {
            padding-top: 50px;
        }
    }
}

.fcle-link-panel-copy {
    & > p {
        margin-bottom: 10px;
    }
}

.fcle-link-panel-title {
    @include title(small, center);
}

.fcle-link-panel-button {
    @include cta-button();

    .secondary-button {
        @include secondary-button-style-override(normal);
    }
}

.fcle-link-panel-inverse-button {
    @include cta-button(inverse);

    .secondary-button {
        @include secondary-button-style-override(inverse);
    }
}

.fcle-link-panel-hollow-button {
    @include cta-button(hollow);

    .secondary-button {
        @include secondary-button-style-override(hollow);
    }
}

.fcle-link-panel-play-button {
    @include play-button();
}

.fcle-link-panel-play-inverse-button {
    @include play-button(inverse);
}

.fcle-link-panel-has-dark-overlay {
    @include dark-overlay();
}

.fcle-link-panel-number {
    @include cta-number;
}

.fcle-link-panel-number-copy {
    font-size: 12px;
    margin-bottom: 10px;
}

.fcle-full-width {
    width: 100vw;
    height: auto;
    padding: 20px 0;
}
