$font-size: 16px;
$light-blue: #e5f1fc;
$dark-blue: #094c8d;
$border-radius: 10px;
$label-width: 140px;

.igc-container {
  background: $light-blue;
  padding: 30px 0;
  color: $dark-blue;
}

.igc-header {
    min-height:160px;
    max-width: 940px;
    margin: auto;
    padding: 20px;
    color: $dark-blue;

    .igc-header-text {
        float:left;
        max-width:500px;
        h2 {
            display: block;
            margin-bottom: 20px;
            font-size: 32px;
            font-weight: 600;
        }

        p {
            display:block;
            line-height: 1.3;
        }
    }

    .igc-header-img {
        float:right;
        img {
            width:250px;
        }
    }


    @media (max-width: 830px) {
        .igc-header-text {
            max-width:400px;
        }
        .igc-header-img {
            img {
                width:200px;
            }
        }
    }

    @media(max-width: $mobile) {
        .igc-header-text {
            max-width:none;
        }
        img {
            display:none;
        }
    }
}

@media(max-width: $mobile) {
    .igc-header {
        min-height:120px;
        margin-bottom:20px;
    }
}

.igc-tool {
    width: 100%;
    max-width: 960px;
    margin: auto;
    padding: 20px;
    font-size: 0;
}

.igc-fields {
    width: 100%;
    margin-bottom: 20px;
}

.igc-field-row {
    line-height:normal;
    border-bottom: 1px solid rgba(0,0,0,0.05);

    &:first-child {
        .igc-select-container {
            border-top-right-radius: $border-radius;
            border-top-left-radius: $border-radius;
        }

        label {
            border-top-left-radius: $border-radius;
        }
    }

    &:last-child {
        border-bottom: none;

        .igc-select-container {
            border-bottom-right-radius: $border-radius;
            border-bottom-left-radius: $border-radius;
        }

        label {
            border-bottom-left-radius: $border-radius;
        }
    }
}


.igc-field {
  display: inline-block;
  width: 100%;
  height: 50px;
  position: relative;

  &.disabled {
      opacity: 0.5;
  }

  &.block {
    width: 100%;
    margin: 0;
  }

  @media(min-width: $mobile) {
      width: 49%;

      &:first-child {
          margin-right: 2%;
      }
  }
}

.igc-label {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    padding-left: 15px;
    line-height: 3;
    width: $label-width;
    height: 100%;
    background: $dark-blue;
    color: #ffffff;
    font-size: $font-size;
}

.igc-select-container {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 100%;
    height: 100%;
    padding-left: $label-width;
    background-color: $white;

    &:after {
        content: ">>";
        position: absolute;
        top: 0;
        right: 0;
        padding: 12px 15px;
        color: $light-blue;
        font-weight: bold;
        font-size: 28px;
        pointer-events: none;
        z-index: 6;
    }

    &.igc-loading {
        &:after {
            width: 60px;
            text-align: left;
            animation: 2s igcLoading linear infinite;
        }
    }

    &.igc-disabled {
        &:after {
            content: "";
        }
    }

    // IE9 hack to hide the dropdown
    &:before {
        display:block;
        position:absolute;
        content:'';
        right:0;
        top:0;
        bottom:0;
        width:40px;
        background:#fff;
        border-top-right-radius: $border-radius;
        border-bottom-right-radius: $border-radius;
        z-index:5;
    }

    // IE10+ to hide the dropdown
    &:-ms-expand {
        display: none;
    }
}

.igc-result-container {
    font-size: round($font-size * 1.6);
    margin-top: 30px;
    text-align: center;

    @media(min-width: $mobile) {
        text-align: left;
    }
}

@keyframes igcLoading {
    0%   { content: "" }
    33%  { content: "." }
    66%  { content: ".." }
    100% { content: "..." }
}

.igc-select {
    position: relative;
    width: 100%;
    padding: 0 20px;
    color: #333333;
    font-size: $font-size;
    font-family: inherit;
    border: none;
    border-radius: 0px;
    background-color: transparent;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    cursor: pointer;
    z-index: 2;
    vertical-align: middle;
    height: 100%;

    &::-ms-value {
        background: transparent;
        color: inherit;
    }
}

.igc-select[disabled] {
    opacity: 0.2;
}

.igc-cta {
    @include cta-button();

    margin-top: 20px;
    width: 200px;
    max-width: 100%;
    display: block;

    @media(min-width: $mobile) {
        float: right;
        margin: -10px 0 0 0;
    }
}