﻿$background-color: $rough-sea;

.pcta-container {
    background-color: $background-color;
}

.pcta-link-panel-container {
    @include content-width();
}

.pcta-link-panel {
    padding-top: 50px;
    padding-bottom: 50px;
    text-align: center;
}

.pcta-link-panel-title {
    @include title(small, center);
}

.pcta-link-panel-button {
    @include cta-button();
}

.pcta-link-panel-inverse-button {
    @include cta-button(inverse);
}

.pcta-link-panel-hollow-button {
    @include cta-button(hollow);
}

.pcta-link-panel-play-button {
    @include play-button();
}

.pcta-link-panel-play-inverse-button {
    @include play-button(inverse);
}