﻿.iih-container {
    position: relative;
    top: 0;
    left: 0;

    .iih-content {

        .iih-selectarea {
            z-index: 3;
            position: absolute;
            top: 1.5em;
            left: 1.5em;

            .intro-text {
                padding: 6px;
                border: 6px;
                position: relative;
                font-size: 1em;
                font-weight: bold;
            }

            .menu {
                font-family: "SUN";
                position: relative;
                z-index: 1;
                font-size: 1em;
                padding: 5px 10px;
                border-radius: 10px;
                border: none;
                outline: none;
            }
        }

        .iih-image {
            position: relative;
            top: 0;
            left: 0;
            z-index: 1;
        }

        .iih-pulse-container {
            z-index: 1;

            .iih-hotspot {
                .iih-pulse {
                    background-repeat: no-repeat;
                    background-size: contain;

                    &:hover {
                        background-repeat: no-repeat;
                        background-size: contain;
                    }
                }

                .iih-tooltip {
                    z-index: 100;
                    opacity: .95;

                    .iih-tooltip-image {

                        &--top {
                            display: block;
                            padding-right: 0;
                            margin-bottom: 10px;
                            max-width: 100%;
                        }

                        &--right {
                            padding-right: 0;
                            padding-left: 10px;
                            float: right;
                        }
                    }

                    .iih-tooltip-content {
                    }

                    .iih-close-tooltip {
                        top: -13px;
                        right: -13px;
                        height: 30px;
                        width: 30px;
                        background: #fff;
                        border: 1px solid #333;
                        border-radius: 15px;
                        &:after {
                            content: "";
                            height: 15px;
                            width: 15px;
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            transform: translate(-50%, -50%);
                            background-image: url(../../responsive2015/images/icons/close.png);
                            background-size: cover;
                        }
                    }
                }
            }
        }     

        /*
          By default the dropdown menu overlays the top left hand corner of the infographic image 
          which requires large ammount of white space on the left side of the image. 
          This class resets the above
        */        
        &--dropdown-outside {
            .iih-selectarea {
                position: initial;
                margin-bottom: 1em;
            }
            .iih-infographic {
                position: relative;
            }
        }  
    }

    .iih-content-tooltip {
        .iih-tooltip-mobile {
            .iih-tooltip-image {
            }

            .iih-tooltip-content {
            }
        }
    }

    // styles are shared across both desktop and mobile versions
    .iih-tooltip-video {
        position: relative;
        height: 0;
        overflow: hidden;
        padding-bottom: 56.25%;     // responsive videos assuming 16:9 aspect ratio
        margin-bottom: 15px;
        iframe {
            position: absolute;
            top:0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }

    // reduce certain values to reduce size of button within popup
    .button-default {
        font-size: 15px;
        padding: 6px 30px 6px 10px;
        min-width: initial;
        &:after {
            font-size: 17px;
            margin-top: -7px;
            right: 10px;
        }
    }

    .iih-backbuttonWrap {
        max-width: 980px;
        margin: 0 auto;
        padding: 0 20px;
        text-align: center;
        @media (min-width: $tablet) {
            text-align: left;
        }
    }

        .iih-backbutton {
            margin: 0 0 20px;
            padding: 6px 10px 6px 30px;
            &:after {
                left: 10px;
                right: auto;
            }       
        
        }
    
}
