.lhb-block-container {
    padding: 30px 0;

    @media(min-width: $mobile) {
        padding: 50px 0;
    }
}

.lhb-block {
    width: 100%;
}

.lhb-title {
    text-align: center;
    font-size: 28px;
    margin-bottom: 15px;
    line-height: 1.3;
    letter-spacing: -1px;
    margin-bottom: 30px;
    color: $primary-blue;
    font-size: 24px;

    @media(min-width: $mobile) {
        margin-bottom: 50px;
        font-size: 28px;
    }
        
    &:after {
        content: '';
        display: block;
        width: 70px;
        height: 2px;
        margin: 15px auto 0;
        background-color: $primary-blue;
    }
}

.lhb-block-image {
    max-width: 960px;
    margin: auto;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.lhb-items {
    display: block;
    list-style-type: none;
    text-align: justify;
    font-size: 0px;
    border-bottom: solid 1px $white;

    &:after {
        content: "";
        width: 100%;
        height: 0px;
        display: block;
    }
}

.lhb-item {
    position: relative;
    display: inline-block;
    vertical-align: top;
    width: 100%;
    margin: 0;
    border-bottom: solid 1px $white;

    @media(min-width: $mobile) {
        width: 33.3333%;
        height: 180px;
        border: solid 1px $white;
    }

    @media(min-width: $desktop) {
        width: 25%;
    }
}


.lhb-link {
    display: table;
    width: 100%;
    height: 100%;
    text-decoration: none;

    @media(min-width: $mobile) {
        &:hover, &:focus {
           .lhb-link-text {
                &:after {
                    margin: 8px auto 0;
                    opacity: 1;
                    visibility: visible;
                }
            }
        }
    }

    &.lhb-overlay:after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,0.5);
        z-index: 1;
    }
}

.lhb-align {
    position: relative;
    display: table-cell;
    vertical-align: middle;
    width: 100%;
    padding: 20px;
    font-size: 18px;
    font-weight: 600;
    text-align: center;
    color: $white;
    z-index: 3;
}

.lhb-link-text {
    display: block;
    margin-bottom: 15px;

    &:after {
        content: "";
        display: block;
        width: 35px;
        height: 2px;
        margin: -2px auto 0;
        background-color: $white;
        opacity: 0;
        visibility: hidden;
        transition: all 0.2s ease-in-out;
    }
}

.lhb-button {
    display: inline-block;
    position: relative;
    margin: auto;
    padding: 6px 40px 6px 12px;
    color: $white;
    font-size: 16px;
    text-align: left;
    border-radius: 8px;
    border: solid 2px $white;
    cursor: pointer;

    &:after {
        @include icon($arrow-full, 19px, $white);
        position: absolute;
        top: 0;
        right: 0;
        padding: 9px;
    }
}
