.ptv-vote-container {
    width: 100%;
    height: auto;
    padding: 25px 0 25px 0;
    background-color: white;
    display: none;
    margin-top: -1px;
    margin-bottom: -1px;
}
.ptv-vote-background {
    width: 100%;
    height: auto;
    padding: 40px 10px;
    background-color: #1e488c;
}
.ptv-vote-content {
    max-width: 757px;
    margin: 0 auto;
    padding: 0px 10px 0px 10px;
    text-align: center;
    display: block;
}
.ptv-confirm-split-1 {
    display: inline-block;
    text-align: right;
}
.ptv-confirm-split-2 {
    width: 49%;
    display: inline-block;
    text-align: left;
    padding-left: 40px;
    vertical-align: middle;
    @media (max-width: $touch-nav) {
        padding-left: 20px;
    }
}
.ptv-animal-circle {
    background-color: transparent;
    display: inline-block;
    vertical-align: middle;
    color: $white;
    cursor: pointer;
    font-size: 22px;
    font-weight: 600;
    font-family: "SUN";
    transition: all 0.3s ease-in-out;
    &:before {
        content: "";
        width: 200px;
        height: 200px;
        border-radius: 50%;
        background-color: white;
        display: block;
        background-repeat: no-repeat;
        background-position: center;
        margin-bottom: 20px;
        -moz-transition: all .2s ease-out;
        -o-transition: all .2s ease-out;
        -webkit-transition: all .2s ease-out;
        transition: all .2s ease-out;
    }
    @media (max-width: $touch-nav) {
        &:before {
            width: 150px;
            height: 150px;
        }
    }
    @media (max-width: $mobile-smallest) {
        &:before {
            width: 100px;
            height: 100px;
        }
    }
    &.ptv-no-margin {
        &:before {
            margin-bottom: 0px;
        }
    }
    &:hover {
        text-decoration: underline;
        &:before {
            box-shadow: 0px 0px 50px #0f2749;
        }
    }
    &.ptv-no-hover {
        &:hover {
            text-decoration: none;
            cursor: default;
            &:before {
                box-shadow: none;
            }
        }
    }
}
.ptv-cat-img {
    &:before {
        background-image: url("../../responsive2015/images/catVsDogVote/cat_image_2.png");
        background-size: 100%;
    }
}
.ptv-dog-img {
    &:before {
        background-image: url("../../responsive2015/images/catVsDogVote/dog_image_2.png");
        background-size: 105%;
    }
}
.ptv-or {
    padding: 0 60px;
    font-size: 22px;
    font-weight: 600;
    @media (max-width: $touch-nav) {
        padding: 0 45px;
    }
    @media (max-width: $mobile-smallest) {
        padding: 0 25px;
    }
}
.ptv-confirm-title {
    font-size: 22px;
    font-weight: 600;
    margin-bottom: 10px;
}
.ptv-tib {
    border-bottom: 3px white solid;
}
.ptv-block-container {
    width: 100%;
    height: auto;
    padding: 1px 0 60px 0;
    background-color: #fff;
    border-top: 1px $white solid;
}
.ptv-wrapper {
    max-width: 978px;
    margin: 0 auto;
    padding: 0px 10px 0px 10px;
}
.ptv-result {
    width: 100%;
    margin: 0px 0 20px 0;
    display: table;
    table-layout: fixed;
}
.ptv-option {
    vertical-align: middle;
    font-size: 22px;
    display: table-cell;
    font-weight: 600;
    color: #1e488c;
    width: 10%;
    @media (max-width: $touch-nav) {
        width: 20%;
        text-align: center;
    }
}
.ptv-chart-bar {
    width: 80%;
    height: 45px;
    background-color: #CCCCCC;
    display: table-cell;
    @media (max-width: $touch-nav) {
        width: 60%;
    }
}
.ptv-chart-bar-value {
    vertical-align: middle;
    display: table-cell;
    font-weight: 600;
    font-size: 22px;
    color: #1e488c;
    width: 10%;
    text-align: right;
    @media (max-width: $touch-nav) {
        width: 20%;
        text-align: center;
    }
}
.ptv-chart-bar-filled {
    width: 0%;
    height: 100%;
    display: block;
    z-index: 2;
    background-color: #1e488c;
    -webkit-transition: width 1.5s ease-in-out;
    -moz-transition: width 1.5s ease-in-out;
    -o-transition: width 1.5s ease-in-out;
    transition: width 1.5s ease-in-out;
    &:hover {
        width: 50%;
    }
}