﻿$media-centre-mobile-breakpoint: 986px;
$overlay-text-color: $white;
$overlay-background: $black;
$hero-item-body: $white;
$hero-media-find-out-more: $that-good-good;
$media-campgin-item: $jim-bean;
$slick-colors: $frosty-jacks;
$slick-active: $white;

.media-centre.campaign {
    font-family: $font-sun;


    &.hero {
        padding-bottom: 0;
        padding: 0 10px;
        margin: 0;
    
        @media(min-width: $mobile) {
            margin: 0 auto;
            padding: 0 25px;
        }
    }
    .clearfix {
        clear: both;
    }

    .hero-media-campaign-item {
        width: 100%;
        height: auto;
        overflow: hidden;
        border-radius: 5px;
        margin-bottom: 1em;
        margin-top: 1em;
        position: relative;

        @media(min-width: $mobile) {
            margin-top: 0;
            border-radius: 0px 0px 5px 5px;
        }

        .hero-media-campaign-item-background {
            height: auto;
            overflow: hidden;

            img, picture {
                width: 100%;
            }

            .link-overlay{
                height:100%;
                width:100%;
                position:absolute;
                top:0;
                right:0;
                left:0;
                bottom:0;
                z-index:10;
            }

            .overlay {
                background: $black;
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                bottom: 0;
                right: 0;
                left: 0;
                color: $overlay-text-color;
                @include opacity(0.6);

                @media(min-width: $mobile) {
                    display: none;
                }
            }
        }


        .hero-media-campaign-item-body {
            position: absolute;
            top: 0;
            left: 0;
            margin: 1rem;
            color: $hero-item-body;
            @media (min-width: $mobile) {
                width: 55%;
            }

            &.right-justify {
                @media(min-width:$mobile) {
                    right: 0;
                    left: auto;
                }
            }

            .hero-media-campaign-find-out-more {
                color: $hero-media-find-out-more;
                display: inline-block;
                font-weight: bold;
                margin-top: 10px;
                position: relative;
                z-index:20;

                &:hover {
                    text-decoration: underline;
                }
            }
        }

        .hero-media-campaign-item-title {
            font-size: 2em;
            line-height: 1.2em;
            margin-bottom: 0.3em;
            font-weight: bold;
        }

        .hero-media-campaign-item-text {
            font-size: 1.2em;
            line-height: 1.3em;
            max-height: 215px;
            overflow: hidden;
        }
    }

    .media-campaign-items {
        width: 100%;
        overflow: hidden;
        width: 990px\9 !important; /* IE 8-9 */
        @media(min-width: $media-centre-mobile-breakpoint) {
            @include flexbox();
            width: 102%;
        }


        .media-campaign-item {
            float: left;
            background: $media-campgin-item;
            border-radius: 5px;
            padding: 1.3em;
            height: 400px;
            margin-right: 1em;
            margin-left: 0.4em;
            max-width: 298px;


            @media(min-width: $media-centre-mobile-breakpoint) {
                @include flex(1,310px);
                width: 310px;
                margin-left: 0;
            }

            .media-campaign-item-title {
                font-size: 1.3em;
                margin-bottom: 0.5em;
                font-weight: bold;
                text-align: center;
                margin-top: 0.5em;
            }

            .media-campaign-item-body {
                margin-bottom: 1em;
                font-family: $font-sun;
                font-size: 0.8em;
                height: 290px;
                overflow: hidden;

                p {
                    margin-top: 0.7em;
                    margin-bottom: 0.7em;
                }


                .media-campaign-item-background {
                    height: 150px;
                    overflow: hidden;

                    img {
                        width: 100%;
                    }

                    .overlay {
                        background: $overlay-background;
                        display: none;
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        top: 0;
                        bottom: 0;
                        right: 0;
                        left: 0;
                        color: $overlay-text-color;
                        @include opacity(0.6);

                        @media(min-width: $media-centre-mobile-breakpoint) {
                            display: none;
                        }
                    }
                }
            }

            .media-campaign-item-footer {
                height: 65px;
            }

            .media-campaign-find-out-more {
                color: $hero-media-find-out-more;
                text-align: center;
                display: block;
                font-weight: bold;
                text-decoration: none;

                &:hover {
                    text-decoration: underline;
                }

                &:after {
                    /* font-family: $font-icons;
                    font-size: 0.7em; */
                    // font-weight: normal;
                    padding-left: 0.6em;
                    @include icon($chevron-right, 0.7em);
                }
            }

            .media-campaign-item-details {
                border-top: 1px solid #A8B9D0;
                margin-top: 1em;
                font-size: 0.9em;
                font-weight: bold;
                text-align: right;
                color: white;
                padding: 5px;

                &:before {
                    @include icon($share, 1em, $white);
                    padding: 5px;
                }
            }
        }
    }

    .media-item-controls {
        display: block;
        margin: 0 auto;
        width: 180px;
        margin-top: -36px;
        margin-bottom: 30px;
        z-index: 5;
        position: relative;

        @media(min-width: $media-centre-mobile-breakpoint) {
            display: none;
        }

        display: block\9 !important; /* IE 8-9 */
        .media-item-control-right {
            text-align: center;
            position: absolute;
            right: 0;
            padding: 15px;
            padding-left: 25px;
            padding-right: 25px;

            &:after {
                /* font-family: $font-icons;
                content: $chevron-right; */
                cursor: pointer;
                @include icon($chevron-right);
            }
        }

        .media-item-control-left {
            text-align: center;
            position: absolute;
            left: 0;
            padding: 15px;
            padding-left: 25px;
            padding-right: 25px;

            &:after {
                /* font-family: $font-icons;
                content: $chevron-left; */
                cursor: pointer;
                @include icon($chevron-left);
            }
        }

        .slick-disabled {
            color: $slick-colors;

            &::after {
                cursor: default;
            }
        }
    }

    .slick-dots {
        width: 90px;
        text-align: center;
        margin: 0 auto;
        margin-top: 20px;

        li {
            display: inline-block;
            background: $slick-colors;
            width: 10px;
            height: 10px;
            margin-right: 5px;
            margin-left: 5px;
            border-radius: 10px;

            button {
                display: none;
            }

            &.slick-active {
                background: $slick-active;
            }
        }
    }

    .view-all-latest-news {
        padding: 1em;
        padding-top: 2em;
        text-align: center;
        font-size: 1.3em;
        color: $hero-media-find-out-more;
        text-shadow: 0px 1px 4px rgba(0,0,0,0.7);
        text-decoration: underline;

        @media(min-width: $media-centre-mobile-breakpoint) {
            padding-top: 1em;
        }
    }
}
