﻿$fw-copy: $white;

.pc-fw-container {
    position: relative;
    overflow: hidden;
    max-height: 500px;
}

.pc-fw-image-container {
    width: 100%;
}

.pc-fw-image {
    width: 100%;
    display: block;
}

.pc-fw-copy-container {
    position: absolute;
    top: 30px;
    left: 0;
    width: 100%;
    padding: 0 20px;
    z-index: 2;

    @media(min-width: $tablet) {
        left: 0;
        right: 0;
        top: 50px;
        max-width: $desktop;
        margin: auto;
    }
}

.pc-fw-copy {
    @include pc-block-heading();
    color: $fw-copy;

    @media(min-width: $mobile-smallest) {
        width: 60%;
    }

    @media(min-width: $tablet) {
        width: 50%;
    }
}

.pc-fw-line {
    @include pc-line-break();
    display: block;
    text-align: left;
}