﻿@mixin hero-container {
    position: relative;
    text-align: center;
    width: 100%;
    overflow: hidden;

    @media(min-width: $hero-medium) {
        max-height: 600px;
    }

    @media (min-width: 691px) and (max-width: 766px) {
        margin-top: 65px; // This is temporary, and should be removed when the new banner is the only one used. See `.header-hero`
    }
}

@mixin hero-media {
    display: block;
    width: 100%;
    position: absolute;
    top: 0;
}

@mixin hero-media-img {
    position: absolute;
    top: 0;
    display: block;
    width: 100%;
    -webkit-user-drag: none;
    -khtml-user-drag: none;
    -moz-user-drag: none;
    -o-user-drag: none;
    -webkit-user-drag: none;

    @media(min-width: $hero-medium) {
        position: static;
        display: inline-block;
        vertical-align: middle;
        width: 1500px;
    }

    @media (min-width: $hero-medium) and (max-width: 1500px) {
        position: relative;
        left: 50%;
        z-index: 0;
        margin-left: -100%;
    }
}

@mixin hero-content-wrapper {
    position: relative;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 1;
}

@mixin hero-content-container {
    display: table;
    table-layout: fixed;
    width: 100%;
    height: 100%;
    padding: 20px 0;
    margin: 0 auto;
    min-height: 300px;

    @media (min-width: 590px) {
        min-height: 420px;
    }

    @media (min-width: $hero-small) {
        padding: 25px 70px;
        max-width: 620px;
    }

    @media (min-width: $hero-medium) {
        width: 480px;
        padding: 20px;
        margin: 0 auto;
    }

    .hero-container-cookie & {
        padding-bottom: $hero-info-bar-height-mobile;

        @media(min-width: $hero-small) {
            padding-bottom: $hero-info-bar-height-tablet;
        }

        @media(min-width: $hero-medium) {
            padding-bottom: $hero-info-bar-height-desktop;
        }
    }
}

@mixin hero-content-align {
    display: table-cell;
    vertical-align: middle;
}

@mixin hero-h1-title {
    font-size: inherit;
    font-weight: 400;
}

@mixin hero-title($line-break: false) {
    font-size: 20px;
    font-weight: 400;
    line-height: 1.3;
    text-shadow: 0 1px 6px rgba(0, 0, 0, 0.6);
    margin-bottom: 30px;
    padding: 0 50px;

    .light-mode & {
        text-shadow: 0 1px 6px rgba(255, 255, 255, 0.6);
    }

    @media (min-width: $hero-medium) {
        font-size: 30px;
        padding: 0;
    }

    @if ($line-break) {
        &:after {
            content: '';
            display: block;
            margin: 15px auto 0;
            width: 70px;
            height: 2px;
            margin-top: 15px;
            background-color: $white;
        }
    }

    .hero-container-cookie & {
        margin-bottom: 0;
    }
}

@mixin hero-proposition-text-mobile {
    @media(min-width: $hero-medium) {
        display: none;
    }
}

@mixin hero-proposition-image {
    display: block;
    margin: 12px auto 0;
    max-width: 100%;

    @media(min-width: $hero-medium) {
        max-width: 350px;
    }
}

@mixin hero-proposition-text-desktop {
    display: block;

    &.is-hidden-on-mobile {
        display: none;

        @media(min-width: $hero-medium) {
            display: block;
        }
    }
}

@mixin hero-sub-title {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 10px;
    text-shadow: 0 1px 6px rgba(0, 0, 0, 0.6);

    .light-mode & {
        text-shadow: 0 1px 6px rgba(255, 255, 255, 0.6);
    }

    @media(min-width: $hero-medium) {
        font-size: 21px;
    }
}

@mixin hero-cta-bubble-container {
    display: inline-block;
    max-width: 75px;
    vertical-align: top;
    margin: 0 10px;
    font-size: 16px;
    line-height: 1.1;

    @media(min-width: $hero-medium) {
        width: 93px;
        margin: 0 20px;
        max-width: none;
    }
}

@mixin hero-cta-bubble-link {
    display: block;
    color: $white;
}

@mixin hero-cta-bubble-text {
    display: block;
    margin-top: 10px;
    font-size: 14px;
    font-weight: 700;
    text-shadow: 0 1px 6px rgba(0, 0, 0, 0.6);

    .light-mode & {
        text-shadow: 0 1px 6px rgba(255, 255, 255, 0.6);
    }

    @media(min-width: $hero-medium) {
        font-size: 18px;
    }
}

$hero-cta-icon-color: $hero-link-icon-color;
$hero-cta-icon-size: 26px;
$hero-cta-icon-size-desktop: 33px;

$hero-cta-icon-config: (car, carIcon, $car, $hero-cta-icon-size, $hero-cta-icon-size-desktop, null)
,
(home, homeIcon, $home, 28px, 36px, null),
(van, vanIcon, $van, $hero-cta-icon-size, $hero-cta-icon-size-desktop, 26px),
(motorbike, bikeIcon, $motorbike, $hero-cta-icon-size, $hero-cta-icon-size-desktop, 26px),
(life, lifeIcon, $life, $hero-cta-icon-size, $hero-cta-icon-size-desktop, null),
(pet, petIcon, $pet, $hero-cta-icon-size, $hero-cta-icon-size-desktop, null),
(energy, energyIcon, $energy, 31px, 39px, 39px),
(travel, travelIcon, $plane, $hero-cta-icon-size, $hero-cta-icon-size-desktop, null)
;

@each $item in $hero-cta-icon-config {
    .hero-cta-link-bubble-#{nth($item, 1)}:before,
    .hero-cta-link-bubble-#{nth($item, 2)}:before {
        @include icon(nth($item, 3), nth($item, 4), $hero-cta-icon-color);
        @include icon-center(nth($item, 4));

        @media(min-width: $hero-medium) {
            @include icon-center(nth($item, 5));
        }
    }

    @if(#{nth($item, 6)} != null) {
        .lph-info-bar-icon .hero-cta-link-bubble-#{nth($item, 2)}:before,
        .hh-info-bar-icon .hero-cta-link-bubble-#{nth($item, 2)}:before {
            @include icon(nth($item, 3), nth($item, 6), #0A4C8C);
            @include icon-center(nth($item, 6));
        }
    }
}

@mixin hero-info-bar-wrapper {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: black;
    background-color: rgba(0, 0, 0, 0.5);
    height: $hero-info-bar-height-mobile;

    @media(min-width: $hero-small) {
        height: $hero-info-bar-height-tablet;
    }

    @media(min-width: $hero-medium) {
        max-height: $hero-info-bar-height-desktop;
    }
}

@mixin hero-info-bar-container {
    padding: 20px 15px;

    @media(min-width: $hero-small) {
        max-width: 500px;
        margin: auto;
        padding: 12px 15px;
    }

    @media(min-width: $hero-medium) {
        max-width: 770px;
        padding: 25px 20px;
    }
}

@mixin hero-info-bar-content {
    position: relative;
    padding-right: 55px;
    margin-bottom: 10px;

    @media(min-width: $hero-small) {
        padding: 0;
        font-size: 0;
        margin-bottom: 15px;
    }

    @media(min-width: $hero-medium) {
        display: inline-block;
        width: 60%;
        margin-bottom: 0;
        vertical-align: middle;
    }
}

@mixin hero-info-bar-personalisation {
    text-align: left;
    margin-bottom: 6px;
    line-height: 1;

    @media(min-width: $hero-small) {
        display: inline-block;
        width: 49%;
        margin: 0 0 0 2%;
        vertical-align: middle;
        font-size: 16px;
    }

    @media(min-width: $hero-medium) {
        line-height: 1.3;
        width: auto;
        margin: 0 35px 0 0;
    }

    .mkt-circular-icon-container {
        position: absolute;
        right: 0;
        top: 50%;
        width: 45px;
        margin-top: -21px;

        @media(min-width: $hero-small) {
            position: static;
            display: inline-block;
            width: 60px;
            vertical-align: middle;
            margin: 0 8px 0 0;
        }
    }
}

@mixin hero-info-bar-icon-container {
    position: absolute;
    right: 0;
    top: 50%;
    width: 45px;
    margin-top: -21px;

    @media(min-width: $hero-small) {
        position: static;
        margin: 0 8px 0 0;
        width: 63px;
        display: inline-block;
        vertical-align: middle;
    }
}

@mixin hero-info-bar-icon-bubble {
    @include compare-bubbles(false);

    &:before {
        color: $hero-non-link-icon-color;
        display: block;
        margin-left: auto;
        margin-right: auto;
        width: 100%;
        position: absolute;
        top: 50%;
    }
}

@mixin hero-info-bar-sub-text {
    font-size: 14px;

    @media(min-width: $hero-small) {
        display: block;
    }

    @media(min-width: 520px) {
        font-size: 16px;
    }
}

@mixin hero-info-bar-main-text {
    font-size: 14px;
    font-weight: 700;

    @media(min-width: $hero-small) {
        display: block;
    }

    @media(min-width: 520px) {
        font-size: 20px;
    }
}

@mixin hero-info-bar-returner-container {
    text-align: left;
    line-height: 1;

    @media(min-width: $hero-small) {
        display: inline-block;
        width: 49%;
        vertical-align: middle;
        text-align: center;
        font-size: 16px;
    }

    @media(min-width: $hero-medium) {
        text-align: left;
        line-height: 1.3;
    }
}

@mixin hero-info-bar-welcome {
    position: relative;

    @media(min-width: $hero-small) {
        display: inline-block;
        vertical-align: middle;
    }

    @media(min-width: $hero-medium) {
        &:after {
            content: "";
            width: 2px;
            height: 58px;
            position: absolute;
            top: -8px;
            right: -15px;
            background-color: $white;
        }
    }
}

@mixin hero-info-bar-cta-container {
    text-align: center;
    font-size: 0;

    @media(min-width: $hero-small) {
        text-align: left;
    }

    @media(min-width: $hero-medium) {
        display: inline-block;
        vertical-align: middle;
        width: 211px;
    }
}

@mixin hero-info-bar-cta-align {
    @media(min-width: $hero-small) {
        display: inline-block;
        vertical-align: middle;
        width: 49%;
        margin: 0 2% 0 0;
    }

    @media(min-width: $hero-medium) {
        width: 100%;
    }
}

@mixin hero-info-bar-cta {
    @include cta-button;
    width: 100%;
    margin-bottom: 9px;

    @media(min-width: 330px) {
        max-width: 210px;
    }

    @media(min-width: $hero-small) {
        margin: 0;
    }

    @media(min-width: $hero-medium) {
        margin-bottom: 20px;
    }
}

@mixin hero-info-bar-cta-secondary {
    font-size: 18px;
    font-weight: 700;
    text-decoration: underline;

    @media(min-width: $hero-small) {
        display: inline-block;
        vertical-align: middle;
        width: 49%;
        text-align: center;
    }

    @media(min-width: $hero-medium) {
        width: 100%;
        text-align: left;
    }

    &:hover {
        color: lighten($brand-green, 8%);
    }
}

@mixin hero-legal-text {
    padding-top: 25px;
    font-size: small;
    color: $secondary-grey;
}

@mixin hero-action-links-divider {
    display: inline-block;
    border: 1px $white solid;
    height: 110px;
    margin: 0 5px;

    @media(min-width: $hero-medium) {
        margin: 0 10px;
        height: 90px;
    }
}