﻿.ss-title {
    font-size: 1rem;
    font-weight: 700;
    margin: 0 0 10px;
}

.ss-container {
    box-sizing: content-box;
    -webkit-box-sizing: content-box;
    -moz-box-sizing: content-box;
    font-size: 0;
    margin-bottom: 20px;
    padding-top:50px;
    @media (max-width: $mobile-low) {
        padding-top:15px;
    }
}

.ss-container-page {
    text-align: center;
    margin-top: 0;
    margin-bottom: 0;
    padding-top: 0;
    padding-bottom: 0;

    @media(min-width: $touch-nav) {
        max-width: $desktop;
        text-align: right;
        height: 45px;
        z-index: 1000;
        padding-right: 22px;
        box-sizing: border-box;
    }
}

.ss-link {
    display: inline-block;
    margin: 0 3px;
    font-size: 22px;
    color: $white;
    text-shadow: none;
    text-align: center;
    height: 45px;
    width: 45px;
    overflow: hidden;
    position: relative;
    text-indent: -999em;

    .no-touch & {
        &:before {
            top: 9px;
        }
    }

    &:before {
        display: block;
        position: absolute;
        top: 11px;
        text-indent: 0;
        width: 100%;
    }
}

.ss-link-fb {
    background-color: $chambray;

    &:before {
        @include icon($facebook, 22px, $white);
    }

    &:hover,
    &:focus {
        background-color: lighten($chambray, 7%);
    }
}

.ss-link-tw {
    background-color: $picton-blue;

    &:before {
        @include icon($twitter, 22px, $white);
    }

    &:hover,
    &:focus {
        background-color: lighten($picton-blue, 7%);
    }
}

.ss-link-gp {
    background-color: $alizarin-crimson;

    &:before {
        @include icon($google-plus, 22px, $white);
    }

    &:hover,
    &:focus {
        background-color: lighten($alizarin-crimson, 7%);
    }
}

.ss-link-li {
    background-color: $linkedinBlue;

    &:before {
        @include icon($linkedin, 22px, $white);
    }

    &:hover,
    &:focus {
        background-color: lighten($linkedinBlue, 7%);
    }
}

.ss-link-wa {
    display: none;
}

.is-mobile > .ss-link-wa {
    display: inline-block;
    background-color: $frog-skin;

    &:before {
        @include icon($whatsapp, 22px, $white);
    }

    &:hover,
    &:focus {
        background-color: lighten($frog-skin, 7%);
    }
}

.ss-link-whatsapp {
    display: none;

    @media (max-width: $mobile) {
        display: inline-block;
        background-color: $frog-skin;

        &:before {
            @include icon($whatsapp, 22px, $white);
        }

        &:hover,
        &:focus {
            background-color: lighten($frog-skin, 7%);
        }
    }
}
