﻿.sb-container {

}

.sb-inner {
    text-align: center;
    padding: 25px;

    @media(min-width: $mobile-low) {
          text-align: left;
    }
}

.sb-copy {
    display: inline-block;
    margin-bottom: 20px;
    margin-right: 1%;
    font-weight: 700;
    font-size: 20px;

    @media(min-width: $mobile-low) {
        margin-bottom: 0;
        width: 58%;
        vertical-align: middle;
    }
}

.sb-cta {
    @media(min-width: $mobile-low) {
        display: inline-block;
          width: 40%;
          text-align: center;
    }
}

.sb-button {
    @include branded-button-small;
    display: inline-block;
    text-align: center;

    @media(min-width: $mobile-low) {
        width: 100%;
    }

    @media(min-width: $mobile) {
        width: 60%;
    }
} 