.ss-click-to-share {
	cursor: pointer;
	position: relative;
	max-width: 255px;
	z-index: 200;
	height: 52.5px;
	
    @media (min-width: $mobile){
    	max-width: 205px;  
    }

	&.clicked {
		.ss-title {
			display: none;
		}
		&.fixed {
			background-color: $rough-sea;
			.ss-container {
				width: 100%;
			}
		}
	}

	&.fixed {
		position: fixed;
		top: 0;
		width: 100%;
		left: 0;
		margin: 0;
		max-width: none;

		.ss-title {
			width: 100%;
		}
	}

	.ss-container {
		position: absolute;
		top: 0;
		left: 50%;
		z-index: -1;
		margin-left: -102px;

		@media (min-width: $mobile) {
			top: 2px;
			left: -1px;
			margin-left: 0;
		}

		&.is-mobile {
			top: 0;
			margin-left: -127.5px;
		}
	}

	.ss-title {
        @include branded-button();
		padding: 15px;
		position: relative;
		color: $white;
		@include transition(background-color 0.2s, color);
		&:hover {
			background-color: $deep-ice;
		}
	}
}