$video-container: $black;
$bottom-carousel-container: $big-stone;
$carousel-control-color: $white;
$image-border: $deep-ice;

.media-centre-playlist-block {
	.title {
		@include media-centre-title();
	}
	.video-container {
		background-color: $video-container;
	}

	.bottom-carousel-container {
		background-color: $bottom-carousel-container;
		position: relative
	}

	.bottom-carousel-content {
		padding: 20px 40px;
	}

	.current-slide-content {
		margin-bottom: 10px;
		padding: 0 10px;
		text-align: center;
		
		.slide-content {
			font-size: 16px;
			margin-top: 40px;
			.slide-title {
				font-weight: 700;
				font-size: 22px;
				margin-bottom: 8px;
			}
		}
	}

	.controls {
		display: none;
		&.active {
			display: block;
		}
		position: absolute;
		top: 55%;
		width: 100%;
		.prev, .next {
			display: block;
			position: absolute;
			top: 0;
			cursor: pointer;
			padding: 7px;
		}

		.prev {
			left: 6px;
			&:before {
				@include icon ($chevron-left, 22px, $carousel-control-color);
			}
		}

		.next {
			right: 15px;
			&:before {
				@include icon ($chevron-right, 22px, $carousel-control-color);
			}
		}
	}

	.carousel-title {
		padding-bottom: 20px;
	}

	.slick-track {
		padding: 5px 0;
		.media-center-playlist-carousel-item {
			margin-right: 20px;
		}
	}

	.media-center-playlist-carousel-wrapper {
		.media-center-playlist-carousel-item {
			margin-right: 20px;
			width: 23%;
			@include inline-block();
			&:last-child {
				margin-right: 0;
			}
			.item-title {
				font-size: 14px;
			}

			.slide-content {
				display: none;
			}
		}
		.thumbnail {
			.thumbnail-image {
				position: relative;
				&:hover {
					cursor: pointer;
				}
				&:after {
					@include icon($play, 30px, $carousel-control-color);
					top: 50%;
					left: 50%;
					margin-top: -15px;
					margin-left: -12.5px;
					position: absolute;
				}
			}
			img {
				max-width: 100%;
				border: 1px solid $image-border;
				padding: 5px;
			}
		}
	}
}