﻿$sbs-background-color: $white;
$sbs-title-color: $deep-ocean;
$sbs-text-color: $shark;

.pc-sbs-block-container {
    font-size: 0px;
    padding: 25px;
    background-color: $sbs-background-color;
}

.pc-sbs-block-title-container {
    display: inline-block;
    width: 100%;
    vertical-align: top;
    margin-bottom: 20px;

    @media only screen and (min-width: $tablet) {
        width: 50%;
        margin-bottom: 0;

        .pc-sbs-block-title-right & {
            float: right;
        }
    }
}

.pc-sbs-block-title {
    @include pc-block-heading();
    color: $sbs-title-color;

    @media only screen and (min-width: $tablet) {
        padding-right: 2em;
    }
}

.pc-sbs-block-line-break {
    @include pc-line-break();
    background-color: $sbs-title-color;
}

.pc-sbs-block-content {
    color: $sbs-text-color;
    display: inline-block;
    font-size: 16px;
    line-height: 1.3;
    width: 100%;

    a {
        @extend .underlinedTextOnHover;
    }

    @media(min-width: $mobile) {
        font-size: 19px;
    }

    @media only screen and (min-width: $tablet) {
        width: 50%;

        .pc-sbs-block-title-right & {
            width: 45%;
        }
    }
}
