.sg-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    text-align: center;
    z-index: 2;
}

.sg-wrapper {
    display: inline-block;
    padding: 10px 15px;
    border: 2px dotted $white;
    border-top: 0;
    border-radius: 0 0 10px 10px;
}

.sg-content {
    display: inline-block;
    vertical-align: middle;
    line-height: 1;
    font-size: 16px;

    &:before {
        @include icon($simples-guide, 24px, #fff);
        display: inline-block;
        vertical-align: middle;
        margin-right: 9px;

        .sg-small & {
            margin-right: 0;
        }
    }
}