﻿.panel {
	padding-top: 30px;
	padding-bottom: 30px;

    @media(min-width: $mobile) {
        padding-left: 25px;
	    padding-right: 25px;
    }
	
    max-width: $desktop;
    margin: auto;
    position: relative;
}

.flex {
	@include flexbox();
	@include flexflow(row, nowrap);
	
    .flex-cell {
		//@include border-box;
		//margin: 0 0 20px 2%;
		
        &:first-child {
			margin-left: 0;
		}
	}
}

.lt-ie10 {
	.flex {
		display: table;
		border-spacing: 15px;
		margin: -15px -15px 5px;
		
        .flex {
			border-spacing: 0;
			margin: 0;
		}
	}

	.flex-cell {
		display: table-cell;
	}
}

.one-quarter {
	@include flex(1 1 23.5%, 23.5%);
}

.one-third, .one-third-to-half {
	@include flex(1 1 32%, 32%);
}

.one-half {
	@include flex(2 2 49%, 49%);
}

.two-thirds, .two-thirds-to-half {
	@include flex(2 2 66%, 66%);
}

.three-quarters, .three-quarters-to-half {
	@include flex(3 3 74.5%, 74.5%);
}

.full-width {
	@include flex(1 1 100%, 100%);
}

.offscreen-text {
  position:absolute;
  left:-10000px;
  top:auto;
  width:1px;
  height:1px;
  overflow:hidden;
}
