.media-centre-title-block {
	position: relative;
	max-width: 978px;
    margin: 0 auto;
    padding: 30px 0;

    @media (min-width: $mobile) {
    	padding: 30px 25px 0;
    }

	img {
		max-width: 100%;
	}
	&.has-teaser-image {
		.title-block-inner {
			margin-bottom: 0;
			padding: 0;
			margin-bottom: 20px;
			padding: 0 20px;
			.title, .date {
				color: $white;
			}

			@media screen and (min-width: $tablet ) {
				position: absolute;
				top: 40px;
				left: 40px;
				right: 40px;
				padding: 0;
				.title, .date {
					color: inherit;
				}
			}
		}
	}
	.title-block-inner {
		position: initial;
		padding: 0;

         @media screen and  (max-width:$mobile) {
        margin: 0 10px;
        }
		
   
	}
	.title {
		font-size: 26px;
		line-height: 38px;

		@media screen and (min-width: $mobile) {
			font-size: 34px;
			line-height: 45px;
		}

		@media screen and (min-width: $tablet) {
			width: 60%;
		}

	}

	.date {
		font-size: 14px;
		font-weight: bold;
		margin-top: 5px;

		@media screen and (min-width: $tablet) {
			margin-top: 10px;
		}
	}
}

.media-centre {
	margin-top: 0;
	padding-top: 0;
	&.item-page {
		@media screen and (min-width: $mobile) {
            text-align: justify;
			&:after {
				content: '';
				width: 100%;
				display: inline-block;
			}
        }
		.left-content {
			margin: 0;

			@media screen and (min-width: $tablet) {
				vertical-align: top;
		        width: 63%;
		        margin: 0 3% 0 0;
		        @include inline-block;
            }

            .fluid-width-video-wrapper {
            	margin: 15px 0;
            }

			img {
				max-width: 100%;
				height: 100%;
			}


			p {
				margin: 0 0 20px 0;
				font-size: 16px;
			}
			ul {
				margin: 0 0 20px 0;
				list-style: disc outside none;
				padding-left: 20px;
				li {
					font-size: 16px;
				}
			}

            table {
                border: 2px solid #fff;
				@include fontsize(0.65);
                line-height: 16px;
                margin-bottom: 5px;
                text-align: center;
            }

            td {
                border: 1px solid #fff;
                padding: 5px;
                vertical-align: middle;
            }
		}

		.right-content {
			margin: 0 0 20px;

			@media screen and (min-width: $tablet) {
		        vertical-align: top;
		        width: 33%;
                margin: 0;
		        @include inline-block;
		    }

			.contact-press-office-person {
		        @media screen and (min-width: $tablet) {
		            width: 100%;
		        }
		    }

			.download-item {
				@include branded-button();
				display: block;
				text-align: center;
				margin: 15px 0;
			}

			.rhp-block {
				.panel {
					padding: 10px;
				}
			}
		}

		.sub-title {
			font-size: 34px;
			line-height: 34px;
            margin-bottom: 0.6em;
		}
	}
}
