﻿.h2i-container {
    @include hero-container();

    @media(min-width: $hero-medium) {
        max-height: none;
    }
}

.h2i-media {
    @include hero-media();
}

.h2i-media-img {
    @include hero-media-img();
}

.h2i-content-wrapper {
    @include hero-content-wrapper();
}

.h2i-content-container {
    @include hero-content-container();
}

.h2i-content-align {
    @include hero-content-align();
}

.h2i-proposition {
    @include hero-title();
    margin: 0;

    .h2i-container-cookie & {
        padding: 20px 0;
    }
}

.h2i-product-name-text {
    @include hero-proposition-text-desktop;  
    font-size:32px;
}

hr.h2i-product-name-desktop{
    border-top: 3px solid #fff;
    width: 17%;
    margin-top: 0;
    margin-bottom: 15px;
    @media(min-width: $hero-medium) {
            margin-bottom: 30px;
        };
}

.h2i-proposition-text-mobile {
    @include hero-proposition-text-mobile;
}

.h2i-proposition-text-desktop {
    @include hero-proposition-text-desktop;
    @include hero-h1-title;
}

.h2i-proposition-image {
    @include hero-proposition-image;
}

.h2i-actions {
    margin: 30px auto;
    .mkt-circular-icon-container {
        width: 65px;
        margin: 0 auto;

        @media(min-width: $hero-medium) {
            width: 90px;
        }
    }
}

.h2i-actions-text {
    @include hero-sub-title;
    margin-top: 12px;
}

.h2i-actions-ctas {
    max-width: 310px;
    margin: 0 auto;
    font-size: 0;

    @media(min-width: $hero-medium) {
        max-width: none;
    }
}

.h2i-actions-cta {
    display: inline-block;
    width: 30%;
    vertical-align: top;
    font-size: 16px;

    @media(min-width: $hero-medium) {
        width: auto;
        padding: 0 20px;
    }

    .mkt-circular-icon-link {
        @include circular-icon-container();

        .light-mode & {
            .mkt-circular-icon-text {
                color: $brand-blue-dark;
                text-shadow: 0 1px 6px rgba(255,255,255,0.6)
            }
        }

        .mkt-circular-icon {
            background-color: $circular-icon-background-color-hover;

            &:before {
                color: $circular-icon-color-hover;
            }
        }

        &:hover {
            text-decoration: none;

            .mkt-circular-icon {
                background-color: $circular-icon-color-hover;

                &:before {
                    color: $circular-icon-background-color-hover;
                }
            }

            .mkt-circular-icon-text {
                text-decoration: underline;
                &.no-underline {
                    text-decoration:none;
                }
            }
        }
    }
}

.h2i-actions-title {
    @media(min-width: $hero-medium) {
        display: inline-block;
        position: relative;
        text-align: left;
        top: -30px;
        margin: 0 0 0 10px;
    }
}

.h2i-icon-left {
    .h2i-actions-title {
        @media(min-width: $hero-medium) {
            max-width: 85px;
        }
    }
}

.h2i-icon-right {
    .h2i-actions-title {
        @media(min-width: $hero-medium) {
            max-width: 40px;
        }
    }
}

.h2i-action-links-divider {
    @include hero-action-links-divider;
}

.h2i-info-bar-cta-hollow {
    @include cta-button("hollow");
}

.h2i-info-bar-cta-align {
    @include hero-info-bar-cta-align;
}

.h2i-legal-text {
    @include hero-legal-text;
}

.h2i-circular-icon-container {
    @extend .mkt-circular-icon-container;

    @media(min-width: $hero-medium) {
        display: inline-block;
    }
}

.h2i-circular-icon-link {
    @extend .mkt-circular-icon-link-inverse;
    display: inline-block;
}


