$media-centre-mobile-breakpoint: 986px;
.media-centre-title {
    &.panel {
        font-size: 46px;
        text-align: center;
        font-weight: 600;
        padding: 40px 0 10px 0;
        line-height: 2.3rem;

        @media(max-width: $media-centre-mobile-breakpoint) {
            font-size: 44px;
            line-height: 2.5rem;
            -webkit-transition: border-color .25s ease,box-shadow .25s ease;
            transition: border-color .25s ease,box-shadow .25s ease;
        }
    }
}

.media-centre-go-back-to-link {
    display: block;
    max-width: 972px;
    margin: 20px auto 0;
    padding: 0 10px;

    @media(min-width: $mobile) {
        padding: 0 25px;
    }

    a {
        display: inline-block;
        font-weight: bold;
        &:hover {
            text-decoration: underline;
        }
    }
}