﻿.pc-article-page{

    .pc-content-intro-links{
        position: absolute;
        left: 20px;
        top: 50%;
        margin-top: -150px;
        width: 140px;
        color: #96d491;
        height: 300px;
        @media(max-width: $touch-nav){
            display: none;
        }
        > div{
            display: table;
            height: 100%;
            > div{
                display: table-cell;
                vertical-align: middle;
            }
        }
        ol {
            counter-reset: item;
            margin-left: 0;
            padding-left: 0;
            list-style-type: decimal;
        }
        li {
            display: block;
            margin-bottom: .5em;
            margin-left: 1em;
            font-size: 30px;
            font-weight: 600;
        }
        li:before {
            display: inline-block;
            content: counter(item) "";
            counter-increment: item;
            width: 30px;
            margin-left: -30px;
            font-size: 35px;
            line-height: 28px;
            font-weight: 300;
        }
        a{
            font-size: 12px;
            color: #96d491;
            display: inline-block;
            vertical-align: top;
            line-height: 14px;
        }
    }

    .pc-maincontent-container{
        width: 50%;
        margin: 0 auto;
        padding: 50px 0;
        h2{
            font-size: 1.8em;
            margin: 40px 0;
            position: relative;
            &::before, &::after{
                content: '';
                position: absolute;
                top: -12px;
                left: 0;
                width: 60px;
                height: 2px;
                background-color: $deep-ice;
            }
            &::after{
                top: auto;
                bottom: -15px;
            }
        }
        p{
            font-size: $paragraph-small;
            margin-bottom: 30px;
        }
        > div{
            :first-child{
                margin-top: 10px;
            }
            :last-child{
                margin-bottom: 0;
            }
        }
        @media(max-width: $touch-nav){
            width: 100%;
            padding-left: 20px;
            padding-right: 20px;
        }
    }

}
