$media-player-thumb-background: $mine-shaft;
$media-player-thumb-background-hover: $viking-blood;

.media-block {
    text-align: center;
    img {
        max-width: 100%;
    }

    .media-block-content {
        z-index: 500;
        margin-top: 0;
        padding-top: 20px;
        &:first-child {
            padding-top: 0;
            padding-bottom: 20px;
        }
        @media screen and (min-width : $mobile) {
            margin-top: 0;
            position: relative;
        }

        p {
            margin: 15px 0;
            &:first-child {
                margin-top: 0;
            }
            &:last-of-type {
                margin-bottom: 0;
            }
        }
    }

    .block-title {
        @include media-centre-title();
        line-height: 1em;

        + .media-block-additional-heading {
            margin-top: 0;
        }

    }

    .block-subtitle {
        display: block;
        font-size: 14px;
        text-transform: none;
        margin-bottom: 8px;
        margin-top: 10px;
    }

    .main-cta {
        display: inline-block;
        margin-top: 20px;
        @include branded-button();
    }

    .main-hyperlink {
        display: block;
        text-decoration: underline;
        margin-top: 20px;
        font-size: 22px;
    }

    .media-player-thumbnail {
        display: block;
        position: relative;
        &:after {
            @include icon($play, 30px, $white);
            position: absolute;
            top: 50%;
            left: 50%;
            padding: 17px 34px;
            background-color: $media-player-thumb-background;
            background-color: rgba(0, 0, 0, 0.78);
            @include border-radius(15px);
            margin-left: -48px;
            margin-top: -32px;
        }

        &:hover {
            &:after {
                background-color: $media-player-thumb-background-hover;
            }
        }

        img {
            display: block;
        }
    }
}