﻿.pc-central-image {
    background-color: #fff;
    overflow: hidden;

    &.pc-central-title-right {
        @media(min-width: $touch-nav) {
            position: relative;
        }

        .pc-content-image-content {
            @media(min-width: $touch-nav) {
                position: absolute;
                left: 0;
                top: 50%;
                -webkit-transform: translateY(-50%);
                -ms-transform: translateY(-50%);
                transform: translateY(-50%);
                width: 33.3%;
            }
        }

        .pc-central-image-left-wrapper {
            @media(min-width: $touch-nav) {
                position: absolute;
                right: 0;
                top: 50%;
                -webkit-transform: translateY(-50%);
                -ms-transform: translateY(-50%);
                transform: translateY(-50%);
            }
        }
    }

    > div {
        display: table;
        max-width: $max-width;
        margin: 0 auto;
        max-width: 980px;

        > * {
            width: 33.33%;
            padding: 1em;
            display: table-cell;
            vertical-align: middle;
            padding: 30px;
        }
    }

    &.no-padding {
        padding: 0;
        > div > * {
            padding: 0;
        }

        .pc-text-image {
            margin-top: 0;
            width: 100%;
        }
    }

    h2.pc-header-large {
        color: $deep-ice;
        text-align: left;
        font-size: 1.5em;
        margin: 0;

        &::before, &::after {
            background-color: $deep-ice;
            left: 0;
            margin-left: 0;
        }
    }

    .pc-content-image-content {
        color: $light-blue;
        font-size: $paragraph-small;
        line-height: 1.35;

        @media(min-width: $mobile-low) {
            width: 80%;
            margin: 0 auto;
        }

        @media(min-width: $mobile) {
            padding: 0 3em;
            width: auto;
        }
    }

    .pc-text-image-half {
        margin: 0;
        padding: 0 3em;

        img {
            float: none;
            margin: 0 auto;
            width: 100%;
        }
    }

    @media(max-width: $touch-nav) {
        padding: 20px 0 200px 0;
        position: relative;

        > div > * {
            width: 100%;
            display: block;
        }

        .pc-text-image-half {
            position: absolute;
            bottom: 0;
            height: initial;

            img {
                max-height: 200px;
                width: auto;
            }
        }
    }
}

.pc-master {
    @media(min-width: $touch-nav) {
        .pc-central-image {
            > div {
                min-height: 300px;
            }
        }
    }

    .pc-central-image .pc-text-image-half {
        vertical-align: bottom;
        padding: 0 1em;
    }
}
