﻿/* 
  Example usage:

  <!-- Icon as a link -->
  <a class="mkt-circular-icon-link">
      <span class="mkt-circular-icon mkt-circular-icon-car"></span>
      <span class="mkt-circular-icon-text">Icon Title</span>
  </a>


  <!-- Icon as a non link -->
  <div class="mkt-circular-icon-container">
      <span class="mkt-circular-icon mkt-circular-icon-car"></span>
      <span class="mkt-circular-icon-text">Icon Title</span>
  </div>

  <!-- Special use case, everything inside triggers the hover state -->
  <a href="#" class="mkt-circular-icon-link">
      <p>Something that should appear hover-able</p>

      <div class="mkt-circular-icon-container">
          <span class="mkt-circular-icon mkt-circular-icon-car"></span>
          <span class="mkt-circular-icon-text">Icon Title</span>
      </div>
  </a>
*/

@import "variables";
@import "mixins";

.mkt-circular-icon-container {
  @include circular-icon-container(false);
}

.mkt-circular-icon-container-large {
  @include circular-icon-container(false);
  
  width: 130px;
  margin: 0 auto;
}

.mkt-circular-icon-link {
  @include circular-icon-container();

  .mkt-circular-icon {
    &:before {
      color: $brand-green;
    }
  }

  &:hover {
      text-decoration: none;

    .mkt-circular-icon {
      background-color: $circular-icon-background-color-hover;

      &:before {
        color: $circular-icon-color-hover;
      }
    }

    .mkt-circular-icon-text {
        text-decoration: underline;
    }
  }
}

.mkt-circular-icon-link-inverse {
  @include circular-icon-container();

  .mkt-circular-icon {
    background-color: $circular-icon-background-color-hover;

    &:before {
      color: $circular-icon-color-hover;
    }
  }

  &:hover {
      text-decoration: none;

    .mkt-circular-icon {
      background-color: $circular-icon-color-hover;

      &:before {
        color: $circular-icon-background-color-hover;
      }
    }

    .mkt-circular-icon-text {
        text-decoration: underline;
    }
  }
}

.mkt-circular-icon-link-large {
  @include circular-icon-container();

  .mkt-circular-icon-large {
    &:before {
      color: $circular-icon-color-hover;
    }
  }

  &:hover {
    text-decoration: none;

    .mkt-circular-icon-text-large {
      color: $brand-green;
    }

    .mkt-circular-icon-large {
      background-color: $circular-icon-background-color;

      &:before {
      color: $brand-green;
      }
    }
  }
}

.mkt-circular-icon {
  @include circular-icon();
}

.mkt-circular-icon-large {
  @include circular-large-icon();
  background-color: $circular-icon-background-color-hover;
}

.mkt-circular-icon-text {
  @include circular-icons-text();
}

.mkt-circular-icon-text-large {
  @include circular-icons-text();
    position: relative;
    top: -68px;
    max-width: 120px;
    text-shadow: none;
    margin-bottom:-40px;
}

@each $item in $circular-icons-config {
   .mkt-circular-icon-#{nth($item, 1)}:before {
      @include icon(nth($item, 2), inherit, $circular-icon-color);
   }
}

@each $item in $circular-icons-config {
   .mkt-circular-icon-#{nth($item, 1)}-large:before {
      @include icon(nth($item, 2), inherit, $circular-icon-color);
   }
}

/* overrides for icons that don't fit the mould too well within the large green circle */
.mkt-circular-icon-large {
    &.mkt-circular-icon-credit-cards:before {
        font-size:1.2em;
    }
    &.mkt-circular-icon-savings:before {
        font-size:0.8em;
    }
    &.mkt-circular-icon-loans:before, &.mkt-circular-icon-mortgages:before, &.mkt-circular-icon-current-accounts:before, &.mkt-circular-icon-isa:before {
        font-size:1em;
    }
    &.mkt-circular-icon-breakdown:before, &.mkt-circular-icon-motorbike:before {
        font-size:.9em;
    }
} 
