﻿$text-image-heading-mobile: $deep-ocean;
$text-image-heading-desktop: #195c99;
$two-things-line-left-color: #255b99;
$two-things-line-right-color: #3ea931;

.pc-text-image-block {
    display: block;
    position: relative;
    padding: 0 20px;
    overflow: hidden;

    &.image-on-left {
        direction: rtl;

        .pc-text-image-half {
            &:first-child {
                @media (min-width: $mobile) {
                    margin-left: 2%;
                    margin-right: 0;
                }
            }

            &:last-child {
                @media (min-width: $mobile) {
                    margin-right: 2%;
                    margin-left: 0;
                }
            }
        }
    }

    @media (min-width: $mobile) {
        padding: 0;
    }

    .panel {
        @media (min-width: $mobile) {
            padding-top: 45px;
            padding-bottom: 45px;
            font-size: 0;
        }
    }

    .pc-text-image-half {
        display: block;
        direction: ltr;
        vertical-align: bottom;

        @media (min-width: $mobile) {
            text-align: left;
            @include inline-block();
            vertical-align: bottom;
            margin: auto;

            width: 48%;
            overflow: hidden;
            font-size: 19px;
        }

        &:first-child {
            @media (min-width: $mobile) {
                margin-right: 2%;
            }
        }

        &:last-child {
            @media (min-width: $mobile) {
                margin-left: 2%;
            }
        }

        p {
            margin-bottom: 10px;
        }

        img {
            display: block;
            max-width: 100%;
            margin-bottom: 0;
            position: relative;
            float: right;
            bottom: 0;

            .image-on-left & {
                left: 0;
                float: none;
            }
        }
    }
}

.pc-text-image-heading {
    @include pc-block-heading();
    color: $text-image-heading-mobile;

    @media (min-width: $tablet) {
        color: $text-image-heading-desktop;
    }
}

.pc-text-image-substantiation {
    font-size: 13px;
    line-height: 13px;
}

.pc-text-line-break {
    @include pc-line-break();
}

.pc-text-image-text {
    line-height: 1.5em;
}

.pc-image-text {
    font-size: 28px;
    line-height: 26px;
}

.pc-text-image {
    display: block;
    position: relative;
    overflow: auto;
    margin-top: 20px;
}

// Styles for the master page split image/text blocks.
// Any overrides in here can be removed once we've transfered
// everything over from the standard pages into the content hub.

.pc-master{
    .pc-text-image-block {
        position: relative;

        &.has-two-things-line {
            &:before, &:after {
                content: "";
                position: absolute;
                bottom: 0;
                width: 50%;
                height: 6px;
                background-color: $two-things-line-left-color;
            }

            .pc-text-image-half {
                margin-bottom: 6px;
            }

            &:before {
                left: 0;
            }

            &:after {
                right: 0;
                background-color: $two-things-line-right-color;
            }
        }
    }
	.pc-text-image-block .panel{
		padding: 0 50px;
	}
	.pc-text-image-heading{
		font-size: 1.7em;
		line-height: 1.2em;
	}
	.pc-text-vcenter{
		height: 100%;
		width: 100%;
		display: table;
		padding: 50px 0;
		> div{
			display: table-cell;
			vertical-align: middle;
		}
	}
	.pc-text-image-text{
		p{
			font-size: $paragraph-small;
			margin-top: 20px;
		}
	}
	.pc-text-image-half{
		height: 100%;
		.pc-button{
			margin: 20px 0 0 0;
		}
		img{
			float: none;
			margin: 0 auto;
		}
	}
	.pc-text-line-break{
		margin: 10px 0;
		height: 3px;
	}
}