﻿#landingPage {
    #DOMWindow {
        overflow: visible !important;
        left: 0 !important;
        right: 0 !important;
        margin-left: 0 !important;
        top: 0 !important;
        transform: initial !important;
        width: inherit !important;

        @media (min-width: $desktop) {
            position: fixed !important;
        }
    }

    .business-energy-panel {
        .secondary-button {
            display: inline-block;
            font: 13.3333330154419px;
            font-family: arial;
            font-weight: bold;
        }
    }
}

#modal-dialog {
    margin: 50px auto;
    width: 96%;

    @media (min-width: $desktop) {
        width: 750px;
    }


    #callback-content {
        width: 100%;

        &.confirmation {

            .advisor-help {
                background: none;
                border: 0;
                padding: 0;
                width: 100%;

                .advisor-help-detail {
                    vertical-align: top;
                    margin-bottom: 20px;

                    .button {
                        @include branded-button();
                    }
                }
            }

            .advisor-help-detail, .bulletpointed-list {
                display: inline-block;
                width: 100%;

                @media (min-width: $desktop) {
                    width: 48.5%;
                    margin-bottom: 0px;
                }
            }
        }

        h1 {
            text-transform: uppercase;
            position: relative;
            font-size: 38px;
            margin-bottom: 20px;
            padding: 5px 0 10px 50px;
            border-bottom: 1px solid $fun-blue;
            letter-spacing: 0;
            line-height: 1;

            &:before {
                content: "";
                background: url(../images/life-sprite.png) -90px 0 no-repeat;
                width: 35px;
                height: 50px;
                position: absolute;
                left: 0;
                top: 0;
            }
        }

        h4 {
            font-size: 18px;
            margin-bottom: 10px;
            padding-right: 30px;
        }

        background: $jacksons-purple;
        background: -webkit-linear-gradient($jacksons-purple,$matisse);
        background: -o-linear-gradient(transparent, transparent); /* rare browser fallback */
        -webkit-border-top-left-radius: 6px;
        -webkit-border-top-rightt-radius: 6px;
        -moz-border-top-left-radius: 6px;
        -moz-border-top-right-radius: 6px;
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1050;
        -webkit-overflow-scrolling: touch;
        outline: 0;
        -webkit-transition: -webkit-transform .3s ease-out;
        -moz-transition: -moz-transform .3s ease-out;
        -o-transition: -o-transform .3s ease-out;
        transition: transform .3s ease-out;
        display: inline-block;
        max-height: inherit;
        overflow: initial;
        padding: 0;
        position: relative;
        padding: 20px 30px;

        .callback-form {
            -webkit-border-radius: 5px;
            -moz-border-radius-: 5px;
            border-radius: 5px;
            background-color: $catalina-blue;
            border: 1px solid $fun-blue;
            padding: 15px 20px;
            position: relative;
            width: 56%;
            float: left;
            font-size: 16px;

            label {
                width: 100%;
                display: block;
                font-size: 14px;
                padding-bottom: 2px;
            }

            li {

                &.hidden-fields-not-required {
                    display: none;
                }

                &.valid-field {
                    input {
                        border-color: $primary-green;
                        color: $primary-green;
                    }

                    .validation-icon {
                        background: transparent url(../images/life-sprite.png) -30px 0 no-repeat;
                    }
                }

                .error-bubble {
                    -moz-box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .3);
                    -webkit-box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .3);
                    box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .3);
                    -webkit-border-radius: 7px;
                    -moz-border-radius-: 7px;
                    border-radius: 7px;
                    -moz-box-sizing: border-box;
                    -webkit-box-sizing: border-box;
                    box-sizing: border-box;
                    font-size: 13px;
                    color: $jacksons-purple;
                    background-color: $white;
                    border: 2px solid $validation-error;
                    width: 100%;
                    position: relative;
                    padding: 10px;
                    text-align: left;
                    line-height: 16px;
                    margin: 10px 0;
                    max-width: 100%;
                    width: 65%;

                    @media (min-width: $desktop) {
                        width: 258px;
                    }

                    em {
                        color: $validation-error;
                        font-weight: bold;
                        display: block;
                        font-size: 16px;
                        padding-bottom: 3px;
                    }

                    a {
                        color: $primary-green;
                    }

                    &:before, &:after {
                        border-style: solid;
                        content: "";
                        height: 0;
                        pointer-events: none;
                        position: absolute;
                        width: 0;
                        top: -16px;
                        left: 12px;
                    }

                    &:before {
                        border-color: $validation-error transparent;
                        border-width: 0 15px 16px;
                    }

                    &:after {
                        border-color: $white transparent;
                        border-width: 0 13px 13px;
                        margin-left: 2px;
                        margin-top: 4px;
                    }

                    .answer {
                        height: 28px;
                    }

                    &.validation-error {
                        display: none;

                        .error {
                            -moz-box-sizing: border-box;
                            background-color: #FFFFFF;
                            border: 2px solid #FF5025;
                            border-radius: 7px 7px 7px 7px;
                            box-shadow: 0 1px 4px 1px rgba(0, 0, 0, 0.3);
                            color: #1A3680;
                            font-size: 13px;
                            line-height: 16px;
                            margin: 20px 0 10px 147px;
                            padding: 10px;
                            position: relative;
                            text-align: left;
                            width: 216px;
                            box-sizing: border-box;

                            &:before {
                                border-color: #FF5025 rgba(0, 0, 0, 0);
                                border-width: 0 15px 16px;
                                border-style: solid;
                                content: "";
                                height: 0;
                                left: 12px;
                                pointer-events: none;
                                position: absolute;
                                top: -16px;
                                width: 0;
                            }

                            &:after {
                                border-color: #FFFFFF rgba(0, 0, 0, 0);
                                border-width: 0 13px 13px;
                                margin-left: 2px;
                                margin-top: 4px;
                                border-style: solid;
                                content: "";
                                height: 0;
                                left: 12px;
                                pointer-events: none;
                                position: absolute;
                                top: -16px;
                                width: 0;
                            }
                        }
                    }
                }

                &.error-show, &.error-required {

                    .validation-icon {
                        background: transparent url(../images/life-sprite.png) -60px 0 no-repeat;
                    }

                    input, select {
                        border: 2px solid #ff5025;
                    }
                }

                .error-required {
                    &:after {
                        background-position: -60px 0;
                    }
                }
            }

            .validation-icon {
                width: 30px;
                height: 30px;
                display: inline-block;
                margin-top: 2px;
                right: 0;
                top: 0;
                position: relative;
            }

            .callMeBackBtnContainer {
                margin-top: 10px;

                .callback-button {
                    -webkit-border-radius: 6px;
                    -moz-border-radius-: 6px;
                    border-radius: 6px;
                    -moz-box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, .1);
                    -webkit-box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, .1);
                    box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, .1);
                    background-color: $japanese-laurel;
                    background-image: -moz-linear-gradient(center top, $forest-green, $camarone);
                    background-image: -webkit-gradient(linear, 0% 0%, 0% 100%, from($forest-green), to($camarone));
                    background-image: -ms-linear-gradient(top, $forest-green 0%, $camarone 100%);
                    background-image: linear-gradient($forest-green, $camarone);
                    text-decoration: none;
                    font-size: 17px;
                    padding: 8px 15px;
                    font-family: arial;
                    font-weight: bold;
                    color: $white;
                    text-align: center;
                    display: inline-block;
                    border: none;
                    cursor: pointer;
                    line-height: 20px;
                    height: auto;
                    max-width: 100%;
                    vertical-align: top;

                    @media (min-width: $desktop) {
                        width: 258px;
                    }

                    &:focus, &:hover {
                        background-color: $forest-green;
                        background-image: -moz-linear-gradient(center top, $apple, $japanese-laurel-2);
                        background-image: -webkit-gradient(linear, 0% 0%, 0% 100%, from($apple), to($japanese-laurel-2));
                        background-image: -ms-linear-gradient(top, $apple 0%, $japanese-laurel-2 100%);
                        background-image: linear-gradient($apple, $japanese-laurel-2);
                        text-decoration: none;
                    }
                }
            }

            input, select {
                -webkit-border-radius: 4px;
                -moz-border-radius-: 4px;
                border-radius: 4px;
                -moz-box-sizing: border-box;
                -webkit-box-sizing: border-box;
                box-sizing: border-box;
                font-family: "SUN", "HelveticaNeue", "Helvetica Neue", Helvetica, "Lucida Grande", Arial, sans-serif;
                font-weight: bold;
                margin: 0 0 10px;
                vertical-align: top;
                border: 2px solid $silver;
                color: $storm-dust;
                font-size: 18px;
                padding: 5px 10px;
                max-width: 100%;
                -moz-box-shadow: none;
                -webkit-box-shadow: none;
                box-shadow: none;
                width: 65%;

                @media (min-width: $desktop) {
                    width: 258px;
                }
            }
        }

        .advisor-help,
        .customer-details,
        .termsPopup {
            -moz-box-sizing: border-box;
            -webkit-box-sizing: border-box;
            box-sizing: border-box;
            display: inline-block;
            vertical-align: top;
            font-size: 16px;
            letter-spacing: 0;
        }

        .advisor-help,
        .customer-details {
            float: left;
            width: 100%;

            @media (min-width: $desktop) {
                width: 48.5%;
            }
        }

        .advisor-help p {
            font-size: 24px;
            line-height: 1.1;
            margin-bottom: 25px;
        }

        .advisor-help.confirm {
            margin-right: 3%;
        }

        .customer-details {
            margin: 20px 0 0 0;

            @media (min-width: $desktop) {
                margin-left: 3%;
            }


            h4 {
                font-size: 18px;
                margin-bottom: 10px;
                padding-right: 30px;
            }

            label {
                width: 100%;
                display: block;
                font-size: 14px;
                padding-bottom: 2px;
            }
        }

        .advisor-help {

            .bulletpointed-list {
                -webkit-border-radius: 5px;
                -moz-border-radius-: 5px;
                border-radius: 5px;
                background-color: $catalina-blue;
                border: 1px solid $fun-blue;
                padding: 15px 20px;
                position: relative;

                ul {
                    border-top: 1px solid $fun-blue;

                    li {
                        padding-left: 30px;
                        text-indent: -30px;
                        padding-top: 4px;

                        &:before {
                            content: "";
                            background: url(../images/life-sprite.png) -33px -4px no-repeat;
                            width: 30px;
                            height: 25px;
                            display: inline-block;
                            position: relative;
                            top: 5px;
                        }
                    }
                }
            }
        }

        .callback-close {
            -webkit-border-radius: 50%;
            -moz-border-radius-: 50%;
            border-radius: 50%;
            background-color: $mischka;
            background-image: -moz-linear-gradient(center top, $white, $french-gray);
            background-image: -webkit-gradient(linear, 0% 0%, 0% 100%, from($white), to($french-gray));
            background-image: -ms-linear-gradient(top, $white 0%, $french-gray 100%);
            background-image: -ms-linear-gradient(top, $white 0%, $french-gray 100%);
            background-image: linear-gradient($white, $french-gray);
            text-decoration: none;
            -moz-box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, .1);
            -webkit-box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, .1);
            box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, .1);
            -moz-box-sizing: border-box;
            -webkit-box-sizing: border-box;
            box-sizing: border-box;
            border: none;
            font-size: 16px;
            position: absolute;
            right: -8px;
            font-weight: bold;
            height: 24px;
            width: 24px;
            top: -8px;
            line-height: 24px;
            padding-bottom: 1px;
            text-transform: lowercase;
            color: $primary-green;
            cursor: pointer;
        }
    }
}

.callback-footer {
    background-color: $japanese-laurel;
    background-image: -moz-linear-gradient(center top, $forest-green, $camarone);
    background-image: -webkit-gradient(linear, 0% 0%, 0% 100%, from($forest-green), to($camarone));
    background-image: -ms-linear-gradient(top, $forest-green 0%, $camarone 100%);
    background-image: linear-gradient($forest-green, $camarone);
    -moz-border-radius-bottomleft: 6px;
    -moz-border-radius-bottomright: 6px;
    -webkit-border-bottom-left-radius: 6px;
    -webkit-border-bottom-right-radius: 6px;
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    z-index: inherit;
    display: block;
    padding: 15px 30px 9px;
    overflow: auto;
    text-align: left;

    a {
        &:link, &:active, &:visited {
            color: $white;
        }
    }

    p {
        font-size: 12px;
        margin-bottom: 6px;
    }
}


// _callback-popup






.callback-form {

    li {
        clear: both;

        .datePicker {
            //margin-left: 38%;
            width: 240px;

            input {
                &.datepickertext {
                    float: left;
                    width: 55px;
                    margin-right: 0.5%;
                }
            }
        }
    }
}



li {
    &.validation-error {
        display: none;
        color: red;

        p {
            font-family: $font-sun3;
        }

        &.error-show {
            display: block;

            .error {
                background-color: white;
                padding: 5px 5px 5px 5px;
                text-align: center;
                width: 100%;
                border-radius: 6px;
                border: 1px solid red;
                background: #fff;
                background: -moz-linear-gradient(center top, #fff 0%, #dde2ed 100%);
                background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #fff), color-stop(100%, #dde2ed));
                background: -webkit-linear-gradient(top, #fff 0%, #dde2ed 100%);
                background: -o-linear-gradient(top, #fff 0%, #dde2ed 100%);
                background: -ms-linear-gradient(top, #fff 0%, #dde2ed 100%);
                background: linear-gradient(to bottom, #fff 0%, #dde2ed 100%);
            }
        }
    }
}

.businessEnergyCallbackIframe {
    display: none;
    height: 0;
}
