﻿.sib-wrapper {
    position: relative;

    &.has-meerkat-styling {
        @include has-border-flourish();

        .sib-container {
            @media(min-width: $mobile) {
                padding: 32px;
            }
        }

        .sib-panel-title {
            @include has-title-flourish();
            @include meerkat-font();
        }

        .sib-panel-button {
            @include meerkat-branded-button();
            font-family: "fjalla-one",Verdana,Arial,sans-serif;
        }

        .sib-content {
            text-align: center;
        }
    }
}

.sib-container {
    @include content-width();

    @media(min-width: $mobile) {
        align-items: center;
        display: flex;
    }
}

.sib-content {
    padding-top: 30px;
    padding-bottom: 30px;

    ol, ul {
        margin: 15px 0 15px 0;
    }

    li {
        list-style: disc;
        margin-left: 25px;
        font-size: 18px;
        padding: 5px 0 5px 0px;
    }

    table {
        width: 100%;
        border-collapse: collapse;

        tr {
            &:first-child {
                font-weight: 600;
            }

            td {
                padding: 5px;
            }
        }
    }

    @media(min-width: $mobile) {
        padding-right: 30px;
        width: 50%;
    }

    .sib-content-right & {
        order: 2;

        @media(min-width: $mobile) {
            padding-left: 30px;
            padding-right: 0;
        }
    }
}

.sib-support-content {
    padding-bottom: 30px;
    padding-top: 0;

    @media(min-width: $mobile) {
        align-self: flex-start;
        width: 50%;
    }

    .sib-vertical-bottom & {
        padding-bottom: 0;

        @media(min-width: $mobile) {
            align-self: flex-end;
            padding-top: 30px;
            padding-bottom: 0;
        }
    }

    .sib-vertical-center & {
        @media(min-width: $mobile) {
            align-self: center;
            padding-top: 30px;
        }
    }

    .sib-content-right & {
        margin: 0 auto;
        order: 1;
    }
}

.sib-img {
    display: block;
    margin: 0 auto;
    max-width: 100%;
}

.sib-panel-title {
    @include title();
    font-weight: 600;

    &.hide {
        display: none;
    }
}

.sib-copy {
    p {
        font-size: 18px;
        margin-bottom: 15px;
        letter-spacing: .3px
    }
}

.sib-second-button-container {
    margin: 30px 0;
}

.has-market-styling {
    .sib-panel-button {
        @include cta-button();
    }

    .sib-panel-inverse-button {
        @include cta-button(inverse);
    }

    .sib-panel-hollow-button {
        @include cta-button(hollow);
    }
}

.sib-panel-button {
    margin: 20px 0 0;
}

.sib-panel-inverse-button {
    margin: 20px 0 0;
}

.sib-panel-hollow-button {
    margin: 20px 0 0;
}

.sib-button-supporting-text {
    display: block;
    margin-top: 10px;
    font-size: 13px;
}

.sib-two-things-line {
    @include two-things-line();
}


    .sib-content .jta {

        &__container {
            @include clearfix();

            &--npb {
                padding-bottom: 0;
            }

            &--npt {
                padding-top: 0;
            }
        }

        &__text {
            font-size: 22px;
            line-height: 1.7rem;
        }

        &__intro {
            margin-bottom: 30px;
            max-width: 800px;
        }

        &__ctaStrapLink {
            margin-bottom: 10px;
        }

        &__left-column,
        &__right-column {
            width: 100%;
            display: block;
        }

        &__left-column {
            @media (min-width: $mobile) {
                float: left;
                clear: left;
            }
        }

        &__right-column {
            @media (min-width: $mobile) {
                float: right;
                clear: right;
            }
        }

        &__appButtons {
            margin-top: 10px;
            margin-left: 0;

            @media (min-width: $mobile) {
                margin-top: 35px;
            }
        }

        &__appButton {
            margin-left: 0px;
            font-size: 18px;
            padding: 15px 0;

            @media (min-width: $mobile) {
                display: inline-block;
            }
        }

        &__appButtonLink {
            display: block;
            line-height: 0;
        }

        &__appStoreImage {
            max-height: 50px;
        }
    }
