﻿.tib-container {
    @include has-border-flourish;

    &.has-border-flourish {
        position: relative;
        padding: 42px 32px;
    }

    padding-top: 50px;
    padding-bottom: 60px;
    padding-right: 40px;
    padding-left: 40px;
    background-color: $white;
    text-align: center;
}

.tib-content {
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;

    a {
        @extend .underlinedTextOnHover;
    }
}
.tib-title {
    @include title('small', 'center', $deep-ice);
    @include has-title-flourish;

    .has-meerkat-styling & {
        @include meerkat-font();
    }

    color: $deep-ice;
    font-weight: 700;
}
.tib-copy {
    color: $mine-shaft;
    font-size: 17px;
}