.car-makes {
	@media(max-width: $mobile) {
		padding-left: 10px;
		padding-right: 10px;
    }
    h1 {
    	line-height: 38px;
    }
	p {
		margin-top: 15px;
		&:first-child {
			margin-top: 0;
		}
	}

	.content-container {
		margin-top: 30px;
		@media(min-width: $mobile) {
			@include justify-blocks();
        }
	}

	.subMenu {
		display: none;
		@include border-radius(5px);
		background-color: $rough-sea;	
		@media(min-width: $mobile) {
			@include inline-block();
			width: 25%;
			text-align: left;
		}

		a {
			padding: 5px;
			margin-top: 10px;
			display: block;
			position: relative;
			color: $white;
			font-size: 17px;
			&:first-child {
				margin-top: 0;
			}
			&:hover {
				text-decoration: underline;
			}

			&.top {
				@include border-radius(5px, top);
			}

			&.selected {
				background-color: $brand-green;
			}

			&.last {
				@include border-radius(5px, bottom);
			}
		}
	}

	.thin-main {
		margin: 20px 0;
		@media(min-width: $mobile) {
			width: 46%;
			@include inline-block();
			text-align: left;
			margin: 0;
		}

		table {
			background-color: $rough-sea;
			margin-top: 20px;
			td {
				padding: 10px;
				//border: 1px solid $ravenclaw;
			}
		}
	}

	.widgets {
		@media(min-width: $mobile) {
			width: 23%;
			@include inline-block();
		}

		.widget {
			margin-top: 20px;
			@include border-radius(5px);
			background-color: $rough-sea;
			padding: 5px;
			@media(min-width: $mobile) {
				padding: 10px;
			}
			&:first-child {
				margin-top: 0;
			}

			&.ButtonList {
				text-align: center;
				.ctaButton {
					@include branded-button();
					font-size: 18px;
					margin-top: 10px;
					a {
						color: $white;
					}
				}
			}

			&.LinkList {
				text-align: left;
				a {
					font-size: 14px;
					color: $white;
					&:before {
						@include icon($caret-right, 14px, $white);
						margin-right: 8px;
					}

					&:hover {
						text-decoration: underline;
					}
				}
			}
			
		}

		.head {
			h2 {
				font-size: 22px;
			}
		}
	}
}