﻿.martha-testing {
    max-width: 1500px;
    margin: 0 auto;
}

html {
	background: $deep-ice !important;
    font: 62.5% sans-serif;
    color: $white;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    // reduce letter spacing to be closer to SUN to reduce FOUT
    letter-spacing: -0.1px;

    &.fonts-loaded {
        font-family: "SUN";
        letter-spacing: normal;
    }
}

/* this is to be removed, once the font sixes have all been refactored for font-size: 62.5% */
.rem-refactor {
    font-size: 16px;

    @media only screen and (min-width: $mobile) {
        font-size: 19px;
    }
}

*, *:before, *:after {
    box-sizing: inherit;
    -moz-box-sizing: inherit;
    -webkit-box-sizing: inherit;
}

body {
    line-height: 1.2rem;
    -webkit-font-smoothing: antialiased;
}

a {
	text-decoration: none;
	color: $brand-green;
}

.global-site-wrapper {
    position: relative;
    overflow: hidden;
}

.container {
    padding: 0 10px;
    margin: auto;
    max-width: $content-width;
}

.no-select {
    @include user-select(none);
}

img[src*="rs.gwallet.com"] {
    display: none;
}