.follow-us-title {
     font-size: 1rem;
     font-weight: 700;
     margin: 20px 0 10px;
}

.follow-us-container {
    box-sizing: content-box;
    -webkit-box-sizing: content-box;
    -moz-box-sizing: content-box;
    font-size: 0;

    ul {
    	list-style-type: none;

    	li {
		    display: inline;
		    position: relative;

			.follow-us-link {
			    display: inline-block;
			    margin: 0 3px;
			    font-size: 22px;
			    color: $white;
			    text-shadow: none;
			    text-align: center;
			    height: 45px;
			    width: 45px;
			    overflow: hidden;

			    &:before {
			        display: block;
			        width: 100%;
			        padding: 13px 0;
			    }
			}
		}
	}
}

.follow-us-link-fb {
    background-color: $chambray;

    &:before {
        @include icon($facebook, 22px, $white);
    }

    &:hover,
    &:focus {
        background-color: lighten($chambray, 7%);
    }
}

.follow-us-link-tw {
    background-color: $picton-blue;

    &:before {
        @include icon($twitter, 22px, $white);
    }

    &:hover,
    &:focus {
        background-color: lighten($picton-blue, 7%);
    }
