.follow-us-link-instagram {
    background-color: #D92E7F;
    background: radial-gradient(circle at 33% 100%, #FED373 4%, #F15245 30%, #D92E7F 62%, #9B36B7 85%, #515ECF);

    &:before {
        @include icon($z-instagram, 22px, $white);
    }

    &:hover,
    &:focus {
        background-color: lighten(#D92E7F, 7%);
        background: radial-gradient(circle at 33% 100%, lighten(#FED373, 7%) 4%, lighten(#F15245, 7%) 30%, lighten(#D92E7F, 7%) 62%, lighten(#9B36B7, 7%) 85%, lighten(#515ECF, 7%))
    }
}

.follow-us-link-li {
    background-color: $linkedinBlue;

    &:before {
        @include icon($linkedin, 22px, $white);
    }

    &:hover,
    &:focus {
        background-color: lighten($linkedinBlue, 7%);
    }
}
