.quote-block {
    background: $deep-ice;
    padding: 30px 0;
    margin: 0 0; 
    @include clearfix();

    @media(min-width: $tablet) {
        padding: 50px 0;
    }
}

.quote-block .panel{
    padding: 0 25px;
}

.quote-block-left {
    @media(min-width: $tablet) {
        float: left;
        width: 25%;
        margin-right: 5%;
    }

    p {
        text-align: center;
    }
}

.quote-block-author-image {
    display: block;
    max-width: 230px;
    height: auto;
    margin: 0 auto 25px;
    text-align: center;

    @media(min-width: $tablet) {
        max-width: 85%;
    }
}

.quote-block-author {
    font-weight: 700;
    font-size: 22px;
    margin: 0 auto 15px;
}

.quote-block-company {
    border-bottom: 1px solid #044e90;
    border-bottom: 1px solid rgba(0, 0, 0, 0.14);
    padding-bottom: 20px;

    @media(min-width: $tablet) {
        padding-bottom: 0;
        border: none;
    }
}

.quote-block-right {
    font-style: italic;
    margin-top: 20px;
    line-height: 1.6;
    text-align: center;

    @media(min-width: $tablet) {
        float: left;
        width: 70%;
        margin: 0;
        text-align: left;
    }
}
