.pc-obts {
	height: initial;
	@media(min-width: $mobile){
		height: 500px;
	}
}

.pc-obts-link-container {
	display: table;
	height: 100%;
	width: 100%;
}

.pc-obts-container {
	text-align: center;
	font-size: $paragraph-small;
	display: table-cell;
	vertical-align: middle;
	width: 100%;
	height: 100%;
	padding: 30px 0;
	overflow: hidden;
	position: relative;

	p {
		width: 50%;
		margin: 0 auto;
		font-size: $paragraph-small;
	}
}

.pc-obts-header-small, .pc-obts-container {
	color: $white;
	text-align: center;
}

.pc-obts-header-small {
	font-size: 1.8em;
    margin-bottom: 30px;
    position: relative;
    line-height: 1.1em;
    letter-spacing: -1px;
    &:before, &:after {
    	content: '';
	    position: absolute;
	    bottom: -15px;
	    margin-left: -35px;
	    left: 50%;
	    width: 70px;
	    height: 2px;
	    background-color: $white;
    }
}

.pc-obts-small, .pc-obts-large {
    background-size: cover !important;
    background-position: center center !important;
    background-repeat: no-repeat;
    width: 100%;
    display: block;
    height: initial;
    @media (min-width: $mobile) {
        width: 50%;
    }
}

.pc-obts-small {
    @media (min-width: $mobile) {
	    height: 50%;
	    float: right;
	    .pc-obts-large-on-right & {
	    	float: left;
	    }
    }
}

.pc-obts-large {
    @media (min-width: $mobile) {
	    height: 100%;
	    float: left;

	    .pc-obts-large-on-right & {
	    	float: right;
	    }
    }
}

.pc-obts-button {
    @include pc-new-button();
    background-image: none;
    text-transform: lowercase;
    position: relative;

    &.pc-obts-button-inverse{
        @include pc-new-button(inverse);
    }

	&.pc-obts-button-hollow{
		@include pc-new-button(hollow);
	}
}

.pc-obts-button::first-letter {
    text-transform: uppercase;
}

.pc-obts-play-button{
	@include pc-new-play-button();
	position: relative;
	margin: 20px auto 0;

    &.pc-obts-play-button-inverse{
        @include pc-new-play-button(inverse);
    }
}
