.main-navigation {
    @include clearfix;
    width: 100%;
    display: block;

    @media(min-width: $desktop) {
        display: block;
        width: $content-width;
        margin: 0 auto;
    }
}

#mainNavigation {
    position: relative;
    z-index: 2;

    @media(min-width: $touch-nav) {
        padding: 0 10px;
    }
    
    > li {
        position: relative; 
        text-align: left;

        @media(min-width: $touch-nav) {
            display: inline-block;
        }

        @media(min-width: $desktop) {
            margin-right: 7px;
        }

        .nav-icon {
            display: block;
            padding: 10px 11px;
            color: $white;
            font-size: 21px;
            font-size: 2.1rem;
                .rem-refactor & { @include fontsize(1.1); }
            line-height: 35px;
            text-decoration: none; 
            white-space: nowrap;
            font-weight: 700;
            background-color: $deep-ice;
            border-bottom: 2px solid #0E3E6B;
            box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.07);

            @media(min-width: $touch-nav) {
                background-color: transparent;
                border: 0;
                font-style: 19px;
                font-size: 1.9rem;
                    .rem-refactor & { @include fontsize(.75); }
                
                line-height: 1;
                box-shadow: none;
                text-shadow: #000 0px 1px 4px;
            }

            @media(min-width: $tablet) {
                font-size: 19px;
                font-size: 1.9rem;
                    .rem-refactor & { @include fontsize(.95); }
            }

            @media(min-width: $desktop) {
                .rem-refactor & { @include fontsize(1); }
            }

            &:before {
                width: 33px;
                text-align: center;

                display: inline-block;
                padding-right: 10px;
                position: relative;
                top: -1px;

                @media(min-width: $touch-nav) { 
                    text-align: left;
                    width: auto;
                }
            }

            &:active,
            &:focus {
                outline: 0;
                box-shadow: inset 0 0 2px 1px $brand-green;
                text-shadow: none;
                
                .ie8 & {
                    outline: 1px solid $brand-green;
                }
            }
        }
        
        &.hover,&:hover {
            @media(min-width: $touch-nav) {
                background: $link-water;
                border: none; 
                border-radius: 2px 2px 0 0;
            }

            > .nav-icon {
                text-shadow: none;
                cursor: pointer;
                
                @media(min-width: $touch-nav) {
                    color: $primary-blue;
                    border-radius: 2px 2px 0 0;
                }
            }
            > .nav-icon:before {
                @media(min-width: $touch-nav) {
                    color: $primary-blue;
                }
            }

            ul {
                list-style: none;

                @media(min-width: $touch-nav) {
                    display: block !important;
                    position: absolute; 
                    top: 100%; 
                    left: 0; 
                    z-index: 1;
                    background: $link-water; 
                    box-shadow: 3px 5px 8px -5px $black;
                }
            }
        }

        &:last-child {
            ul {
                left: inherit; 
                right: 0;
                border-radius: 2px 0 2px 2px;
                box-shadow: -3px 5px 8px -5px $black;
            }
        }

        ul {
            padding: 5px 14px;

            @media(min-width: $touch-nav) {
                display: none !important;
                position: absolute; 
                z-index: 3;
                clear: left;
                max-width: 230px;
                min-width: 200px;
                padding: 5px 10px;
                border-radius: 0 2px 2px;
            }
            
            li {
                margin: 4px 0;
                
                a {
                    display: block;
                    padding: 10px 15px; 
                    text-align: left; 
                    text-decoration: none; 
                    line-height: 20px; 
                    cursor: pointer;                    
                    border-radius: 2px;
                    color: $white;
                    font-size: 18px;
                    font-size: 1.8rem;
                        .rem-refactor & { @include fontsize(1.1); }
                    font-weight: 700;

                    @media(min-width: $touch-nav) {
                        color: $primary-blue;
                        padding: 5px;
                        font-size: 18px;
                        font-size: 1.8rem;
                            .rem-refactor & { @include fontsize(1); }
                    }
                    
                    &:active, 
                    &:focus {
                        outline: 0;
                        box-shadow: 0 0 2px 1px $brand-green;
                        
                        .ie8 & {
                            outline: 1px solid  $brand-green;
                        }               
                    }
                }

                &:hover {
                    a {
                        background: rgba(30, 63, 136, 0.2);
                    }
                }
            }
        }
    }

    $menuConfig: 
        yourvehiclesCategory $car, 
        yourhomeandpetsCategory $home, 
        yourfinancesCategory $credit-card-original,
        yourlifeCategory $life, 
        yourbusinessCategory $business, 
        yourtravelsCategory $plane;

    @each $menuConfigItem in $menuConfig {
        .#{nth($menuConfigItem, 1)} > .nav-icon:before {
            @include icon(nth($menuConfigItem, 2), 14px, $white);
        }     
    }
}

#mainNavigation {
    > li {
        &.hover {
            > .nav-icon {
                &:before {
                    color: $white;

                    @media(min-width: $touch-nav) {
                        color: $primary-blue;                        
                    }
                }
            }
        }
    }
}

.sub-nav {
    display: none;
    background-color: $deep-ice;

    @media(min-width: $touch-nav) {

    }
}

.navigation-toggle {
    display: table-cell;
    vertical-align: middle;
    width: 45px;
    text-align: center;

    @media(min-width: $touch-nav) {
        display: none;
    }
}

.lines {
    display: block;
    position: relative;
    width: 24px;
    height: 2px;
    margin: 0 auto;
    text-indent: -999em;
    background: $white;
    border-radius: 2px;
    transition: all 0.3s ease-in-out;
    
    &:before,
    &:after {
        background: $white;
        border-radius: 2px;
        width: 100%;
        transition: all 0.3s ease-in-out;
        height: 2px;
        display: block;
        position: absolute;
        content: "\00a0";
    }

    &:before {
        top: 6px;
    }

    &:after {
        top: -6px;
    }
}

.nav-animation .toggle-nav-close {
    .lines {
        background: transparent;
    }

    .lines:before,
    .lines:after {
        top: 0;
    }

    .lines:before {
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
    }

    .lines:after {
        -webkit-transform: rotate(-45deg);
        transform: rotate(-45deg);
    }
}

#ctm-navigation {
    display: none;

    @media(min-width: $touch-nav) {
        display: block !important;
        width: 100%;
    }
}

.hidden {
    display: none;
}

.navigation-toggle-text {
    color: $white;
    display: block;
    margin: 16px 0 0;
    text-transform: uppercase;
    font-size: 10px;
    line-height: 0;
    font-weight: 700;

    @media(min-width: $mobile) {
        font-size: 10px;
    }
}