﻿.hh-container {
    @include hero-container();
}

.hh-media {
    @include hero-media();
}

.hh-media-img {
    @include hero-media-img();
}

.hh-content-wrapper {
    @include hero-content-wrapper();
}

.hh-martha-legal-text {
    width: 200px;

    @media(min-width: $hero-medium) {
        position: relative;
        bottom: 25px;
        width: 300px;
    }
}

.hh-martha-lp-legal-text {
    @extend .hh-martha-legal-text;

    @media(max-width: $hero-medium) {
        position: relative;
        bottom: 10px;
    }
}

.hh-content-container {
    @include hero-content-container();

    .hh-container-cookie & {
        padding-bottom: $hero-info-bar-height-mobile;

        @media(min-width: $hero-small) {
            padding-bottom: $hero-info-bar-height-tablet;
        }

        @media(min-width: $hero-medium) {
            padding-bottom: $hero-info-bar-height-desktop;
        }
    }
}

.hh-content-align {
    @include hero-content-align();
}

.hh-proposition {
    @include hero-title();
    margin: 0;

    .hh-container-cookie & {
        padding: 20px 0;
    }
}

.hh-product-name-text-desktop {
    @include hero-proposition-text-desktop;  
    font-size:32px; 
}

.hh-product-name-text-mobile {
    @include hero-proposition-text-mobile;
    font-size: 25px;
    font-weight: bold;
}

hr.lph-product-name-desktop{
    border-top: 3px solid #fff;
    width: 17%;
    margin-top: 0;
    margin-bottom: 15px;
    @media(min-width: $hero-medium) {
            margin-bottom: 30px;
        };
}


.hh-proposition-text-mobile {
    @include hero-proposition-text-mobile;

}

.hh-proposition-text-desktop {
    @include hero-proposition-text-desktop;
    @include hero-h1-title;
}

.hh-proposition-image {
    @include hero-proposition-image;

    @media(max-width: $mobile-low) {
        margin-top: 160px;
    }
}

.hh-actions {
    .mkt-circular-icon-container {
        width: 65px;
        margin: 0 auto;

        @media(min-width: $hero-medium) {
            width: 90px;
        }
    }
}

.hh-actions-text {
    @include hero-sub-title;
    margin-top: 12px;

    .light-mode & {
        color: $brand-blue-dark;
    }
}

.hh-actions-ctas {
    max-width: 310px;
    margin: 0 auto;
    font-size: 0;

    @media(min-width: $hero-medium) {
        max-width: none;
    }
}

.hh-actions-cta {
    display: inline-block;
    width: 30%;
    vertical-align: top;
    font-size: 16px;

    @media(min-width: $hero-medium) {
        width: auto;
        padding: 0 20px;
    }

    .mkt-circular-icon-link {
        @include circular-icon-container();

        .light-mode & {
            .mkt-circular-icon-text {
                color: $brand-blue-dark;
                text-shadow: 0 1px 6px rgba(255,255,255,0.6)
            }
        }

        .mkt-circular-icon {
            background-color: $circular-icon-background-color-hover;

            &:before {
                color: $circular-icon-color-hover;
            }
        }

        &:hover {
            text-decoration: none;

            .mkt-circular-icon {
                background-color: $circular-icon-color-hover;

                &:before {
                    color: $circular-icon-background-color-hover;
                }
            }

            .mkt-circular-icon-text {
                text-decoration: underline;
                &.no-underline {
                    text-decoration:none;
                }
            }
        }
    }
}

.light-mode .hh-actions-cta .mkt-circular-icon:after {
    content: '';
    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    border: 3px solid $circular-icon-background-color-hover;
    border-radius: 50%;
}

