﻿/*
    Component colours
*/
$circular-icon-background-color: $white;
$circular-icon-background-color-hover: $brand-green;
$circular-icon-color: #0A4C8C;
$circular-icon-color-hover: $white;
$circular-icon-text-color: $white;

/*
    Component breakpoints
*/
$circular-icon-small: 450px;
$circular-icon-medium: 767px;


/*
    Component font sizes
*/
$circular-icon-fallback-font-size: 30px;

/* 
    Config map

    This map defines which icon should be used for which product
    
    It is intended to be used with a Sass loop that uses the first key (a string)
    as part of a CSS class name

    The second key is the variable name to be passed into the general `icon` mixin.

    The dynamic sizing of these icons is handled by JavaScript. There is a CSS `font-size`
    fallback, as a default size — in case JS doesn't load.
*/

$circular-icons-config: 
    car                                 $car,
    breakdown                           $breakdown, 
    van                                 $van,
    motorbike                           $motorbike,
    bike                                $motorbike,
    home                                $home,
    holiday-home                        $sunny-house,
    buildings                           $home,
    contents                            $home, 
    home-tel-and-broadband              $broadband,
    home-broadband                      $broadband,
    mobile-phones                       $mobile-phone,
    mobile-broadband                    $mobile-phone,
    digital-tv                          $tv,
    energy                              $energy,
    business-energy                     $energy,
    business-electricity                $energy,
    business-gas                        $fire,
    energy-has-bill                     $document,
    energy-no-bill                      $torn-document,
    travel                              $plane, 
    life                                $life,
    accidental-sickness-and-illness     $life,
    critical-illness                    $life,
    income-protection                   $savings,
