﻿$bubble-icon-colour: $brand-green;
$bubble-icon-colour-hover: $white;
$bubble-title: $brand-green;

.lph-container {
  @include hero-container();
  min-height: 420px;

  @media(min-width: $hero-medium) {
      max-height: none;
  }
}

.lph-media {
  @include hero-media();
}

.lph-media-img {
  @include hero-media-img();
}

.lph-content-wrapper {
  @include hero-content-wrapper();
  display: table;
}

.lph-content-container {
  @include hero-content-container();
}

.lph-content-align {
  @include hero-content-align();
}

.lph-proposition {
  @include hero-title();
  margin: 0;

  .lph-container-cookie & {
    padding: 20px 0; 
  }
}

.lph-proposition-text-mobile {
  @include hero-proposition-text-mobile;
}

.lph-proposition-text-desktop {
  @include hero-proposition-text-desktop;
  @include hero-h1-title;
}

.lph-proposition-image {
  @include hero-proposition-image;
}

.lph-actions-text {
  @include hero-sub-title;
  margin-top: 12px;
}

.lph-post-cta-text {
  @include hero-sub-title;
  margin-top: 25px;
}

.lph-actions-ctas {
  font-size: 0;
}

.lph-actions {
  padding: 0 50px;

  @media(min-width: $hero-medium) {
    padding: 0;
  }

  .mkt-circular-icon-container {
    width: 90px;
    margin: 0 auto;
  }

  &-title {
    max-width: 180px;
    margin-left: auto;
    margin-right: auto;
  }

  &-title-large {
    max-width: 120px;
    margin-left: auto;
    margin-right: auto;
    font-size: 18px;
  }
}

.lph-telephone-container {
  margin-top: 15px;
}

.lph-telephone-number {
  font-weight: 700;
  font-size: 20px;
  margin-top: 5px;
  @media (min-width: $hero-medium) {
    margin-top: 15px;
    font-size: 30px;
  }
}

.lph-info-bar-wrapper {
  @include hero-info-bar-wrapper;
}

.lph-info-bar-container {
  @include hero-info-bar-container; 
}

.lph-info-bar-personalisation {
}

.lph-info-bar-container {
  @include hero-info-bar-container;
}

.lph-info-bar-content {
  @include hero-info-bar-content;
}

.lph-info-bar-personalisation {
  @include hero-info-bar-personalisation;
}

.lph-info-bar-icon-bubble {
  @include hero-info-bar-icon-bubble;
}

.lph-info-bar-icon {
}

.lph-info-bar-welcome {
  @include hero-info-bar-welcome;
}

.lph-info-bar-welcome-text {
  @include hero-info-bar-sub-text;
}

.lph-info-bar-welcome-name {
  @include hero-info-bar-main-text;
}

.lph-info-bar-returner-container {
  @include hero-info-bar-returner-container;
}

.lph-info-bar-returner-sub {
  @include hero-info-bar-sub-text;
    @media (min-width: $hero-small) {
      display: none;
    }
}

.lph-info-bar-returner-main {
  @include hero-info-bar-main-text; 
}

.lph-info-bar-cta-container {
  @include hero-info-bar-cta-container;
}

.lph-info-bar-cta { 
  @include hero-info-bar-cta;
}

.lph-info-bar-cta-align {
  @include hero-info-bar-cta-align;
}

.lph-info-bar-cta-secondary {
  @include hero-info-bar-cta-secondary;
}

.lph-legal-text {
    padding-top: 25px;
    font-size: small;
    color: lightgray;
}
