$title-colour: $deep-ice;
$background-colour: #D4DFE7;
$bubble-icon-colour: $brand-green;
$bubble-icon-colour-hover: $white;
$bubble-title: $deep-ice;

.lsc-container {
	text-align: center;
	padding: 30px 20px;
	max-width: 980px;
	margin: 0 auto;

	@media(min-width: $touch-nav) {
		padding: 50px 20px;
	}

	@media(min-width: $desktop) {
		// padding: 50px 20px;
	}
}

.lsc-title {
	@include title(small, center);
	margin-bottom: 30px;
	color: $title-colour;
	font-size: 24px;
	&:after {
		background-color: $title-colour;
	}

	@media(min-width: $mobile) {
		margin-bottom: 50px;
		font-size: 28px;
	}
}

.lsc-copy {
	margin-bottom: 20px;
	font-size: 14px;
	line-height: 1.3;
	color: $bubble-title;

	@media(min-width: $touch-nav) {
		margin-bottom: 30px;
	}
}

.lsc-categories {
	font-size: 0;
    margin-top: -20px;
    @media(min-width: $desktop) {
        margin-top: -60px;		
	}
}

.lsc-category {
	display: inline-block;
	vertical-align: top;
	text-align: center;
	color: $bubble-background-colour;
	width: 25%;
	margin: 20px 4%;

	@media(min-width: $mobile) {
		width: 100px;
	}

	@media(min-width: $touch-nav) {
		margin-left: 5%;
		margin-right: 5%;
	}

	@media(min-width: $desktop) {
		margin: 30px 4.5%;
		width: 135px;
	}
}

.lsc-category {
	.mkt-circular-icon-text {
		display: block;
		font-size: 14px;
		text-decoration: none;
		color: $bubble-title;
		max-width: 100px;
		margin: 0 auto;
		font-weight: 700;
		text-shadow: none;
		margin-top: 10px;

		@media(min-width: $touch-nav) {
			font-size: 16px;
			max-width: none;
		}

		&:hover {
			text-decoration: underline;
		}
	}
}

.lsc-link-text {
	line-height: 1.2;
}


/* Lets Start Comparing Block styles when Hero Home Block is on the page (eg Home page) */

// hide the spinner on standard lets start comparing blocks
.lsc-container .loading-spinner {
    display: none;

    // display on the home page as it's appears under the hero block
    .home & {
        display: block;

        // hide when the below data attribute has been added by the hero block returner-panel js
        [data-customer-view] & {
            display: none;
        }
    }
}

.home {

    // hide all ctas when on home page as it appears under the hero block
    .lsc-category {
        display: none;

        // display the ctas when the below data attribute has been added by the hero block returner-panel js
        [data-customer-view] & {
            display: inline-block;
        }

        // don't display the first 3 items when you have no quote as they will be in the hero block
        [data-customer-view='no-quote'] &:nth-child(-n + 3) {
            display: none;
        }

        // don't display the last 3 links when you are a returner as you want home, 
        [data-customer-view$='-returner'] &:nth-last-child(-n + 3) {
            display: none;      
        }
    }

}


    