﻿@mixin circular-icon-container($is-a-link: true) {
  @if ($is-a-link) {
    display: block;
    color: $circular-icon-text-color;

    &:hover {
      text-decoration: underline;
      cursor: pointer;
    }
  }
}

@mixin circular-icon() {
  position: relative;
  display: block;
  height: auto;
  padding-top: 100%;
  margin-left: auto;
  margin-right: auto;
  font-size: $circular-icon-fallback-font-size;
  text-align: center;
  color: $circular-icon-text-color;
  background-color: $circular-icon-background-color;
  border-radius: 50%;

  &:before {
    display: block;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    position: absolute;
    top: 50%;

    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);

    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}


@mixin circular-large-icon() {
  @include circular-icon(); 

  &:before {
    top: 34%;
  }
}

@mixin circular-icons-text($text-shadow: true) {
  display: block;
  margin-top: 10px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.05;

  @media(min-width: $circular-icon-medium) {
    font-size: 18px;
  }

  @if ($text-shadow) {
    text-shadow: 0 1px 6px rgba(0, 0, 0, 0.6);
  }
}