﻿.gbs-text-block
{
    @include clearfix();
    border-radius: 5px;
    max-width: 980px;
    padding: 1em;

    @media(min-width: $mobile-low) {
        padding: 1em 1em 0;
    }

    .title-block-wrapper {
        width: 100%;

        @media(min-width: $mobile-low) {
            display: block;
        }
    }

    &.align-right {
        @media(min-width: $mobile-low) {
          .title-wrapper {
              float: left;
          }
      }

      .image-wrapper {
          @media(min-width: $mobile-low) {
              padding: 0px 0px 1em 20px;
              float: right;
          }
      }

    }

    .title-wrapper
    {
        @media(min-width: $mobile-low) {
            float: right;
            width: 75%;
        }

        .title {
            @include intertia-title();
            @include font-size(1.5);
            margin-bottom: 20px;

            @media(min-width: $mobile-low) {
                margin-top: 0px;
                text-align: left;
            }
        }
    }

    .image {
        max-width: 100%;
        height: auto;
    }

    .image-wrapper {
        text-align: center;

        @media(min-width: $mobile-low) {
            display: block;
            float: left;
            width: 25%;
            padding: 0 20px 1em 0;
        }
    }

    .content-wrapper {
        @include font-size(0.9);
        line-height: 1.2;

        ul, li {
            list-style: disc;
        }

        li {
            margin-left: 1em;
        }

        p, ul {
            margin-bottom: 10px;
        }
    }

    .subtitle {
        @include font-size(1.2);
        line-height: 1.5rem;
        margin-bottom: 20px;
    }

    .button-wrapper {
        text-align: center;
        padding-bottom: 1em;
    }

    .button {
        @include branded-button();
        display: inline-block;
        margin-top: 10px;
    }
}
