.bv-cleanslate *[class*="bv-core-container-"] .bv-section-summary .bv-content-title, 
.bv-cleanslate *[class*="bv-core-container-"] .bv-control-bar,
.bv-cleanslate *[class*="bv-core-container-"] .bv-content-pagination {
	background-color: #1a3c7f !important;
	background-image: -moz-linear-gradient(center top, #1a3c7f, #000034) !important;
	background-image: -webkit-gradient(linear, 0% 0%, 0% 100%, from(#1a3c7f), to(#000034)) !important;
	background-image: -ms-linear-gradient(top, #1a3c7f 0%, #000034 100%) !important;
	background-image: linear-gradient(#1a3c7f, #000034) !important;
}

.bv-cleanslate *[class*="bv-core-container-"] .bv-control-bar, 
.bv-cleanslate *[class*="bv-core-container-"] .bv-content-pagination {
	border-radius: 5px !important;
	border: none !important;
}

.bv-cleanslate *[class*="bv-core-container-"] .bv-dropdown-container .bv-dropdown ul {
	background-color: #000034 !important;
	color: white!important;
}

.bv-cleanslate *[class*="bv-core-container-"] .bv-dropdown-container .bv-dropdown ul .bv-dropdown-item{
	background-color: #000034 !important;
	color: white!important;
	&:hover {
		color: white!important;
		background-color: #1a3c7f !important;
	}
}

.bv-cleanslate *[class*="bv-core-container-"] .bv-dropdown-container .bv-dropdown ul .bv-dropdown-item-selected{
	color: white!important;
	background-color: #1a3c7f !important;
}

.bv-cleanslate *[class*="bv-core-container-"] .bv-content-secondary-ratings-value {
	background-color: #189507!important;
}
.bv-cleanslate *[class*="bv-core-container-"] .bv-content-secondary-ratings-container {
	background-color: #1a3680!important;	
}

.bv-cleanslate *[class*="bv-core-container-"] .bv-content-details-offset-on {
	//width: 100%!important;
}

.bv-cleanslate *[class*="bv-core-container-"] .bv-content-summary p {
	font-size: 17px!important;
	line-height: 21px!important;
}

.bv-cleanslate *[class*="bv-core-container-"] .bv-content-review {
	border-radius: 0!important;
	border-top: 1px solid #334461!important;
	border-bottom: 1px solid #517ECF!important;
}

.bv-cleanslate *[class*="bv-core-container-"] .bv-content-review:first-of-type {
	border-top: none!important;
}

.bv-cleanslate *[class*="bv-core-container-"] .bv-content-review:last-of-type {
	border-bottom: none!important;
}

.bv-cleanslate *[class*="bv-core-container-"] .bv-content-details-container {
	margin: -23px 0 10px 0!important;
}

.bv-cleanslate *[class*="bv-core-container-"] .bv-content-item .bv-content-secondary-ratings dt {
	margin-bottom: 0!important;
}

.bv-cleanslate *[class*="bv-core-container-"] .bv-content-item .bv-content-secondary-ratings dd {
	padding-bottom: 0.5em !important;
}

.bv-cleanslate *[class*="bv-core-container-"] .bv-action-bar {
	display: none!important;
}