﻿.title-and-text-title {
    margin-bottom: 1em;
    @include fontsize(1.4);
    line-height: 1;

    @media(min-width: $mobile) {
        @include fontsize(1.3);
    }
}

.title-and-text-text {
    @include fontsize(1.2);

    li {
        margin-bottom: .5em;
    }

    a {
        text-decoration: underline;

        &:hover {
            color: $brand-green-hover;
        }
    }

    @media(min-width: $mobile) {
        @include fontsize(1);
    }
}