﻿$pulse-color: rgba(253,253,255,.56);
$lightbox-collapse: 765px;
@keyframes pulse {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.3);
    }
    100% {
        transform: scale(1);
    }
}
.ilb-container {
    position: relative;
    display: none;
}
.ilb-image-wrapper {
    position: relative;
    max-width: 100%;
    margin: 0 auto;
    display: block;
    &.ilb-ContentWidth {
        @media(min-width: $touch-nav) {
            padding: 15px;
        }
    }
}
.ilb-image {
    width: 100%;
    height: auto;
    position: relative;
    display: block;
}
.ilb-fullWidth {
    width: 100%;
}
.ilb-ContentWidth {
    width: 980px;
}
.ilb-content {
    position: relative;
    max-width: 100%;
    margin: 0 auto;
    display: block;
    padding: 10px;
    @media (min-width: $mobile) {
        max-width: 980px;
        padding: 10px;
    }
}
.ilb-hotspot {
    position: absolute;
}
.ilb-pulse {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 30px;
    height: 30px;
    margin: -15px 0 0 -15px;
    cursor: pointer;
    z-index: 10;
    will-change: transform;
    animation-fill-mode: both;
    animation-iteration-count: infinite;
    animation-name: pulse;
    animation-duration: 1s;
    transition: all .2s ease-in-out;
    -webkit-transform: translateZ(0);
    @media (min-width: $mobile) {
        width: 40px;
        height: 40px;
        margin: -20px 0 0 -20px;
    }
    &:hover {
        transform: scale(1.3);
        transition: all .5s ease-in-out;
    }
    &:focus {
        outline: 0;
        transform: scale(1.1);
        animation: 0;
        &:after {
            animation: none;
        }
    }
}
.ilb-lightbox-container {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 10;
}
.ilb-lightbox {
    display: table;
    height: 100%;
    width: 100%;
}
.ilb-lightbox-align {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 90%;
    max-width: 980px;
    height: 80%;
    padding: 40px 2% 2% 2%;
    @media(min-width: $lightbox-collapse) {
         border-radius: 5px;
    }
}
.ilb-lightbox-content {
    position: relative;
    
    height: 100%;
    margin: auto;
    text-align: center;
    color: #004A8E;
    text-align: left;
    overflow-y: auto;
    overflow-x: hidden;
}
.ilb-close-lightbox {
    position: absolute;
    width: 16px;
    height: 16px;
    cursor: pointer;
    background-repeat: no-repeat;
    background-image: url('../../responsive2015/images/icons/close.png');
    background-size: cover;
    top: 10px;
    right: 4%;
    display: none;

    

    @media(min-width: $lightbox-collapse) {
      &.nonresponsive-view {
        right: 1%;
        display:    block; 
      }
    }

    @media(max-width: $lightbox-collapse) {
      &.nonresponsive-view {
        display:    none; 
      }
    }


    @media(max-width: $lightbox-collapse) {
        display: block;
    }
}
.ilb-lightbox-content-title {
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 20px;
    padding: 0 35px 4% 4%;
}
.ilb-lightbox-mainContent,
.ilb-lightbox-contentImages {
    position: relative;
    display: table-cell;
    table-layout: fixed;
    vertical-align: top;
}
.ilb-lightbox-mainContent {
    width: 60%;
    @media(max-width: $lightbox-collapse) {
        width: 100%;
    }
}
.ilb-lightbox-contentImages {
    width: 40%;
    padding: 0 10px 0 20px;
    img {
        position: relative;
        display: block;
        width: 100%;
        margin: 0 0 20px;
        @media(max-width: $lightbox-collapse) {
            display: none;
        }
    }
}
.ilb-lightbox-mainContentWrapper {
    position: relative;
    padding: 0 0 4% 4%;
}

/* when second column is enabled */

.ilb-lightbox-align.useSecondColumn {

    .ilb-lightbox-content {
        display: block;
        float: left;
        width: 50%;

        @media(max-width: $lightbox-collapse) {
            float: none;
            width: 100%;
            overflow: visible;
            margin-bottom: 40px;
            height: auto;
            clear: both;
        }

    }

    
     @media(max-width: $lightbox-collapse) {
            overflow-y: auto;
            padding-right: 40px;
        }
}