﻿.pc-cta {
    position: relative;
    background-color: $rough-sea;
    padding: 50px 0;

    &.pc-cta-border::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: calc(100% - 30px);
        height: calc(100% - 30px);
        margin: 15px;
        border: 25px solid transparent;
        border-image: url("data:image/svg+xml;charset=utf8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22282.7%22%20height%3D%22282.7%22%3E%3Cpath%20fill%3D%22%239b8647%22%20d%3D%22M204%20282.7H79v-6c0-40.2-32.8-73-73-73H0V79h6C46.2%2079%2079%2046%2079%206V0h124.8v6c0%2040.2%2032.7%2073%2073%2073h6v125h-6c-40.2%200-73%2032.6-73%2072.7v6zm-113.3-12H192c3-42%2036.7-75.6%2078.7-78.6V90.8C228.7%2087.7%20195%2054%20192%2012H90.7C87.7%2054%2054%2087.7%2012%2090.7V192c42%203%2075.7%2036.7%2078.7%2078.7z%22%2F%3E%3C%2Fsvg%3E%0A") 40% 40% repeat;
    }

    .pc-cta-container {
        position: relative;
        width: 50%;
        margin: 0 auto;
        z-index: 2;

        p {
            text-align: center;
        }

        .pc-button {
            margin-top: 30px;
        }
        @media(max-width: $touch-nav){
            width: 100%;
            padding-left: 20px;
            padding-right: 20px;
        }
    }
}
