$media-centre-mobile-breakpoint: 986px;
$media-centre-smaller-breakpoint: 900px;
$overlay-text-color: $white;
$overlay-background: $black;
$hero-item-body: $white;
$hero-item-body-desktop: $jim-bean;
$hero-item-color-iefix: #192b59\9;
$hero-media-find-out-more: $that-good-good;
$media-item: $jim-bean;
$slick-colors: $frosty-jacks;
$slick-disabled: $white;

.media-centre.latest-news {
    .clearfix { clear: both; }
    font-family: $font-sun;

    .hero-media-item {
        width: 100%;
        height: auto;
        overflow: hidden;
        border-radius: 5px;
        margin-bottom: 1em;
        margin-top: 1em;
        position: relative;
        
        @media(min-width: $media-centre-mobile-breakpoint) {  
            margin-top: 0;
            border-radius: 0px 0px 5px 5px;
        }
         
        .hero-media-item-background {
            height: auto;

            img
            {
                height: 100%;
                
                @media(min-width: $media-centre-smaller-breakpoint) {  
                    height: 100%;
                    width: 100%;
                }

            }
            .overlay { 
                background: $overlay-background;
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                bottom: 0;
                right: 0;
                left: 0;
                color: $overlay-text-color;
                @include opacity(0.6);

                @media(min-width: $media-centre-mobile-breakpoint) {
                    display: none;
                }
            }
        }

        .hero-media-item-body {
            position: absolute;
            top: 1em;
            left: 2em;
            width: auto;
            color: $hero-item-body;
            
            @media(min-width: $media-centre-mobile-breakpoint) {  
                width: 55%;
                color: $hero-item-body-desktop !important;
            }
        }

        .hero-media-item-title {
            font-size: 1.4em;
            line-height: 1.2em;
            margin-bottom: 0.3em;
            font-weight: bold;
            width: 70%;
            color: $hero-item-color-iefix !important; /* IE 8-9 */

            @media(min-width: $media-centre-mobile-breakpoint) {  
                font-size: 2.2em;
                width: 100%;
            }
        }

        .hero-media-item-text {
            width: 72%;
            font-size: 1.2em;
            line-height: 1.3em;
            max-height: 215px;
            overflow: hidden;
            color: $hero-item-color-iefix !important; /* IE 8-9 */

            @media(min-width: $mobile) {
                width: 62%;
                max-height: 208px;
            }

            @media(min-width: $media-centre-mobile-breakpoint){
                width: 72%;
                max-height: 200px;
            }
        }

        .hero-read-more {
            color: $hero-media-find-out-more;
            font-weight: 700;
            position: absolute;
            bottom: 20px;
            left: 30px;
            font-size: 1.2em;
            position: absolute;
            bottom: 1.5em;
            left: 1.7em;

            &:hover { text-decoration: underline; }

            @media(min-width: $media-centre-mobile-breakpoint)
            {
                text-shadow: none;
            }
        }
    }

    .media-items {
        width: 100%;
        overflow: hidden;
        width: 990px\9 !important; /* IE 8-9 */

        @media(min-width: $media-centre-mobile-breakpoint) {
            @include flexbox();
            width: 102%;
        }

         
        .media-item {
            float: left;
            background: $media-item;
            border-radius: 5px;
            padding: 1.3em;
            height: 300px;
            margin-right: 1em;
            margin-left: 0.4em;
            max-width: 298px;
            
            
            @media(min-width: $media-centre-mobile-breakpoint) {
                @include flex(1,310px);
                width: 310px;
                margin-left: 0;
            }

            .media-item-title {
                font-size: 1.3em;
                margin-bottom: 0.3em;
                font-weight: bold;
            }
            .media-item-body {
                margin-bottom: 1em;
                font-family: $font-sun;
                font-size: 0.8em;
                height: 183px;
                overflow: hidden;

                p {
                    margin-top: 0.7em;
                    margin-bottom: 0.7em;
                }

            }

            .media-item-footer {
                height: 65px;
            }

            .media-centre-read-more {
                color: $hero-media-find-out-more;
                font-weight: bold;
                display: block;
                
                &:hover { 
                    text-decoration: underline; 
                }

                &:after {
                   /*  content: $chevron-right;
                    font-family: $font-icons; */
                    // font-size: 0.7em;
                    // font-weight: normal;
                    padding-left: 0.6em;
                    @include icon($chevron-right, 0.7em);
                }
            }
            .media-item-details {
                margin-top: 1em;
                font-size: 0.8em;
                font-weight: bold;
            }
        }
    }
    .media-item-controls {
        display: block;
        margin: 0 auto;
        width: 180px;
        margin-top: -36px;
        z-index: 5;
        position: relative;
        
        @media(min-width: $media-centre-mobile-breakpoint) {
            display: none;
        }
        
        display: block\9 !important; /* IE 8-9 */
        
        .media-item-control-right {
            text-align: center;
            position: absolute;
            right: 0;
            padding: 15px;
            padding-left: 25px;
            padding-right: 25px;

            &:after {
                /* font-family: $font-icons;
                content: $chevron-right; */
                cursor: pointer;
                @include icon($chevron-right);
            }
        }
        .media-item-control-left {
            text-align: center;
            position: absolute;
            left: 0;
            padding: 15px;
            padding-left: 25px;
            padding-right: 25px;

            &:after {
                /* font-family: $font-icons;
                content: $chevron-left; */
                cursor: pointer;
                @include icon($chevron-left);
            }
        }
        .slick-disabled {
            color: $slick-colors;

            &:after {
                cursor: default;
            }
        }
    }

    .slick-dots {
        width: 90px;
        text-align: center;
        margin: 0 auto;
        margin-top: 20px;

        li {
            display: inline-block;
            background: $slick-colors;
            width: 10px;
            height: 10px;
            margin-right: 5px;
            margin-left: 5px;
            border-radius: 10px;

            button { 
                display: none; 
            }

            &.slick-active { 
                background: $slick-disabled; 
            }
        }
    }

    .view-all-latest-news {
        padding: 1em;
        padding-top: 2em;
        text-align: center;
        font-size: 1.3em;
        color: $hero-media-find-out-more;
        text-shadow: 0px 1px 4px rgba(0,0,0,0.7);
        text-decoration: underline;

        @media(min-width: $media-centre-mobile-breakpoint) {
            padding-top: 1em;
        }
    }
}