﻿.faq-page {
    #content-container {
        #faq-overlay {
            opacity: 0.75;
            background-color: #000;
            top: -100px;
            left: -100px;
            right: -100px;
            bottom: -100px;
            display: none;
            z-index: 99999;
            position: fixed;
        }

        .customer-service-heading, .customer-service-container{
            max-width: 905px;
            margin: auto;
        }


        h1.customer-service-heading{
            background-color: #0E1E47;
            padding: 20px;
            border-radius: 8px 8px 0px 0px;
            @include fontsize(3.1);
        }

        .customer-service-container{
            background-color: #24307f;
            padding:20px;
            @media screen and (min-width: $mobile) {
                position: relative;
                margin-bottom: 20px;
            }
             .categories {
                @media screen and (min-width: $mobile) {
                    width: 60%;
                }
                p {
                    color: $secondary-light-green;
                    border-bottom: 1px solid #1E488C;
                    display: block;
                    margin-bottom: 5px;
                    cursor: pointer;
                    padding-bottom: 5px;

                    &:hover {
                                text-decoration: underline;
                            }

                    &.active {
                        text-decoration: underline;
                    }
                }
                    
            }
                        
            #faqSearchText {
                width: 100%;
                height: 45px;
                margin: 5px 0;
                border-radius: 7px;
                border: 2px solid #BBBBBB;
                color: #777777;
                font-weight: 700;
                @include fontsize(1.6);
                padding: 0px 4px 2px;
                @media screen and (min-width: $mobile) {
                    width: 74%;
                    float: left;
                    margin-right: 1%;
                }

            } 
            #faqSearch{
                // @extend .button-green-large;
                @include branded-button;
                width: 100%;

                @media screen and (min-width: $mobile) {
                    width: 25%;
                }
            }
            #faq{
                &:before {
                    content: "";
                    display: block;
                    background-image: url("../../assets/img/icons/green-loader.gif");
                    background-repeat: no-repeat;
                    background-position: 50% 40%;
                    height: 80px;
                    width: 100%;
                    position: absolute;            
                }
                min-height: 80px;
                position: relative;
                margin-bottom: 20px;

                .KnowledgeSyndication {
                    background-color: #24307f;
                    position: relative;
                }

                .rn_SearchArea {
                    opacity: 0;
                    height: 0;
                    overflow: hidden;
                }

                .rn_Content{
                    margin-top:15px;
                }

                a{
                    text-decoration: none;
                    @include fontsize(1.5);
                    line-height: 1.25rem;
                }
                .rn_Description {
                    @include fontsize(1.4);
                    line-height: 1.25rem;
                }

                ul{
                    padding: 0px;
                    min-height: 80px;

                    li{
                        margin-bottom: 10px;
                        list-style: none;
                    }
                }


            }
            @media screen and (min-width: $mobile) {
                .contact-us-mobile{
                    display: block;
                    border: 1px solid #1E488C;
                    border-radius: 5px;
                    background-color: #1A3680;                
                    position: absolute;
                    top: 85px;
                    right: 20px;
                    height: 228px;
                    width: 35%;                

                    h2{
                        @include fontsize(2.4);
                        font-weight: normal;
                        padding: 10px;
                        border-bottom: 1px solid #1E488C;
                        margin-top: 5px;

                        @media screen and (min-width: $mobile) {
                            border: 0;
                            margin-top: 0;
                            margin-bottom: 0;
                            padding-bottom: 0;
                        }
                    }

                    div {           
                        padding: 10px;

                        a {
                            display: block;
                            padding: 3px 0;
                        }
                    }
                }  
                .contact-us-desktop {
                    display: none;
                }
            }

            @media screen and (min-width: $tablet) {
                .contact-us-desktop{
                    display: block;
                    border: 1px solid #1E488C;
                    border-radius: 5px;
                    background-color: #1A3680;                
                    position: absolute;
                    top: 85px;
                    right: 20px;
                    height: 228px;
                    width: 35%;                

                    h2{
                        @include fontsize(2.4);
                        font-weight: normal;
                        padding: 10px;
                        border-bottom: 1px solid #1E488C;
                        margin-top: 5px;

                        @media screen and (min-width: $mobile) {
                            border: 0;
                            margin-top: 0;
                            margin-bottom: 0;
                            padding-bottom: 0;
                        }
                    }

                    div {           
                        padding: 10px;

                        a {
                            display: block;
                            padding: 3px 0;
                        }
                    }
                }  
                .contact-us-mobile {
                    display: none;
                }
            }
        }
    }

    

    iframe {
        display: none;
    }

    .rn_AnswerOverlay{
        top: 5%;
        left: 5%;
        width: 90%;
        height: 90%;
        margin: 0px;
        background-color: #24307f;
        color: $white;
        border: none;
        box-shadow: none;
        border: 1px solid #3D619D;
        border-radius: 5px;
        z-index: 100000;

        @media screen and (min-width: $mobile) {
            left: 0;
            right: 0;
            width: 500px;
            margin: auto;
            padding: 20px;
            height: auto;
        }

        .rn_AnswerInfo{
            display:none;
        }

        .rn_AnswerSummary{
            line-height: 1em;
            margin-bottom: 20px;
        }

        .rn_AnswerSolution{
            p {
                margin-bottom: 10px;
                span {
                    font-weight: 700;
                }
            }
        }

        .rn_CloseOverlay{
            outline: none;
            white-space: normal;
            background-color: transparent;
            height: 23px;
            width: 24px;
            background-image: url('../../assets/img/icons/lightbox_close.png');
            background-position: 0 0;
            background-repeat: no-repeat;
            top: 2px;
            right: 2px;
        }
    }
}