$track-color: #96d491;
$thumb-color: $primary-blue;

$thumb-radius: 50%;
$thumb-height: 30px;
$thumb-width: 30px;
$thumb-shadow-size: 0px;
$thumb-shadow-blur: 0px;
$thumb-shadow-color: #111;
$thumb-border-width: 1px;
$thumb-border-color: #fff;

$track-width: 100%;
$track-height: 15px;
$track-shadow-size: 0px;
$track-shadow-blur: 0px;
$track-shadow-color: #222;
$track-border-width: 1px;
$track-border-color: #fff;

$track-radius: 5px;
$contrast: 5%;

@mixin shadow($shadow-size, $shadow-blur, $shadow-color) {
  box-shadow: $shadow-size $shadow-size $shadow-blur $shadow-color, 0 0 $shadow-size lighten($shadow-color, 5%);
}

@mixin track() {
  width: $track-width;
  height: $track-height;
  cursor: pointer;
  transition: all .2s ease;
}

@mixin thumb() {
  @include shadow($thumb-shadow-size, $thumb-shadow-blur, $thumb-shadow-color);
  border: $thumb-border-width solid $thumb-border-color;
  height: $thumb-height;
  width: $thumb-width;
  border-radius: $thumb-radius;
  background: $thumb-color;
  cursor: pointer;
  z-index: 2;
}

.lig-results {
 
    .lig-results-choose {
        display: block;
        position: relative;
        font-family: SUN;
        font-size: 18px;
        font-weight: 600;
        color: $primary-blue;
        text-align: center;

        @media (max-width: $touch-nav) {
            font-size: 14px;
        }

        &:before,
        &:after {
            position: absolute;
            top: 0;
            font-family: SUN;
            font-size: 18px;
            font-weight: 600;
            color: darken(#e6ebf2, 30%);

            @media (max-width: $touch-nav) {
                font-size: 14px;
            }
        }
            
        &:before {
            content: '18';
            left: 0;
        }

        &:after {
            content: '70';
            right: 0;
        }
    }

    [type=range] {
        display: block;
        position: relative;
        -webkit-appearance: none;
        margin: 15px auto 30px;
        width: $track-width;
        z-index: 2;

        &:before,
        &:after {
            content: "";
            position: absolute;
            width: 2px;
            height: 12px;
            top: -12px;
            background: darken(#e6ebf2, 30%);
            z-index: -1;
        }
            
        &:before {
            left: 8px;

            @media (max-width: $touch-nav) {
                left: 6px;
            }
        }

        &:after {
            right: 8px;
            
            @media (max-width: $touch-nav) {
                right: 6px;
            }
        }

      &:focus {
        outline: none;
      }

      &::-webkit-slider-runnable-track {
        @include track();
        @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);
        background: $track-color;
        border: $track-border-width solid $track-border-color;
        border-radius: $track-radius;
      }

      &::-webkit-slider-thumb {
        @include thumb();
        -webkit-appearance: none;
        margin-top: ((-$track-border-width * 2 + $track-height) / 2) - ($thumb-height / 2);
      }

      &:focus::-webkit-slider-runnable-track {
        background: lighten($track-color, $contrast);
      }

      &::-moz-range-track {
        @include track();
        @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);
        background: $track-color;
        border: $track-border-width solid $track-border-color;
        border-radius: $track-radius;
      }

      &::-moz-range-thumb {
        @include thumb();
      }

      &::-ms-track {
        @include track();
        background: transparent;
        border-color: transparent;
        border-width: $thumb-width 0;
        color: transparent;
      }

      &::-ms-fill-lower {
        @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);
        background: darken($track-color, $contrast);
        border: $track-border-width solid $track-border-color;
        border-radius: $track-radius * 2;
      }

      &::-ms-fill-upper {
        @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);
        background: $track-color;
        border: $track-border-width solid $track-border-color;
        border-radius: $track-radius * 2;
      }

      &::-ms-thumb {
        @include thumb();
      }

      &:focus::-ms-fill-lower {
        background: $track-color;
      }

      &:focus::-ms-fill-upper {
        background: lighten($track-color, $contrast);
      }
    }
}

.lig-block {
    width: 100%;
    height: 100%;
    background-color: $white;
}

.lig-content-wrapper {
    padding-bottom: 50px;
    max-width: 978px;
    margin: 0 auto;
    display: block;
    color: $primary-blue;
    text-align: center;
}

.lig-container {
    padding-top: 50px;
    padding-right: 40px;
    padding-left: 40px;
    background-color: $white;
    text-align: center;
}
.lig-content {
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;

    a {
        @extend .underlinedTextOnHover;
    }
}
.lig-title {
    @include title('small', 'center', $deep-ice);
    color: $deep-ice;
    font-weight: 700;
}

.lig-graph {
    display: inline-block;
    width: 100%;
    margin-bottom: 40px;
    
    @media (max-width: $touch-nav) {
        display: block;
        width: 100%;
        padding: 0 20px 0 20px;
        margin-bottom: 20px;
    }
    
}

.lig-intro {
    display: inline-block;
    vertical-align: middle;
    width: 45%;
    font-weight: bold;
    margin: 20px;
    text-align: left;

    @media (max-width: $touch-nav) {
        width: 90%;
        max-width: 600px;
        margin: 20px 0;
    }
}

.lig-results {
    display: inline-block;
    width: 45%;
    vertical-align: middle;
    background-color: #e6ebf2;
    padding: 20px;
    margin: 0 20px;
    text-align: left;

    @media (max-width: $touch-nav) {
        width: 90%;
        max-width: 600px;
        margin: 0;
    }

    p {
        margin: 0 0 10px;
        font-weight: 600;
    }
}

.lig-chart {
    width: 100%;
    height: 380px;
    @media (max-width: $touch-nav) {
        height: 200px;
    }
}

.lig-chart-wrapper {
    width: 100%;
    height: 100%;
}

.lig-coffee-container {
    position: relative;
    display: inline-block;
    margin-top: 10px;

    img {
        max-width: 100%;
    }
}

.lig-coffee-mask {
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #e6ebf2;
    transition: width 0.3s ease-in-out;
}