﻿// hide 3 cta buttons and returner panel for all vistors to prevent flash of styling

.hh-actions, 
.hh-info-bar-wrapper,
.hh-info-bar-returner-sub,
.lph-actions, 
.lph-info-bar-wrapper,
.lph-info-bar-returner-sub {
	display: none;
}

// if no quote then display 3 cta buttons
[data-customer-view="no-quote"] {
	.hh-actions,
	.lph-actions {
		display: block;

	}
}

// if a returner
[data-customer-view$="-returner"] {
	
    // if returner then display panel
	.hh-info-bar-wrapper,
	.lph-info-bar-wrapper {
		display: block;
	}

    // if returner move optional propsition logo up above panel
	.hh-proposition img,
	.lph-proposition img {
		@media (min-width: 450px){
			bottom: 150px;
		}
	}

    // if returner add padding to content to ensure panel does not overlap text
	.hh-content-container,
	.lph-content-container {
		padding-bottom: $hero-info-bar-height-mobile;
		@media(min-width: $hero-small) {
            padding-bottom: $hero-info-bar-height-tablet;
        }

        @media(min-width: $hero-medium) {
            padding-bottom: $hero-info-bar-height-desktop;
        }
	}
}

// only display pretext for recent returner and eleven month returner
// could also be done entirely with JS
[data-customer-view="recent-returner"] .hh-info-bar-returner-sub,
[data-customer-view="eleven-month-returner"] .hh-info-bar-returner-sub,
[data-customer-view="recent-returner"] .lph-info-bar-returner-sub,                         
[data-customer-view="eleven-month-returner"] .lph-info-bar-returner-sub {
	display: inline;
}