$video-main-background-color: $white;
$video-media-wrapper: $deep-ocean;
$video-play-button: #2BA338;
    
.pc-video-block {
    text-align: center;
    background-color: $video-main-background-color;

    .panel {
        padding: 0;
        height: 100%;

        @media(min-width: $mobile) {
            padding: 0 25px;
        }
    }

    .pc-video-block-media-wrapper {
        border-bottom: solid 15px $video-media-wrapper;
    }

    img {
        max-width: 100%;
    }

    .media-player-thumbnail {
        display: block;
        position: relative;

        img {
            display: block;
        }
    }
}

.pc-video-block-title {
    @include pc-title();
    @include font-size(1.5);
    text-transform: uppercase;
    line-height: 1em;
    margin-bottom: 0px;

    @media(min-width: $mobile) {
        @include font-size(2.0);
    }
}

.pc-video-block-pretitle {
    font-weight: 700;
    margin-bottom: 10px;
    @include font-size(1.4);
}

.pc-video-block-content {
    width: 100%;
    z-index: 500;
    pointer-events: none;
    background: $video-media-wrapper;
    padding: 15px;

    &.desktop{
        background: none;
        top: 0px; 
        left: 0px;
        position: absolute;
        height: 100%;
        padding: 0px;
    }

    &:hover {
        .pc-video-block-play-button {
            @include opacity(1);
        }
    }

    .pc-video-block-content-titles-container {
        display: table;
        height: 40%;
        width: 95%;
        margin: 0 auto;
    }

    .pc-video-block-content-titles {
        display: table-cell;
        width: 100%;
        vertical-align: middle;
        padding: 0 10px;
    }

    .pc-video-block-play-button {
        @include border-radius(5px);
        @include opacity(0.8);
        background-color: $video-play-button;
        height: 35px;
        width: 55px;
        margin: 0 auto;
        cursor: pointer;
        

        @media(min-width: $mobile) {
            height: 70px;
            width: 110px;
        }

        &:before {
            display: block;
            @include icon($play, 10px, $white);
            padding-top:12px;

            .lt-ie9 & {
                padding-left:24px;
                
                @media(min-width: $mobile) {
                    padding-left: 48px;
                }
            }
            
            @media(min-width: $mobile) {
                font-size: 20px;
                padding-top:25px;     
            }
        }
    }
}

.media-player-video-wrapper
{
	position: relative;
	padding-bottom: 56.25%; /* 16:9 */
	height: 0;
    max-height: 100%;
}
.media-player-video-pc
{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.pc-master-page{
    .pc-hero-video-block.playing > div{
        &::after,
        .pc-hero-video-media-wrapper{
            height: 100%;
            opacity: 1;
            background-color: #000;
            -webkit-transition: opacity .5s ease;
            -moz-transition: opacity .5s ease;
            -ms-transition: opacity .5s ease;
            -o-transition: opacity .5s ease;
            transition: opacity .5s ease;
        }
        .pc-hero-video-media-wrapper{
            opacity: 1;
            position: relative;
            z-index: 10;
        }
    }
    .pc-hero-video-media-wrapper{
        opacity: 0;
        z-index: -1;
        position: relative;
    }
    .pc-hero-video-block{
        background-size: cover !important;
        position: relative;
        
        > div {
            margin: 0 auto;
        }
        &.mobile{
            .pc-header-large{
                position: relative;
                margin: 0;
                width: 100%;
                padding: 20px 20px 40px 20px;
                left: 0;
                &::after{
                    bottom: 20px;
                }
            }
            .pc-hero-video-media-wrapper{
                z-index: 1;
                opacity: 1;
            }
            .pc-play-button{
                display: none;
            }
        }
        > div::after{
            position: absolute;
            content: '';
            height: 0;
            width: 100%;
            left: 0;
            top: 0;
            opacity: 0;
            background-color: #000;
            z-index: 5;
        }
        .panel{
            padding: 0;
            cursor: pointer;
        }
        .pc-play-button{
            position: absolute;
            left: 50%;
            margin-left: -25px;
            bottom: -83px;
        }
    }
    .pc-hero-video-block-content-titles-container{
        .pc-header-large{
            position: absolute;
            top: 50%;
            left: 25%;
            width: 50%;
            margin-top: -100px;
            @media(max-width: $touch-nav) {
                width: 80%;
                left: 10%;
            }
        }
    }
}