﻿$bubble-icon-colour: $brand-green;
$bubble-icon-colour-hover: $white;
$bubble-title: $brand-green;

.plh-container {
    @include hero-container();

    @media(min-width: $hero-medium) {
        max-height: none;
    }
}

.plh-media {
    @include hero-media();
}

.plh-media-img {
    @include hero-media-img();
}

.plh-content-wrapper {
    @include hero-content-wrapper();
}

.plh-content-container {
    @include hero-content-container();
}

.plh-content-align {
    @include hero-content-align();
}

.plh-product-name-text {
    line-height: 1.3;
    @include hero-proposition-text-desktop;  
    font-size:32px; 
}

hr.plh-product-name {
    border-top: 3px solid #fff;
    width: 17%;
    margin-top: 0;
    margin-bottom: 15px;
    @media(min-width: $hero-medium) {
            margin-bottom: 30px;
        };
}

.plh-proposition-text {
    @include hero-title();
    @include hero-h1-title();
}

.plh-sign-in {
    @include cta-button(hollow);
    margin-top:5px;
}

.plh-actions {
    display: block;
    padding: 0 50px;

    @media(min-width: $hero-medium) {
        padding: 0;
    }

    .mkt-circular-icon-container {
        width: 90px;
        margin: 0 auto;
    }

    &-title {
        max-width: 180px;
        margin-left: auto;
        margin-right: auto;
    }
}

.plh-actions-text {
    @include hero-sub-title;
}

.plh-callback-intro {
    @include hero-sub-title;
    margin-top: 25px;
}

.plh-callback-categories {
    font-size: 0;

    .mkt-circular-icon-container {
        width: 55px;
        margin: 0 auto;

        @media(min-width: $hero-medium) {
            width: 65px;
        }
    }

    .mkt-circular-icon-text {
        @media(min-width: $hero-medium) {
            font-size: 14px;
        }
    }
}
.plh-post-cta-text {
  @include hero-sub-title;
  margin-top: 25px;
}

.plh-callback-category {
    display: inline-block;
    width: 25%;
    font-size: 16px;

    @media(min-width: $hero-medium) {
        width: auto;
        padding: 0 10px;
    }
}

.plh-legal-text {
    padding-top: 22px;
    font-size: small;
    color: lightgray;

}

.plh-actions, .plh-callback-category {   
    
    .mkt-circular-icon-link {
        @include circular-icon-container();

        .mkt-circular-icon {
            background-color: $circular-icon-background-color-hover;

            &:before {
                color: $circular-icon-color-hover;
            }
        }

        &:hover {
            text-decoration: none;

            .mkt-circular-icon {
                background-color: $circular-icon-color-hover;

                &:before {
                    color: $circular-icon-background-color-hover;
                }
            }

            .mkt-circular-icon-text {
                text-decoration: underline;
                &.no-underline {
                    text-decoration:none;
                }
            }
        }
    }
}