﻿.pc-travel-map-container {
    position: relative;
    width: 100%;
    height: 400px;

    @media(min-width: $touch-nav) {
        height: 600px;
    }

    .pc-travel-map-overlay {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-color: rgba(0,0,0,0.7);
        color: $primary-blue;

        .pc-travel-map-start-button {
            position: relative;
            top: 170px;
            @include pc-button();
        }
    }

    .pc-travel-map-button {
        display: none;
    }

    &.pc-travel-map-mobile {
        .pc-travel-map-overlay {
            display: none;
        }

        .pc-travel-map-button {
            position: relative;
            @include pc-button();
            position: absolute;
            width: 110px;
            top: 10px;
            right: 10px;
            font-family: "SUN";
            z-index: 99;
        }
    }

    @media(min-width: $touch-nav) {
        .pc-travel-map-overlay,
        .pc-travel-map-button {
            display: none;
        }
    }
}

#pc-travel-map {
    width: 100%;
    height: 100%;
    margin: auto;
    padding: 0 20px;
}


.pc-travel-map-location {
    font-family: "SUN";
    color: #333333;
    font-size: 14px;
    line-height: 1.3;
    padding: 15px 15px 10px 10px;

    h2, strong {
        color: $primary-blue;
    }

    h2, p, ul, img {
        max-width: 100%;
        margin-bottom: 15px;
    }

    div {
        @media(min-width: $mobile) {
            float: left;
            width: 62%;
        }
    }
    
    img {
        display: none;

        @media(min-width: $mobile) {
            display: block;
            float: right;
            width: 35%;
        }
    }
}