#ratings-reviews-page {
    h1 {
        margin: 36px 27px;
        padding: 0;
        line-height: 1;

        @media(min-width: $mobile) {
            margin-left: 0;
            margin-right: 0;
        }
    }


    .reviews-page {
        margin: 0 auto;
        width: 100%;
        padding: 10px;
        background-color: #315A9A;
        @include border-radius(5px);
        @include drop-shadow-speech-bubble;
        @import "_ratings-reviews";

        .bv-action-bar {
            display: none !important;
        }
    }

    &.panel {
        padding-top: 0;
    }
}



.sticky-banner-holder {
    position: relative;
    max-width: 928px;
    overflow: hidden;

    @include border-radius(5px);
    @include drop-shadow-speech-bubble;

    .container {
        padding: 0;

        .cta-banner {
            margin: 0;
        }
    }

    .cta-banner-content a {
    }

    &.sticky {        
        position: fixed;
        top: 0;
        width: 100%;
        z-index: 99;
        left: 0;
        right: 0;
    }

    &.panel {
        padding: 0;
    }

    .cta-banner {
        background-color: #315a9a;
    }

    .cta-banner-content {
        overflow: hidden;
    }

    .cta-banner-copy {
        margin-top: 5px;

        @media(min-width: $mobile-low) {
            float: left;
            width: 65%;
        }
    }

    .cta-banner-link {
        padding-top: 8px;
        padding-bottom: 8px;
        margin-top: 8px;

        @media(min-width: $mobile-low) {
            float: right;
            width: 32%;
            margin-left: 3%;
            margin-top: -15px;
        }
    }
}
