﻿.hi-container {
  @include hero-container();
}

.hi-media {
  @include hero-media();
}

.hi-media-img {
  @include hero-media-img();
}

.hi-content-wrapper {
  @include hero-content-wrapper();
}

.hi-content-container {
  @include hero-content-container();
}

.hi-content-align {
  @include hero-content-align();
}

.hi-proposition {
  @include hero-title();
  margin: 0;
}

.hi-proposition-text-mobile {
  @include hero-proposition-text-mobile;
}

.hi-proposition-text-desktop {
  @include hero-proposition-text-desktop;
  @include hero-h1-title;
}

.hi-proposition-image {
  @include hero-proposition-image;
}

.hi-actions {
  .mkt-circular-icon-container {
    width: 65px;
    margin: 0 auto;

    @media(min-width: $hero-medium) {
      width: 90px;
    }
  }
  .mkt-circular-icon-text {
      margin-top: 10px;
  }
}

.hi-actions-text {
  @include hero-sub-title;
  margin-top: 12px;
}

.hi-actions-ctas {
  max-width: 310px;
  margin: 0 auto;
  font-size: 0;

  @media(min-width: $hero-medium) {
    max-width: none;
  }
}

.hi-actions-cta {
  display: inline-block;
  width: 30%;
  vertical-align: top;
  font-size: 16px;

  @media(min-width: $hero-medium) {
    width: auto;
    padding: 0 20px;
  }
}