.carousel-block {
	background-color: $ultralight-blue;
	.panel {
		color: $deep-ice;

		.carousel-wrapper {
			.carousel-item {
				display: block;
				@media (min-width: $mobile) {
					@include flexbox();
					@include justify-content(space-around);
	            }

				a {
					@extend .underlinedTextOnHover;
				}

				.left-content {
					width: auto;
					margin-bottom: 20px;
					text-align: center;
					@media (min-width: $mobile) {
						width: 39%;
						margin: 0;
						text-align: left;
						@include inline-block();
					}
					
                    .title {
							font-size: 24px;
							font-weight: 700;
							margin-bottom: 10px;
                    }

					.carousel-content {
						font-family: $font-sun;
						font-weight: 400;
						font-size: 16px;
						line-height: 1.2rem;

						@media (min-width: $mobile) {
							font-size: 19px;
						}
					}
				}

				.right-content {
					width: auto;
					@media (min-width: $mobile) {
						width: 60%;
						@include inline-block();
					}
					img {
						max-width: 100%;
						margin: 0 auto;
						@media (min-width: $mobile) {
							margin: 0;
							float: right;
						}
					}
				}
			}
		}

		.controls {
			position: relative;
			width: 140px;
			margin: 0 auto;
			margin-top: 20px;
			@media (min-width: $mobile) {
				bottom: 0;
				margin: 0;
				display: inline-block;
			}
			.next, .prev {
				position: absolute;
			}

			.prev {
				left: 0;
				&:before {
					@include icon($chevron-left-circle, 22px, $primary-green);
				}
			}

			.next {
					right: 0;
				&:before {
					@include icon($chevron-right-circle, 22px, $primary-green);
				}
			}

			.position {
				position: absolute;
				font-family: $font-sun;
				font-weight: 700;
				color: $primary-green;
				font-size: 16px;
				left: 50%;
				margin-left: -14px;
			}
		}
	}
}