$ftq-body-text-color: #9FA2AE;
.ftq-block {
    background-color: $white;

    .panel {
        padding-top: 38px;
        border-top: 15px solid $brand-green;
        @include flexbox();
        @include justify-content(space-between);

        @media(min-width: $mobile) {
            padding-top: 55px;
            padding-bottom: 55px;
        }
    }

    .text-column {
        @include inline-block;
        width: 100%;

        h2, b {
            display: block;
            color: $deep-ice;
            @include fontsize(1.5);
            position: relative;
            padding-left: 60px;
            margin-left: 20px;

            &:before {
                @include icon ($info, 42px, $brand-green);
                position: absolute;
                top: 50%;
                left: 0;
                margin-top: -22px;
            }

            @media(min-width: $mobile) {
                margin-bottom: 15px;
                margin-left: 0;
            }
        }

        a {
            font-weight: 700;
            text-decoration: underline;
            line-height: 1;
            margin-bottom: 15px;
            font-size: 1rem;
        }

        .text {
            padding: 5px 0;
            line-height: 1.4;
            font-size: 1.1rem;
        }

        &.left {
            @media (min-width: $mobile) {
                display: inline-block;
                  width: 56%;
                  margin-right: 3%;
            }
            
            .text {
                display: none;

                @media(min-width: $mobile) {
                    display: block;
                }
            }
        }

        &.right {
            padding: 0 20px;
            display: none;
            @media (min-width: $mobile) {
                width: 40%;
                display: inline-block;
            }

            a {
                display: none;
            }

            @media (min-width: $mobile) {
                padding: 0px;

                h2 {
                    padding-left: 0;

                    &:before {
                        display: none;
                    }
                }
                    

                a {
                    display: block;
                }
            }
        }

        .text {
            color: $black;
        }
    }
}
.martha-testing 
{

    $ftq-block-heading-color: #004A8E;
    $ftq-block-body-color: #333;
    
    .ftq-block-wrapper
    {
        background-color: $white;

        .panel
        {
            border-top: 0;
            padding-top: 30px;
            padding-bottom: 30px;

            
            @media(min-width: $mobile) {
                padding-top: 80px;
                padding-bottom: 80px;
            }

            @include clearfix;
        }
        
        .ftq-block-title
        {
            color: $ftq-block-heading-color;
            font-weight: 700;
            font-size: 35px;
            width: 100%; 
            line-height: 50px;
            letter-spacing: -1px;
            padding: 10px;
            
            @media(min-width: $mobile) {
                padding: 0;
            }

            @include clearfix;
        }

        .ftq-block-title__heading {
            font-size: 35px;
        }

        .ftq-block-title-underline
        {
            @include title(small, left);
            
            &:after
            {
                background-color: $ftq-block-heading-color;
            }
        }

        .ftq-block-text
        {
            color: $ftq-block-body-color;
            padding: 10px;
            width: 100%;
            margin: 0;
            float: none;

            
            @media(min-width: $mobile) {
                width: 40%;
                max-width: 300px;
                margin-right: 8%;
                float: left;
                padding: 0;
                padding-top: 10px;
            }

            p
            {
                line-height: 24px;
                font-size: 16px;
            }

            a
            {
                @extend .underlinedTextOnHover;
            }
        }

        .ftq-block-index 
        {
            line-height: 24px;
            font-size: 16px;
            width: 100%;
            padding: 10px;
            float: none;
            
            @media(min-width: $mobile) {
                width: auto;
                float: left;
                padding: 0;
                padding-top: 10px;
            }

            a
            {
                display: block;
                
                @extend .underlinedTextOnHover;
            }
        }
    }
}