footer#global-footer {
    @include fontsize(0.8);
    max-width: 1500px;
    margin: 0 auto;
    color: white;
    background: $deep-ice;

    @media(min-width: $mobile) {
        padding: 0 10px;
    }

    @media only screen and (min-width: 970px) {
        padding: 0;
    }

    .panel {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        align-content: flex-start;
        align-items: flex-start;
        padding-left: 0;
        padding-right: 0;

        .footer {
            margin-bottom: 20px;
            padding-left: 10px;
            padding-right: 10px;

            a {
                color: white;
                text-decoration: underline;

                &:hover {
                    color: white;
                }
            }

            &.copyright {
                // display: none;
                @include flex-grow(1);
                @include flex-shrink(0);
                @include flex-basis(90%);
                @media only screen and (min-width: $mobile-low) {
                    display: inline-block;
                    @include flex-basis(90%);
                }

                @media only screen and (min-width: $mobile) {
                    /*margin-right: 6%;*/
                }

                p {
                    &:first-child {
                        // margin-bottom: 20px;
                    }
                }
            }

            &.terms {
                // display: none;
                @include flex-grow(1);
                @include flex-shrink(0);
                @include flex-basis(90%);

                @media only screen and (min-width: $mobile-low) {
                    display: inline-block;
                }
            }

            &.hollywood {
                // display: none;
                @include flex-grow(1);
                @include flex-shrink(0);
                @include flex-basis(90%);

                @media only screen and (min-width: $mobile-low) {
                    display: inline-block;
                }

                p {
                    padding-top: 20px;
                    /*border-top: grey 1px solid;*/
                }

                img {
                    float: left;
                    max-width: 102px;
                    height: 19px;
                    margin-right: 5px;
                    padding-top: 1px;
                }
            }

            &.additional {
                display: block;
                width: 100%;

                p {
                    margin: 5px 0;

                    &:first-child {
                        margin-top: 0;
                    }

                    @media only screen and (min-width: $mobile) {
                        margin: 0;
                    }
                }
            }

            &.links {
                line-height: 1.6;
                display: block;
                @include flex-grow(1);
                @include flex-shrink(0);
                @include flex-basis(100%);

                nav {
                    /* @include flexbox();
					@include justify-content(space-between);
					@include flexflow(row, wrap);
					-webkit-align-content: flex-start;
					align-content: flex-start; */
                    @include justify-blocks();

                    @media only screen and (min-width: $mobile-low) {
                        // @include justify-content(flex-start);
                        text-align: left;
                    }

                    @media only screen and (min-width: $mobile) {
                        // @include justify-content(space-between);
                    }

                    .link-list {
                        /*border-top: 1px solid $rough-sea;*/
                        width: 48%;
                        padding-top: 15px;
                        margin-bottom: 20px;
                        display: inline-block;
                        text-align: left;
                        vertical-align: top;

                        @media only screen and (min-width: $mobile-low) {
                            width: 32%;
                        }

                        @media only screen and (min-width: $mobile) {
                            padding-top: 20px;
                            padding-bottom: 0;
                            width: 14%;
                            margin-bottom: 0;
                        }

                        @media only screen and (min-width: $touch-nav) {
                            width: 15%;
                        }

                        @media only screen and (min-width: $tablet) {
                            width: 16%;
                        }

                        p.heading {
                            @include fontsize(0.8);
                            font-weight: bold;
                        }

                        ul {
                            display: block;

                            li {
                                a {
                                    color: white;
                                    text-decoration: none;

                                    &:hover {
                                        text-decoration: underline;
                                    }
                                }
                            }
                        }

                        &:nth-child(5) {
                            @media only screen and (min-width: $touch-nav) {
                                width: 12%;
                            }
                        }

                        &-logo {
                            @media only screen and (min-width: $mobile) {
                                width: 150px;
                                float: right;
                            }
                        }

                        &-logo-img {
                            width: 150px;
                        }
                    }
                }
            }
        }
    }
}

.footer-branding {
    font-size: 175%;
    line-height: 1;
    margin-bottom: 6px;
}


.top-border {
    border-top: grey 1px solid;
}

.bottom-border {
    border-bottom: grey 1px solid;
}
