$block-background-color: $jaguar;
$block-font-color: $white;
$block-font-light-color: $deep-ice;

.pc-related-content-like-panel {
	text-align: center;
	padding: 30px 0;

    .pc-related-content-flourish {
        margin: 10px auto;
        width: 200px;
    }
}

.pc-related-content-container {
	background-color: $block-background-color;
	display: flex;
	flex-wrap: wrap;

	@media (min-width: $mobile) {
		flex-wrap: nowrap;
	}
}

.pc-related-content-panel {
	width: 100%;
	height: 330px;
	@include inline-block(bottom);
	color: $block-font-color;
	position: relative;
	&.light {
		color: $block-font-light-color;
	}

	@media (min-width: $mobile) {
		width: 33.33%;
		height: 400px;
	}

	.pc-simples-logo-wrapper {
		position: absolute;
	}
}

.pc-related-content {
	height: 100%;
	padding: 80px 10px 20px;
	overflow: auto;
	position: relative;
	background-size: cover !important;
	padding-top: 80px;
	text-align: center;
	@media (min-width: $mobile) {
		overflow: hidden;
	}
}

.pc-related-content-header-small {
	@include pc-header();
}

.pc-related-content-copy {
	position: relative;
	width: 75%;
	margin: 0 auto;
	text-align: center;
	font-size: $paragraph-small;
	@media (min-width: $mobile) {
		width: 50%;
	}
}

.pc-related-content-button {
    @include pc-new-button();
    background-image: none;
    text-transform: lowercase;
    position: absolute;
	bottom: 30px;
	@include translate(-50%, 0);
	left: 50%;

    &.pc-related-content-button-inverse {
        @include pc-new-button(inverse);
    }
}

.pc-related-content-button::first-letter {
    text-transform: uppercase;
}

.pc-related-content-play-button {
	@include pc-new-play-button();
    @include absolute-center('horizontal', 50px);
    bottom: 28%;

    @media (min-width: $mobile) {
    	bottom: 40%;
    }

    &.pc-related-content-play-button-inverse {
        @include pc-new-play-button(inverse);
        @include absolute-center('horizontal', 50px);
	        bottom: 28%;

	    @media (min-width: $mobile) {
	    	bottom: 40%;
	    }
    }
}

.pc-related-content-has-dark-overlay {
		@include dark-overlay;
}
