.fcs-container {
    background-size: cover;
    background-position: center center;
    width: 100%;
    position: relative;
    @media (min-width: $mobile) {
        height: 250px;
    }
}

.fcs-link-panel-container {
    @include content-width();
}

.fcs-link-panel {
    margin-right: auto;
    margin-left: auto;
    text-align: center;
    padding-top: 50px;
    padding-bottom: 50px;
    z-index: 200;
    position: relative;
    @media (min-width: $mobile) {
        width: 60%;
        padding-bottom: 0;
        padding-top: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        @include translate(-50%, -50%);

        .fcs-link-panel-container--narrow & {
            width: 30%;
        }
    }
}

.sg-container {
    & + .fcs-link-panel {
        padding-top: 70px;
        @media (min-width: $mobile) {
            padding-top: 35px;
        }
    }
}

.fcs-link-panel-title {
    @include title(small, center);
}

.fcs-link-panel-has-dark-overlay {
    @include dark-overlay();
}

.fcs-link-panel-button {
    @include cta-button();
}

.fcs-link-panel-inverse-button {
    @include cta-button(inverse);
}

.fcs-link-panel-hollow-button {
    @include cta-button(hollow);
}

.fcs-link-panel-play-button {
    @include play-button();
}

.fcs-link-panel-play-inverse-button {
    @include play-button(inverse);
}