.ratio-block {
	padding-left: 20px;
	padding-right: 20px;
	
	@media (min-width: $mobile) {
		padding-left: 0;
		padding-right: 0;
	}
	.titles-container {
		// display: none;
		@media (min-width: $mobile) {
			// @include flexbox();
			// @include justify-content(space-between);
			display: block;
		}
		.title {
			text-align: center;
			line-height: 1.3;
			font-size: 34px;
			font-weight: 700;
			margin-bottom: 30px;
			display: none;
			@media (min-width: $mobile) {
	            @include inline-block();
			}
			&:first-child { 	
				display: block;
	            @media (min-width: $mobile) {
	            	@include inline-block();
		            margin-right: 6%;
				}
			}
		}
	}
	.blocks-wrapper {
		@media (min-width: $mobile) {
			display: block;
		}
	}

	.blocks-item {
		width: auto;
		display: block;
		margin-top: 30px;
		@media (min-width: $mobile) {
			margin-top: 0;
		}
		&:first-child { 
			margin-top: 0;
			
            @media (min-width: $mobile) {
	            margin-right: 7%;
			}
		}
		@media (min-width: $mobile) {
        	@include inline-block();
            
		}
	}

	&.fiftyfifty {
		.blocks-item, .title {
			@media (min-width: $mobile) {
				width: 47%;
			}
		}

		.blocks-item {
			&:first-child {
	            @media (min-width: $mobile) {
		            margin-right: 6%;
				}
			}
		}
	}

	&.thirtyseventy {
		.blocks-item, .title {
			@media (min-width: $mobile) {
				width: 63%;
				&:first-child {
					width: 30%;
				}
			}
			
		}
	}

	&.seventythirty {
		.blocks-item, .title {
			@media (min-width: $mobile) {
				width: 30%;
				&:first-child {
					width: 63%;
				}
			}
		}
	} 
}