.ratings-reviews-block {
    .panel {
        @include clearfix;

        @media (min-width: $mobile) {
            @include flexbox();
        }

        @media(max-width: $desktop) {
            padding-left: $mobile-side-margin;
            padding-right: $mobile-side-margin;
        }
    }

    .ratings-reviews,
    .secondary-content {
        width: 100%;

        @media (min-width: $mobile) {
            .lt-ie10 & {
                float: left;
            }
        }
    }

    .ratings-reviews {
        overflow: auto;
        margin-bottom: 25px;

        @media (min-width: $mobile) {
            width: 60%;
            margin-bottom: 0;
        }
    }

    .secondary-content {
        @media (min-width: $mobile) {
            @include flexbox();
            width: 38%;
            margin-left: 2%;
        }
    }

    .star-container {
        display: inline-block;
        position: relative;
        margin-bottom: 15px;
    }

    .star {
        display: inline-block;

        &:before {
            @include icon($star, 0.85rem, $rough-sea);
            @include font-size(.85);
        }

        &.active:before {
            color: $corn;
            background: -webkit-linear-gradient(top, $corn 0%, $terracotta 100%);
            background: -o-linear-gradient(transparent, transparent); /* rare browser fallback */
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }
    }

    .active-star-container {
        position: absolute;
        left: 0;
        top: 0;
        width: 50%;
        white-space: nowrap;
        overflow: hidden;
    }

    .block-title {
        @include font-size(1.6);
        font-weight: 700;
        margin-bottom: 6px;
    }

    .ratings-summary,
    .user-reviews {
        @media (min-width: $mobile-low) {
            float: left;
        }
    }

    .ratings-summary {
        margin-bottom: 1em;

        @media (min-width: $mobile-low) {
            width: 48%;
            margin-bottom: 0;
            padding-right: 5%;
        }
    }

    .user-reviews {
        padding-top: 20px;
        border-style: solid;
        border-color: $border-fallback;
        border-color: rgba(255, 255, 255, $border-alpha);
        border-top-width: $border-width;

        @media (min-width: $mobile-low) {
            width: 52%;
            padding-left: 5%;
            margin: 5px 0;
            border-top-width: 0;
            border-left-width: $border-width;
        }
    }

    .rating-fraction {
        display: block;

        .value {
            font-weight: 700;
            font-size: 1.8em;
        }
    }

    .review-item {
        @include font-size(.8);

        .star-container {
            margin: 7px 0;
        }
    }

    .review-author {
        padding-right: 7px;
        display: inline-block;
        font-size: 1.3em;
        font-weight: 700;
    }

    .review-date {
    }

    .review-rating {
        font-size: 0;
    }

    .review-body {
        line-height: 1.35em;
    }

    .reviews-cta {
        display: inline-block;
        margin-top: 0.5em;
        @include font-size(.85);

        &:hover {
            text-decoration: underline;
        }
    }

    .block-child {
        @include flexbox();
    }
}

