﻿$mc-blue: #123e83;
$tf-bg: $baby-blue;
$slick-border: #86C9DE;
$tf-dots: $white;

.tf-wrapper {
    position: relative;
    width: 100%;
}

.tf-title {
    @include media-centre-title();
}

.tf-container {
    position: relative;
    padding: 20px 20px 40px;
    text-align: center;
    color: $mc-blue;
    background-color: $tf-bg;
    border-radius: 4px;

    &:before {
        @include icon($twitter, 55px, $mc-blue);
        display: block;
        margin: 0 auto 15px 0;
    }

    .slick-prev,
    .slick-next {
        position: absolute;
        bottom: 10px;
        -webkit-appearance: none;
    }

    .slick-prev {
        left: 10px;
        @include icon($chevron-left, 20px, $mc-blue);
    }

    .slick-next {
        right: 10px;
        @include icon($chevron-right, 20px, $mc-blue);
    }

    .slick-dots {
        width: 100%;
        position: absolute;
        bottom: 17px;
        left: 0;
        text-align: center;

        &:before {
            border-top: 1px solid $slick-border;
            content: '';
            width: 80%;
            margin: auto;
            display: block;
            padding-bottom: 16px;
        }

        li {
            display: inline-block;
            padding: 0 3px;
        }
    }

    .user {
        position: relative;
        margin-bottom: 20px;
        font-size: 15px;

        a {
            color: $mc-blue;
        }

        img {
            display: none;
        }

        span {
            display: block;

            &:last-child {
                position: absolute;
                width: 100%;
                top: 25px;
                left: 0;
                text-align: center;
            }
        }

        span {
            &[title="Verified Account"],
            &[data-scribe="element:verified_badge"] {
                display: none;
            }
        }
    }

    .timePosted {
        display: none;
    }

    .tweet {
        margin-bottom: 20px;
        font-size: 17px;
        line-height: 1.5;
        font-weight: 700;
        word-wrap: break-word;
        -webkit-text-size-adjust: none;
    }

    .interact {
        // border-bottom: 1px solid #86C9DE;
        padding-bottom: 20px;
        margin-bottom: 20px;
    }

    .twitter_reply_icon,
    .twitter_retweet_icon,
    .twitter_fav_icon {
        font-size: 0;
        padding: 0 9%;

        &:hover,
        &:focus {
            &:before {
                color: lighten($mc-blue, 8%);
            }
        }

    }

    .twitter_reply_icon {
        &:before {
            @include icon($twitter-reply, 20px, $mc-blue);
        }
    }

    .twitter_retweet_icon {
        &:before {
            @include icon($twitter-re-tweet, 20px, $mc-blue);
        }
    }

    .twitter_fav_icon {
        &:before {
            @include icon($twitter-fav, 20px, $mc-blue);
        }
    }
}

.tf-control {
    font-size: 0;
    position: absolute;
    bottom: 1px;
    padding: 10px;

    &:hover {
        &:before {
            color: lighten($mc-blue, 10%);
        }
    }
}

.tf-control-prev {
    left: 23px;

    &:before {
        @include icon($chevron-left, 20px, $mc-blue);
    }
}

.tf-control-next {
    right: 23px;

    &:before {
        @include icon($chevron-right, 20px, $mc-blue);
    }
}

.tf-dot {
    display: inline-block;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: $tf-dots;

    .slick-active & {
        background-color: $mc-blue;
    }

    &:hover {
        background-color: darken($tf-dots, 2%);
    }
}
