.gpb-vote-container {
    background-color: $primary-blue;
    padding: 50px 0;
}

.gpb-vote-content {
    max-width: 760px;
    margin: auto;
    padding: 0px 10px;
    text-align: center;
}

.gpb-question {
    margin-bottom: 30px;
    font-size: 22px;
    font-weight: 600;
    line-height: 1.3;
}

.gpb-answer-block {
    display: inline-block;
    font-size: 0px;
}

.gpb-circle {
    background-color: transparent;
    display: inline-block;
    padding: 10px 20px;
    color: $white;
    font-family: "SUN";
    font-size: 22px;
    font-weight: 600;
    vertical-align: middle;
    cursor: pointer;

    &:before {
        content: "";
        width: 200px;
        height: 200px;
        border-radius: 50%;
        background-color: white;
        display: block;
        background-repeat: no-repeat;
        background-position: center;
        background-size: 70%;
        margin: 0 auto 20px;
        -moz-transition: all .2s ease-out;
        -o-transition: all .2s ease-out;
        -webkit-transition: all .2s ease-out;
        transition: all .2s ease-out;
    }

    @media (max-width: $touch-nav) {
        &:before {
            width: 150px;
            height: 150px;
        }
    }

    @media (max-width: $mobile-smallest) {
        &:before {
            width: 100px;
            height: 100px;
        }
    }

    &:hover {
        text-decoration: underline;

        &:before {
            box-shadow: 0px 0px 50px #0f2749;
        }
    }
}

.gpb-result {
    display: inline-block;
    text-align: left;
}

.gpb-result-title {
    font-size: 22px;
    font-weight: 600;
    margin-bottom: 10px;
}





.gpb-chart-container {
    background: $white;
    padding-bottom: 40px;
}

.gpb-chart {
    max-width: 940px;
    margin: 0 auto 20px;
    padding: 0px 10px;
    text-align: center;
}

.gpb-chart-value {
    vertical-align: middle;
    display: inline-block;
    width: 13%;
    padding: 0 10px;
    font-weight: 600;
    font-size: 20px;
    color: #1e488c;
    text-align: left;
    
    @media (max-width: $touch-nav) {
        width: 18%;
        text-align: left;
    }

    @media (max-width: $mobile) {
        width: auto;
    }
}

.gpb-chart-bar {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: 70%;
    height: 45px;
    background-color: #cccccc;

    @media (max-width: $touch-nav) {
        width: 60%;
    }

    @media (max-width: $mobile) {
        display: none;
    }
}

.gpb-chart-mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 0%;
    height: 100%;
    background-color: #1e488c;
    -webkit-transition: width 1.5s ease-in-out;
    -moz-transition: width 1.5s ease-in-out;
    -o-transition: width 1.5s ease-in-out;
    transition: width 1.5s ease-in-out;
    z-index: 2;

    &:hover {
        width: 50%;
    }
}