//*****************************************************************
// au-lightbox.scss
// styles for the australia popup
//*****************************************************************
@import "../framework/mixins-n-variables/_mixins";
@import "../framework/mixins-n-variables/_font-variables.scss";
@import "../framework/mixins-n-variables/_typography.scss";

#countryContents {
    background: #0c4c8d;
    border-radius: 10px;
    padding: 10px;
}

#countryInner {
    border-radius: 10px;
    padding: 10px;
    text-align: left;

	h3 {
		border-bottom: #4476a8 1px solid;
		font-family: $font-sun7;
		padding: 8px 12px 7px 12px;
	}

	a:hover {
		text-decoration: none;
	}

	p {
		margin: 12px;
		@include fontsize(1.6);
	}

	strong {
		font-family: $font-sun7;
	}

	ul {
		border-top: #4476a8 1px solid;
		height: 140px;

		li {
			padding: 18px 0 0;

			&+li {

				p {
					background-position: 15px -71px;

				}
			}

			a {
				display: block;
				margin: 0 auto;
			}
		}
	}

	#country-au {
		
		p {					
			display: block;
			margin: 0 auto;
			background: #132c58 url(../img/bgs/country.png) no-repeat 15px -21px;
			border-radius: 5px 5px 0 0;
			padding: 12px 0 12px 50px;
			width: 166px;
			@include fontsize(1.5);
		}

		a {
			background-color: #189507;
			background-image: linear-gradient(#31AE20, #006300);
			border: 0 none;
			border-radius: 4px 4px 4px 4px;
			box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.3);
			color: white !important;
			padding: 11px 15px;
			width: 225px;
			@include fontsize(1.8);

			&:hover, &:focus {
				background-color: #31ae20;
				background-image: -moz-linear-gradient(center top, #4ac739, #007c00);
				background-image: -webkit-gradient(linear, 0% 0%, 0% 100%, from(#4ac739), to(#007c00));
				background-image: -ms-linear-gradient(top, #4ac739 0%, #007c00 100%);
				background-image: linear-gradient(#4ac739, #007c00);
				text-decoration: none; 
			}
		}

		span {
			display: block;
			background: url(../img/bgs/country.png) no-repeat right 6px;
		}
	}

	#country-uk {
		a{
			@include fontsize(1.6);
			text-align: center;
			width: 255px;
		}
		
		&:hover, &:focus{
			text-decoration: underline; 
		}
	}
}