﻿.main-header {
    @include clearfix;
    display: flex;
    width: 100%;
    padding: 9px 0;
    position: relative;
    z-index: 2;
    text-align: center;
    background-color: $black;
    box-shadow: 0 0px 15px rgba(0, 0, 0, 0.29);
    @include user-select(none);

    @media(min-width: $touch-nav) {
        margin: 0 auto;
        padding: 0 1em;
        height: auto;
        text-align: left;
        box-shadow: none;
        background-color: transparent !important;
    }

    @media(min-width: $desktop) {
        width: $content-width;
        @include clearfix;
    }
}

.site-branding {
    flex: 1 0 auto;

    a {
        display: block;

        @media(min-width: $touch-nav) {
            width: auto;
        }
    }

    @media(min-width: $touch-nav) {
        margin: 1.2em 0 .6em;
        width: 50%;
    }

    @media(min-width: $desktop) {
        width: 40%;
		z-index: 2;
        margin: 1.25em 0 0.5em;
    }
}

.site-branding-img {
    img {
        height: 26px;
        display: block;
        margin: 0 auto;
        /* Due to the shadow on the image */
        position: relative;
        top: 3px;

        @media(max-width:$mobile){
            height:35px;
        }

        @media(max-width: $mobile-low){
            height:32px;
        }

         @media(max-width: $mobile-smallest){
            height:26px;
        }

        @media(min-width: $mobile-low) {
            height: 32px;
        }

        @media(min-width: $mobile-smallest) {
            height: 34px;
        }

        @media(min-width: $touch-nav) {
            display: block;
            margin: 0;
            top: 0;       
        }
    }
}

.user-actions {
    padding-top: 2px;
    width: 42px;
    text-align: center;

    @media(min-width: $touch-nav) {
        display: none;
    }
}
.user-account{
    .user-welcome-message {
        display:none;

        [data-signed-in] & {
            @media(min-width: $touch-nav) {
                display: inline-block;
            }
        }
    }

    .user-sign-in-button--container {
        display:none;

        [data-signed-out] & {
            @media(min-width: $touch-nav) {
                display: inline-block;
            }
        }
    }
}

.user-log-in {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;

    .account-text {
        position: absolute;
        top: -999999em;
        left: auto;
        width: 1px;
        height: 1px;
        overflow: hidden;
    }

    &:before {
        @include icon($profile, 20px, $white);
    }
}



.user-account {  

    a {
        display: inline-block;
        margin-top: 30px;

        &:hover{
          text-decoration: underline;
        }

        &.notYouLink, &.fcs-link-panel-hollow-button {
            margin-top:20px;
            margin-left: .4em;
            display: inline-block;
        }

        &.notYouLink{
            margin-left:0;
        }
    }

    p {
        display: inline-block;
    }

    @media(min-width: $touch-nav) {
        display: block;
        float: right;
        width: 50%;
        padding: 0 .5em;
        position: relative;
        z-index: 1;
        color: $white;
        text-align: right;
        @include fontsize(0.9);
        text-shadow: #000 0px 1px 4px;
    }


}

.martha-testing {
    .user-welcome-message {
        @media(min-width: $touch-nav) {
            margin: 0;
        }

        @media(min-width: $desktop) {
            margin-top: 3px;
        }
    }

    .user-account {
        a:hover{
          text-decoration: none;
        }
        
        @media(min-width: $touch-nav) {
            margin-top: 0;
        }

        @media(min-width: $desktop) {
            margin-top: 0;
        }
    }
}

.user-account-mobile {
    width: 100%;
    padding:10px;
    display: none;
    margin: 0 auto 15px;
      border-top: 1px solid #195c99;
        border-bottom: 1px solid #195c99;
        text-align: center;

    p, a {
        display: inline-block;
        font-weight: 700;
        @include fontsize(1);
    }

    p {
        margin-right: 7px;
    }

    [data-signed-in] & {
        display: inline-block;

        @media(min-width: $touch-nav) {
            display: none;
        }
    }

    
}
